import {
  getDB,
} from './methods';
import { IPasswordFromFile } from '../../interfaces/passwords-decryptor.interfaces';
import { sendErrorToSentry } from '../sentry';

export const loadPasswordsFromFile = async (filePath: string): Promise<{passwords: IPasswordFromFile[]}> => {
  const passwords: IPasswordFromFile[] = [];
  const db = await getDB(filePath);

  try {
    const loginsRows = await db.all('select * from logins');
    for (const row of loginsRows) {
      const {
        origin_url,
        action_url,
        username_element,
        username_value,
        password_element,
        password_value,
        submit_element,
      } = row;

      passwords.push({
        origin_url,
        action_url,
        username_element,
        username_value,
        password_element,
        password_value,
        submit_element,
      });
    }
  } catch (error: unknown) {
    const message = error instanceof Error ? error.message : error;
    if (typeof message === 'string' && message.includes('no such table')) {
      return { passwords };
    }

    sendErrorToSentry({
      message, transactionName: 'load-passwords-from-file', level: 'error',
      tags: [['scenario', 'close-profile']],
    });
  } finally {
    await db.close();
  }

  return {
    passwords,
  };
};
