import { EventEmitter } from 'stream';

import { IGetTimezoneResult } from '../interfaces/timezone.manager.interfaces';

export class ProfileReadyChecker {
  #isProxyReady = false;
  #proxyCheckResult: IGetTimezoneResult = {} as IGetTimezoneResult;
  #isProfilePrepared = false;
  #eventEmitter: EventEmitter;

  constructor(eventEmitter: EventEmitter) {
    this.#eventEmitter = eventEmitter;
  }

  set isProxyReady(proxyResult: IGetTimezoneResult) {
    this.#isProxyReady = true;
    this.#proxyCheckResult = proxyResult;
    this.sendReadyEvent();
  }

  get eventEmitter(): EventEmitter {
    return this.#eventEmitter;
  }

  set isProfilePrepared(status: boolean) {
    this.#isProfilePrepared = status;
    this.sendReadyEvent();
  }

  sendReadyEvent(): void {
    if (!(this.#isProfilePrepared && this.#isProxyReady)) {
      return;
    }

    this.#eventEmitter.emit('profile-ready', this.#proxyCheckResult);
  }
}
