import { access, mkdir, readFile, writeFile } from 'fs/promises';
import path from 'path';

import fs from 'fs-extra';
import got from 'got';

// import { checkDownloadedExtFolder } from './electron/managers/utils/extensions.utils';
import { downloadWithRetry } from './downloadWithRetry';
import { extractArchive } from './extract-archive';
import { EXTENSIONS_PATH } from './path-platforms';
import withRetry from './with-retry';
import { makeRequest } from '../../electron/utils/make-request';
import { sendCommonErrorToSentry } from '../../electron/utils/sentry';
import { IExtensionObject } from '../interfaces/check-gologin-extensions.interfaces';
import { checkDownloadedExtFolder } from '../managers/utils/extract.downloaded.extensions';

const util = require('util');
const exec = util.promisify(require('child_process').exec);

export const EXTENSIONS_TO_DOWNLOAD = [
  {
    name: 'Cookies Ext',
    extName: 'cookies-ext',
    extZipName: 'cookies-ext.zip',
    hashFile: 'hashfile.txt',
    hashFileUrl: 'https://extensions.gologin.com/hashfile.txt',
    versionFile: 'latest-version.txt',
    versionFileUrl: 'https://extensions.gologin.com/latest-version.txt',
    extArchiveUrl: 'https://extensions.gologin.com/gologin-browser-ext.zip',
    tempFolder: 'downloaded-cookies-ext',
    baseFolderName: 'cookies-ext-base',
  },
  {
    name: 'Passwords Ext',
    extName: 'passwords-ext',
    extZipName: 'passwords-ext.zip',
    hashFile: 'passwords-hashfile.txt',
    hashFileUrl: 'https://extensions.gologin.com/passwords-hashfile.txt',
    versionFile: 'passwords-latest-version.txt',
    versionFileUrl: 'https://extensions.gologin.com/passwords-latest-version.txt',
    extArchiveUrl: 'https://extensions.gologin.com/gologin-browser-passwords-ext.zip',
    tempFolder: 'downloaded-passwords-ext',
    baseFolderName: 'passwords-ext-base',
  },
];

const getLatestExtVersion = (extFileUrl: string): Promise<string> => makeRequest({ url: extFileUrl, json: false });

const checkExtArchive = (extObj: IExtensionObject): Promise<void> => withRetry<void>({
  fn() {
    return access(path.join(EXTENSIONS_PATH, extObj.tempFolder, extObj.extZipName))
      .catch((error: unknown) => {
        const message = error instanceof Error ? error.message : error;
        sendCommonErrorToSentry({
          message, transactionName: 'check-gologin-extensions',
          tags: [['scenario', 'download-extensions'], ['name', 'check-gologin-extensions']],
        });

        return Promise.reject(new Error(`${extObj.name} Archive has not been found. ${message}`));
      });
  },
});

const checkArchiveSum = async (extObj: IExtensionObject): Promise<void> => {
  console.log(`IN checkArchiveSum ${extObj.name}`);
  const { platform } = process;
  const tempFolderPath = path.join(EXTENSIONS_PATH, extObj.tempFolder);
  const hashFilePath = path.join(tempFolderPath, extObj.hashFile);
  let commandToGenerateHash = `cd ${tempFolderPath} && sha256sum ${extObj.extZipName}`;

  if (platform === 'win32') {
    return Promise.resolve();
    // commandToGenerateHash = `cd "${tempFolderPath}" && \
    // CertUtil -hashfile ${extObj.extZipName} SHA256 | find /i /v "SHA256" | find /i /v "certutil"`;
    // commandToGetServerHash = `type "${hashFilePath}"`;
  } else if (platform === 'darwin') {
    commandToGenerateHash = `cd ${tempFolderPath} && shasum -a 256 ${extObj.extZipName}`;
  }

  return withRetry({
    fn() {
      return new Promise((resolve, reject) => {
        const req = got.stream(extObj.hashFileUrl, {
          timeout: {
            request: 30 * 1000,
          },
        });

        req.on('response', () => {
          const file = fs.createWriteStream(hashFilePath, { mode: 0o666 });
          req.pipe(file);
          file.on('close', () => {
            resolve(null);
          });

          file.on('error', (error) => {
            reject(error);
          });
        });

        req.on('error', (error) => {
          reject(error);
        });
      });
    },
  })
    .then(() => withRetry({
      fn() {
        return access(hashFilePath)
          .catch(() => Promise.reject(new Error('Ext Hash file has not been downloaded')));
      },
    }))
    .then(() =>
      withRetry({
        fn() {
          return readFile(hashFilePath, 'utf8').then((severRes: any) => {
            const checkServerRes = severRes;

            return exec(commandToGenerateHash).then((userRes: any)  => {
              const checkUserRes = (userRes.stdout || '').toString().trim();
              const [serverHash] = checkServerRes.split(' ');

              const splittedUserHash = checkUserRes.split(' ');
              let [userHash] = splittedUserHash;
              if (splittedUserHash.length > 2) {
                splittedUserHash.pop();
                userHash = splittedUserHash.join('');
              }

              console.log(`serverHash ${extObj.name}:`, serverHash);
              console.log(`userHash ${extObj.name}:`, userHash);
              if (userHash !== serverHash) {
                return Promise.reject(new Error(`Error in ${extObj.name} sum matching`));
              }

              return Promise.resolve();
            });
          });
        },
      }),
    );
};

const moveNewExt = (extObj: IExtensionObject): Promise<void> => {
  console.log(`IN moveNewExt ${extObj.name}`);

  return withRetry<void>({
    fn() {
      return fs.remove(path.join(EXTENSIONS_PATH, extObj.extZipName))
        .then(() => fs.remove(path.join(EXTENSIONS_PATH, extObj.versionFile)))
        .then(() => extractArchive(
          path.join(EXTENSIONS_PATH, extObj.tempFolder, extObj.extZipName), path.join(EXTENSIONS_PATH, extObj.baseFolderName),
        ))
        .then(() => fs.copy(path.join(EXTENSIONS_PATH, extObj.tempFolder, extObj.versionFile), path.join(EXTENSIONS_PATH, extObj.versionFile)));
    },
  })
    .then(() => fs.remove(path.join(EXTENSIONS_PATH, extObj.tempFolder)))
    .catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      sendCommonErrorToSentry({
        message, transactionName: 'extract-gologin-extension',
        tags: [['scenario', 'download-extensions'], ['name', 'extract-gologin-extension']],
      });

      return Promise.resolve();
    });
};

const downloadNewExt = (extObj: IExtensionObject): Promise<void> => {
  console.log(`IN downloadNewExt ${extObj.name}`);

  return downloadWithRetry({
    url: extObj.extArchiveUrl,
    destFile: path.join(EXTENSIONS_PATH, extObj.tempFolder, extObj.extZipName),
  })
    .then(() => console.log('download completed'))
    .then(() => checkExtArchive(extObj))
    .then(() => checkArchiveSum(extObj))
    .then(() => moveNewExt(extObj))
    .catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      sendCommonErrorToSentry({
        message, transactionName: 'download-gologin-extensions',
        tags: [['name', 'download-gologin-extensions']],
      });
    });
};

export const checkGologinExtensions = async (): Promise<void> => {
  const promises = EXTENSIONS_TO_DOWNLOAD.map(async (extObj) => {
    const currentVersion = await readFile(path.join(EXTENSIONS_PATH, extObj.versionFile))
      .catch(() => '');

    const currentVersionString = currentVersion.toString();

    const cloudVersion = await getLatestExtVersion(extObj.versionFileUrl);
    const cloudVersionString = cloudVersion.toString().trim();
    const isExtFolderExists = await checkDownloadedExtFolder(extObj);

    if (!cloudVersionString || (cloudVersionString === currentVersionString && isExtFolderExists)) {
      return;
    }

    await withRetry({
      fn() {
        return mkdir(path.join(EXTENSIONS_PATH, extObj.tempFolder), { recursive: true })
          .then(() => writeFile(path.join(EXTENSIONS_PATH, extObj.tempFolder, extObj.versionFile), cloudVersionString, { mode: 0o666 }))
          .then(() => mkdir(path.join(EXTENSIONS_PATH, extObj.extName), { recursive: true }));
      },
    }).catch((error) => Promise.reject(error));

    await downloadNewExt(extObj);
  });

  const promisesWithCatch = promises.map(promise => promise.catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    sendCommonErrorToSentry({
      message, transactionName: 'check-gologin-extensions',
      tags: [['scenario', 'download-extensions'], ['name', 'check-gologin-extensions']],
    });
  }));

  await Promise.allSettled(promisesWithCatch);
};

export const deleteProfileExtFolder = (profileId: string): Promise<void|void[]> => {
  const promises: Promise<void>[] = [];
  for (const extObj of EXTENSIONS_TO_DOWNLOAD) {
    promises.push(withRetry<void>({
      fn() {
        return fs.remove(path.join(EXTENSIONS_PATH, extObj.extName, profileId));
      },
    }));
  }

  return Promise.all(promises).catch(() => Promise.resolve());
};
