/* eslint-disable max-len */
import { writeFile, rm, copyFile } from 'fs/promises';

import { open } from 'sqlite';
import sqlite3 from 'sqlite3';

import { getDB } from './methods';
import { IUserCookie } from '../../interfaces/browser.user-data.manager.interfaces';
import {
  IDiffCookiesResult,
  IInsertCookiesToDbProps,
  ISameSite,
  ISaveCookiesToFile,
} from '../../interfaces/cookies-decryptor.interfaces';
import { CommonDataManager } from '../../managers/common-data-manager';
import { CookiesManager } from '../../managers/cookies-manager';
import { sendErrorToSentry } from '../sentry';
import withRetry from '../with-retry';

const MAX_SQLITE_VARIABLES = 76;

/*
   samesite  
   chromium:

  enum class CookieSameSite {
    UNSPECIFIED = -1,
    NO_RESTRICTION = 0,
    LAX_MODE = 1,
    STRICT_MODE = 2,
    // Reserved 3 (was EXTENDED_MODE), next number is 4.
  };
*/
export const SAME_SITE: ISameSite = {
  '-1': 'unspecified',
  '0': 'no_restriction',
  '1': 'lax',
  '2': 'strict',
};

const chunk = <T>(arr: T[], chunkSize = 1): T[][] => {
  const tmp = [...arr];
  const cache: T[][] = [];

  if (chunkSize <= 0) {
    return cache;
  }

  while (tmp.length) {
    cache.push(tmp.splice(0, chunkSize));
  }

  return cache;
};

export const buildCookieURL = (domain: string, secure: boolean, path: string): string => {
  let domainWithoutDot = domain;
  if (domain.startsWith('.')) {
    domainWithoutDot = domain.substring(1);
  }

  return 'http' + (secure ? 's' : '') + '://' + domainWithoutDot + path;
};

const unixToLDAP = (unixtime: number): number => {
  if (unixtime === 0) {
    return unixtime;
  }

  const win32filetime = new Date(Date.UTC(1601, 0, 1)).getTime() / 1000;
  const sum = unixtime - win32filetime;

  return sum * 1000000;
};

export const ldapToUnix = (ldap: number): number => {
  const ldapLength = ldap.toString().length;
  if (ldap === 0 || ldapLength > 18) {
    return ldap;
  }

  // eslint-disable-next-line no-underscore-dangle
  let _ldap = ldap;
  if (ldapLength < 18) {
    _ldap = Number(_ldap + '0'.repeat(18 - ldapLength));
  }

  const win32filetime = new Date(Date.UTC(1601, 0, 1)).getTime();

  return (_ldap / 10000 + win32filetime) / 1000;
};

const equalCookieWith = (targetCookie: IUserCookie) => ({ domain, name, path }: IUserCookie): boolean => {
  const targetCookieDomainWithoutDot = targetCookie.domain && targetCookie.domain.startsWith('.')
    ? targetCookie.domain.substring(1)
    : targetCookie.domain;

  const cookieDomainWithoutDot = domain.startsWith('.') ? domain.substring(1) : domain;

  return (
    targetCookieDomainWithoutDot === cookieDomainWithoutDot &&
    targetCookie.name === name &&
    targetCookie.path === path
  );
};

const getDiffCookies = (fileCookies: IUserCookie[], newCookies: IUserCookie[]): IDiffCookiesResult => {
  const uniqueDbCookies = newCookies.filter(
    (dbCookie) => fileCookies.findIndex(equalCookieWith(dbCookie)) === -1);

  const cookiesDifferenceValues = newCookies.filter((dbCookie) => {
    const sameCookie = fileCookies.find(equalCookieWith(dbCookie));

    return sameCookie && Buffer.compare(Buffer.from(sameCookie.value), (Buffer.from(dbCookie.value))) !== 0;
  });

  return {
    unique: uniqueDbCookies,
    withNewValue: cookiesDifferenceValues,
  };
};

const getChunckedInsertValues = (cookiesArr: IUserCookie[]): (string | (string | number | Buffer | undefined)[])[][] => {
  const todayUnix = Math.floor(new Date().getTime() / 1000.0);
  const chunckedCookiesArr: IUserCookie[][] = chunk<IUserCookie>(cookiesArr, MAX_SQLITE_VARIABLES);

  return chunckedCookiesArr.map((cookies) => {
    const queryPlaceholders = cookies.map(() => '(?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?)').join(', ');
    const query = `insert or replace into cookies (creation_utc, host_key, top_frame_site_key, name, value, encrypted_value, path, expires_utc, is_secure, is_httponly, last_access_utc, has_expires, is_persistent, priority, samesite, source_scheme, source_port, is_same_party, last_update_utc) values ${queryPlaceholders}`;
    const queryParams = cookies.flatMap((cookie) => {
      const creationDate = cookie.creationDate || unixToLDAP(todayUnix);
      let expirationDate = cookie.session ? 0 : unixToLDAP(cookie.expirationDate);
      const encryptedValue = Buffer.from(cookie.value);

      const samesite = Object.keys(SAME_SITE).find((key) => SAME_SITE[key] === (cookie.sameSite || '-1'));

      const isSecure =
        cookie.name.startsWith('__Host-') || cookie.name.startsWith('__Secure-') ? 1 : Number(cookie.secure ?? 1);

      const sourceScheme = isSecure === 1 ? 2 : 1;
      const sourcePort = isSecure === 1 ? 443 : 80;
      // eslint-disable-next-line no-undefined
      let isPersistent = cookie.session === undefined
        ? Number(expirationDate !== 0)
        : Number(!cookie.session);

      if (/^(\.)?mail.google.com$/.test(cookie.domain) && cookie.name === 'COMPASS') {
        expirationDate = 0;
        isPersistent = 0;
      }

      return [
        creationDate,
        cookie.domain,
        '', // top_frame_site_key
        cookie.name,
        '', // value
        encryptedValue,
        cookie.path,
        expirationDate,
        isSecure,
        Number(cookie.httpOnly ?? 1),
        0, // last_access_utc
        expirationDate === 0 ? 0 : 1, // has_expires
        isPersistent,
        1, // default priority value (https://github.com/chromium/chromium/blob/main/net/cookies/cookie_constants.h)
        samesite,
        sourceScheme,
        sourcePort,
        0, // is_same_party
        0, // last_update_utc
      ];
    });

    return [query, queryParams];
  });
};

export const loadCookiesFromFile = async (filePath: string, convertValueToUtf8 = false): Promise<{cookies: IUserCookie[]}> => {
  const db = await getDB(filePath);
  const cookies: IUserCookie[] = [];

  try {
    const cookiesRows = await db.all('select * from cookies');
    for (const row of cookiesRows) {
      const {
        host_key,
        name,
        encrypted_value,
        path,
        is_secure,
        is_httponly,
        expires_utc,
        is_persistent,
        samesite,
        creation_utc,
      } = row;

      cookies.push({
        url: buildCookieURL(host_key, is_secure, path),
        domain: host_key,
        name,
        value: convertValueToUtf8 ? encrypted_value.toString('utf8') : encrypted_value,
        path,
        sameSite: SAME_SITE[samesite],
        secure: Boolean(is_secure),
        httpOnly: Boolean(is_httponly),
        hostOnly: !host_key.startsWith('.'),
        session: !is_persistent,
        expirationDate: ldapToUnix(expires_utc),
        creationDate: ldapToUnix(creation_utc),
      });
    }
  } catch (error) {
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, transactionName: 'cookies-decrypt', level: 'error',
      tags: [['scenario', 'start-profile']],
    });
  } finally {
    await withRetry({
      fn() {
        return db.close();
      },
      retryDelay: 300,
    }).catch((error) => {
      sendErrorToSentry({
        message: error.message, transactionName: 'close-cookie-db-error', level: 'error',
        tags: [['scenario', 'start-profile']],
      });
    });
  }

  return {
    cookies,
  };
};

export const saveCookiesToFile = async (props: ISaveCookiesToFile): Promise<void> => {
  const { cookiesFromFile = [], serverCookies, profileId, cookiesFilePath } = props;
  //      ,    
  const diffCookies = getDiffCookies(cookiesFromFile, serverCookies);
  const cookiesArr = [...diffCookies.unique, ...diffCookies.withNewValue];
  const chunkInsertValues = getChunckedInsertValues(cookiesArr);
  const db = await getDB(cookiesFilePath, false);

  let insertCookiesResult = await insertCookiesToDbAndCopyFile({
    chunkInsertValues, db, cookiesFilePath, profileId,
  }).catch((error) => error.message) || '';

  const CommonDataManagerInst = new CommonDataManager();

  if (insertCookiesResult.includes('no such table: cookies')) {
    await db.run(CommonDataManagerInst.createCookiesTableQuery);
    await insertCookiesToDbAndCopyFile({
      chunkInsertValues, db, cookiesFilePath, profileId,
    }).catch((error) => {
      console.log(error);
      insertCookiesResult = error.message;
      sendErrorToSentry({
        message: error.message, transactionName: 'save-cookies-to-file', level: 'error',
        tags: [['scenario', 'start-profile']],
      });
    });
  }

  if (insertCookiesResult.includes('table cookies has no column named')) {
    console.log('insertCookiesResult', insertCookiesResult);
    const [missingColumn] = insertCookiesResult.split(' ').reverse();
    const createColumnQuery = `ALTER TABLE cookies ADD ${missingColumn} INTEGER`;

    await db.run(createColumnQuery);

    await insertCookiesToDbAndCopyFile({
      chunkInsertValues, db, cookiesFilePath, profileId,
    }).catch((error) => {
      console.log(error);
      sendErrorToSentry({
        message: error.message, transactionName: 'save-cookies-to-file-after-add-column-error', level: 'error',
        tags: [['scenario', 'start-profile']],
      });
    });
  }

  await db.close().catch((error) => {
    sendErrorToSentry({
      message: error.message, transactionName: 'close-cookie-db-error', level: 'error',
      tags: [['scenario', 'start-profile']],
    });
  });
};

const insertCookiesToDbAndCopyFile = async (props: IInsertCookiesToDbProps): Promise<void> => {
  const { chunkInsertValues, db, cookiesFilePath, profileId } = props;
  for (const [query, queryParams] of chunkInsertValues) {
    const insertStmt = await db.prepare(query);
    await insertStmt.run(queryParams);
    await insertStmt.finalize();
  }

  await CookiesManager.copyCookiesFile(cookiesFilePath, profileId);
};

export const clearCookiesFile = async (filePath: string): Promise<void> => {
  const db = await getDB(filePath, false);
  try {
    const query = 'delete from cookies';
    const insertStmt = await db.prepare(query);
    await insertStmt.run();
    await insertStmt.finalize();
  } catch (error: unknown) {
    const message = error instanceof Error ? error.message : error;
    console.log(message);
  } finally {
    await db.close();
  }
};

export const removeCookiesFile = (filePath: string): Promise<void> =>
  withRetry({
    fn() {
      return rm(filePath, { recursive: true, force: true });
    },
    retryDelay: 300,
  });

export const createDBFile = async (pathToCookiesFile: string, secondPathToCookiesFile = ''): Promise<void> => {
  await writeFile(pathToCookiesFile, '', { mode: 0o666 }).catch(console.log);

  await new Promise((resolve) => {
    new sqlite3.Database(pathToCookiesFile, (error) => {
      if (error) {
        throw error;
      }

      resolve(null);
    });
  });

  const connectionOpts = {
    filename: pathToCookiesFile,
    driver: sqlite3.Database,
  };

  const CommonDataManagerInst = new CommonDataManager();

  const db = await open(connectionOpts);
  await db.run(CommonDataManagerInst.createCookiesTableQuery);
  await db.close();

  secondPathToCookiesFile && await copyFile(pathToCookiesFile, secondPathToCookiesFile).catch(console.log);
};
