import path from 'path';

import { sentryTransactionDefaultObject } from '../../helpers/helper.functions';
import { IExtExistsObj, IExtObject, IUpdateFileOptions } from '../../interfaces/extract.download.ext.interface';
import { checkGologinExtensions, EXTENSIONS_TO_DOWNLOAD } from '../../utils/check-gologin-extensions';
import { EXTENSIONS_PATH } from '../../utils/path-platforms';
import { copyFolder } from '../../utils/utils';

declare let global: any;

const { access, readFile, writeFile, readdir } = require('fs').promises;

export const checkDownloadedExtFolder = (extObj: IExtObject): boolean => access(path.join(EXTENSIONS_PATH, extObj.baseFolderName))
  .then(() => true)
  .catch(() => false);

const copyExtensionFolder = async (extObj: IExtObject, profileId: string): Promise<boolean|IExtExistsObj> => {
  const source = path.join(EXTENSIONS_PATH, extObj.baseFolderName);
  const dest = path.join(EXTENSIONS_PATH, extObj.extName, profileId);
  await copyFolder(source, dest);

  const checkExtractRes = true;

  return checkProfileExtExists(extObj, { profileId }, checkExtractRes);
};

const updateUidFile = async (extObj: IExtObject, opts: IUpdateFileOptions): Promise<void> => {
  const { profileId, password, token, name } = opts;
  const fileContent = await readFile(path.join(EXTENSIONS_PATH, extObj.extName, profileId, 'uid.example.json'), 'utf-8');
  const parsedContent = JSON.parse(fileContent);

  parsedContent.uid = profileId;
  parsedContent.port = global.appPort || 36912;

  if (extObj.name.includes('Pass')) {
    delete parsedContent.port;
    parsedContent.password = password;
    parsedContent.token = token;
    parsedContent.name = name;
  }

  await writeFile(path.join(EXTENSIONS_PATH, extObj.extName, profileId, 'uid.json'), JSON.stringify(parsedContent), { mode: 0o666 });
};

const checkProfileExtExists =
  async (
    extObj: IExtObject,
    opts: IUpdateFileOptions,
    checkExtractRes = false,
  ): Promise<boolean|IExtExistsObj> => {
    const { profileId } = opts;
    const objToReturn = { extPath: '' };
    const isProfileExtExists = await access(path.join(EXTENSIONS_PATH, extObj.extName, profileId, 'manifest.json'))
      .then(() => true)
      .catch(() => false);

    if (checkExtractRes || !isProfileExtExists) {
      return checkExtractRes ? isProfileExtExists : objToReturn;
    }

    const extVersionFileContent = await readFile(path.join(EXTENSIONS_PATH, extObj.versionFile), 'utf-8').catch(() => '');
    console.log(`${extObj.name} extVersionFileContent:`, extVersionFileContent);

    if (!extVersionFileContent) {
      console.log('ext version file not found!!');
      const extensionsFolderContent = await readdir(EXTENSIONS_PATH).catch((error: unknown) => console.log(error));
      console.log(extensionsFolderContent);
      await checkGologinExtensions();

      return objToReturn;
    }

    await updateUidFile(extObj, opts).then(() => true).catch(() => false);

    return { ...objToReturn, extPath: path.join(EXTENSIONS_PATH, extObj.extName, profileId) };
  };

export const createProfileExt =
  async (
    opts: IUpdateFileOptions,
    returnRes = false,
    startProfileSentryTransaction = sentryTransactionDefaultObject,
  ): Promise<IExtExistsObj[]> => {
    const { profileId, token, useCookiesExt = true } = opts;

    const promises = EXTENSIONS_TO_DOWNLOAD.map(async (extObj) => {
      if (
        extObj.name === 'Passwords Ext' && !token ||
        extObj.name === 'Cookies Ext' && !useCookiesExt
      ) {
        throw 'do nothing';
      }

      const extInfo = await checkProfileExtExists(extObj, opts);
      const { extPath } = typeof extInfo === 'boolean' ? { extPath: null } : extInfo;

      if (extPath) {
        return { extPath };
      }

      const isExtArchiveExists = checkDownloadedExtFolder(extObj);
      if (!isExtArchiveExists) {
        startProfileSentryTransaction.setTag('download-gologin-extensions', true);
        console.log('ext version file not found!!');
        const extensionsFolderContent = await readdir(EXTENSIONS_PATH).catch((error: unknown) => console.log(error));
        console.log(extensionsFolderContent);
        await checkGologinExtensions();
      }

      const extractedRes = await copyExtensionFolder(extObj, profileId);
      if (!extractedRes) {
        throw `${extObj.name} extracting failed`;
      }

      await updateUidFile(extObj, opts).then(() => true).catch(() => false);

      return { extPath: path.join(EXTENSIONS_PATH, extObj.extName, profileId) };
    });

    const promiseResult = await Promise.allSettled(promises) as { status: 'fulfilled' | 'rejected'; value: any }[];
    const isAllExtAreExtracted = promiseResult.every(result => result.status === 'fulfilled');
    if (isAllExtAreExtracted || returnRes) {
      return promiseResult.map(result => result.value);
    }

    return createProfileExt(opts, true);
  };
