import path from 'path';

import { makeRequestWithFullResponse } from '../../electron/utils/make-request';
import { sendCommonErrorToSentry } from '../../electron/utils/sentry';
import { IExtMetadata } from '../interfaces/extension.manager.interfaces';
import { ITransactionObject } from '../interfaces/helper.functions.interfaces';
import { ExtensionsExtractor } from '../managers/utils/extensions-extractor';
import { CHROME_EXTENSIONS_PATH } from '../utils/path-platforms';
import withRetry from '../utils/with-retry';

export const getExtMetadata = (extUrl: string): Promise<void|IExtMetadata> => withRetry<IExtMetadata>({
  fn() {
    return makeRequestWithFullResponse({ url: extUrl, method: 'HEAD', retryLimit: 1 });
  },
  retryDelay: 300,
}).catch((error: unknown) => {
  const message = error instanceof Error ? error.message : error;
  if (typeof message === 'string' && message.includes('404')) {
    return;
  }

  sendCommonErrorToSentry({
    message, transactionName: 'update-extension-get-last-version',
    tags: [['scenario', 'update-extensions'], ['name', 'update-extension-get-last-version']],
  });
});

export const getExtVersion = (metadata: string): string => {
  const [extFullName = ''] = metadata.split('/').reverse();
  const [extName = ''] = extFullName.split('.');
  const splitExtName = extName.split('_');
  splitExtName.shift();

  return splitExtName.join('_');
};

export const composeExtractionPromises = (filteredArchives: string[], destPath = CHROME_EXTENSIONS_PATH): Promise<void>[] => (
  filteredArchives.map((extArchivePath: string) => {
    const [archiveName = ''] = extArchivePath.split(path.sep).reverse();
    const [destFolder] = archiveName.split('.');

    return ExtensionsExtractor.extractExtension(extArchivePath, path.join(destPath, destFolder))
      .then(() => ExtensionsExtractor.deleteExtensionArchive(extArchivePath));
  })
);

export const sentryTransactionDefaultObject: ITransactionObject = {
  startChild: () => ({
    finish: () => void 0,
    setStatus: () => void 0,
  }),
  setStatus: () => void 0,
  setTag: () => void 0,
  finish: () => void 0,
};
