/* eslint-disable camelcase */
import { access, rmdir } from 'fs/promises';
import { join, sep } from 'path';

import { IExtractProfileResult } from '../../interfaces/profile-extractor.interfaces';
import { loadCookiesFromFile } from '../../utils/crypto/cookies-decryptor';
import { getPathOs } from '../../utils/path-platforms';
import { decompressProfile } from '../../utils/profile-archiver';
import { sendErrorToSentry } from '../../utils/sentry';
import withRetry from '../../utils/with-retry';

const pathOS = getPathOs();

export const getDolphinProfilesCookies = async (profileIds: string[]): Promise<any[]> => {
  const promises = profileIds.map(async (profileId) => {
    const pathToProfileArchive =
      join(
        pathOS.DOLPHIN_DIR_PATH,
        sep,
        'browser_profiles',
        sep,
        profileId,
        sep,
        `${profileId}.datadir.zip`,
      );

    const isProfileDataExists = await access(pathToProfileArchive)
      .then(() => true, () => false);

    if (!isProfileDataExists) {
      return null;
    }

    const pathToUnzip = join(
      pathOS.BASE_FOLDER,
      sep,
      'dolphin-temp',
      sep,
      profileId,
    );

    const decompressRes = await extractProfile(pathToProfileArchive, pathToUnzip);
    if (decompressRes.status === 'error') {
      return null;
    }

    const pathToCookies = join(
      pathToUnzip,
      sep,
      'Default',
      sep,
      'Cookies',
    );

    const cookiesFileReq = await loadCookiesFromFile(pathToCookies, true).finally(() =>
      rmdir(pathToUnzip, { recursive: true }));

    const { cookies = [] } = cookiesFileReq;
    if (!cookies.length) {
      return null;
    }

    return {
      profileId,
      cookies,
    };
  });

  return Promise.all(promises);
};

const extractProfile = async (zipPath: string, profilePath: string): Promise<IExtractProfileResult>  => {
  console.log(`zipPath: ${zipPath}, profilePath: ${profilePath}`);
  const decompressRes = await withRetry<IExtractProfileResult>({
    fn() {
      return decompressProfile(zipPath, profilePath)
        .then(() => ({ status: 'success' }));
    },
    retryDelay: 300,
  }).catch(async (error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    if (typeof message === 'string' && message === 'Invalid filename') {
      const isExists = await access(zipPath).then(() => true, () => false);
      console.log('input: ', zipPath);
      console.log('isFolderExists: ', isExists);
    }

    sendErrorToSentry({
      message, transactionName: 'extract-dolphin-profile', level: 'fatal',
      tags: [['scenario', 'import-dolphin-cookies']],
    });

    console.error('error extracting dolphin profile zip');
    console.log(zipPath);

    return { status: 'error', error };
  }) || {};

  return decompressRes;
};
