import { access, mkdir } from 'fs/promises';
import path from 'path';

import { BrowserWindow } from 'electron';

import { CONFIG_APP_PATH, getPathOs } from '../../electron/utils/path-platforms';
import { ICheckCompetitorsArgs, IMigrationData } from '../interfaces/check-competitors.interfaces';
import { readConfigFile, saveConfigFile } from '../utils/config-manager';

const pathOS = getPathOs();

const pathToMigrationDataFolder = path.join(CONFIG_APP_PATH, path.sep, 'migration');
const migrationConfigFileName = 'migrationConfig.json';
const configFilePath = path.join(pathToMigrationDataFolder, path.sep, migrationConfigFileName);

export const checkCompetitorsApps = async (window: BrowserWindow|null, args: ICheckCompetitorsArgs): Promise<IMigrationData|null> => {
  const { migrationData = {} } = args;
  if (migrationData.isDolphinChecked) {
    return null;
  }

  const migrationConfig = await readConfigFile(configFilePath);
  if (migrationConfig) {
    const { dolphinChecked } = migrationConfig;
    if (dolphinChecked) {
      return {
        isDolphinChecked: true,
      };
    }
  }

  const dolphinAppPath = pathOS.DOLPHIN_DIR_PATH;
  const dolphinExists = await access(dolphinAppPath).then(() => true, () => false);
  if (!dolphinExists) {
    return null;
  }

  window && window.webContents.send('dolphin-anty-detected');

  const changedConfig = migrationConfig || {};
  changedConfig.dolphinChecked = true;
  await mkdir(pathToMigrationDataFolder, { recursive: true });
  await saveConfigFile(configFilePath, changedConfig, 'migration').catch(() => void 0);

  return {
    isDolphinChecked: true,
  };
};
