import os from 'os';
import util from 'util';

import { OpenProfileViaLink } from '../../common/constants/analytics';
import { DeepLinkProtocol } from '../../common/types/deep-link-protocol';
import { IPidList } from '../interfaces/focus-orbita-utils.interfaces';
import { PLATFORM } from '../utils/path-platforms';
import { sendActionAnalytics } from '../utils/send-action-analytics';

const exec = util.promisify(require('child_process').exec);

export const pidList: IPidList = {};

export const getWinOrbitaRealPid = async (pid: string): Promise<string> => {
  const { stdout: childPID } = await exec(`wmic process get ProcessID, ParentProcessID | find "${pid}"`)
    .catch(() => ({ stdout: '' }));

  if (!childPID) {
    return '';
  }

  const arrayOfPidLinks = childPID.toString().split('\n');
  console.log('array of pid: ', arrayOfPidLinks);
  const [targetArray] = arrayOfPidLinks.filter((pidInfo: string) => pidInfo.split(' ')[0] === pid.toString());
  console.log('target array: ', targetArray);
  const [_, targetPID] = targetArray.split(' ').filter(Boolean);

  return targetPID;
};

export const focusOrbitaWindow = async (profileId: string, deepLinkProtocol?: DeepLinkProtocol): Promise<void> => {
  let targetPID = pidList[profileId];

  if (PLATFORM === 'win32') {
    const addon = require('./win32/pid_raiser.node');
    targetPID = await getWinOrbitaRealPid(pidList[profileId]);

    if (!targetPID) {
      return;
    }

    addon.awaken(Number(targetPID));
  }

  if (PLATFORM === 'linux') {
    const addon = require('./linux/pid_raiser.node');

    const { stdout: childPID } = await exec(`pgrep -P ${targetPID}`)
      .catch(() => ({ stdout: '' }));

    if (!childPID) {
      return;
    }

    addon.awaken(Number(childPID));
  }

  if (PLATFORM === 'darwin' && os.arch() ==='x64') {
    const addon = require('./darwin/pid_raiser_intel.node');
    addon.awaken(Number(targetPID));
  }

  if (PLATFORM === 'darwin' && os.arch() ==='arm64') {
    const addon = require('./darwin/pid_raiser_m1.node');
    addon.awaken(Number(targetPID));
  }

  if (deepLinkProtocol) {
    sendActionAnalytics(OpenProfileViaLink.focused, { mode: deepLinkProtocol, profile: profileId });
  }
};
