import { API_BASE_URL } from '../../common/constants/constants';
import { IAppConfigRecord } from '../interfaces/app-config.interfaces';
import { UserOS } from '../interfaces/common-data-manager.interfaces';
import { loadAppConfig } from '../utils/app-config.utils';
import { getGologinMetaHeader, getOS } from '../utils/userOs';

export class CommonDataManager {
  #API_BASE_URL = API_BASE_URL;

  static instance: any;

  #ACCESS_TOKEN = '';
  #USER_AGENT = '';
  #TWO_FA_KEY = '';
  #OS_WITH_M1 = '';
  #META_HEADER = '';
  #userPlanId = '';
  #APP_CONFIG: IAppConfigRecord = {};
  createCookiesTableQuery = `CREATE TABLE cookies(
        creation_utc INTEGER NOT NULL,
        top_frame_site_key TEXT NOT NULL,
        host_key TEXT NOT NULL,
        name TEXT NOT NULL,
        value TEXT NOT NULL,
        encrypted_value BLOB NOT NULL,
        path TEXT NOT NULL,
        expires_utc INTEGER NOT NULL,
        is_secure INTEGER NOT NULL,
        is_httponly INTEGER NOT NULL,
        last_access_utc INTEGER NOT NULL,
        has_expires INTEGER NOT NULL,
        is_persistent INTEGER NOT NULL,
        priority INTEGER NOT NULL,
        samesite INTEGER NOT NULL,
        source_scheme INTEGER NOT NULL,
        source_port INTEGER NOT NULL,
        is_same_party INTEGER,
        last_update_utc INTEGER NOT NULL,
        source_type INTEGER,
        has_cross_site_ancestor INTEGER
      );`;

  constructor() {
    if (!CommonDataManager.instance) {
      CommonDataManager.instance = this;

      this.getUserOS().then(res => {
        this.#OS_WITH_M1 = res || '';
      });

      getGologinMetaHeader().then((result) => {
        this.#META_HEADER = result;
      });

      loadAppConfig()
        .then((result) => {
          if (result) {
            this.#APP_CONFIG = result;
            console.log('Loaded config:', result);
          }
        })
        .catch(() => null);
    }

    return CommonDataManager.instance;
  }

  getUserOS(): Promise<UserOS | null> {
    return getOS().then(res => res).catch(() => null);
  }

  get userAgent(): string {
    return this.#USER_AGENT;
  }

  set userAgent(userAgent) {
    if (!userAgent) {
      return;
    }

    this.#USER_AGENT = userAgent;
  }

  get userPlanId(): string {
    return this.#userPlanId;
  }

  set userPlanId(userPlanId) {
    this.#userPlanId = userPlanId;
  }

  get accessToken(): string {
    return this.#ACCESS_TOKEN;
  }

  set accessToken(accessToken) {
    this.#ACCESS_TOKEN = accessToken;
  }

  get twoFaKey(): string {
    return this.#TWO_FA_KEY;
  }

  set twoFaKey(twoFaKey) {
    if (!twoFaKey) {
      return;
    }

    this.#TWO_FA_KEY = twoFaKey;
  }

  get apiBaseUrl(): string {
    return this.#API_BASE_URL;
  }

  get userOs(): string {
    return this.#OS_WITH_M1;
  }

  get metaHeader(): string {
    return this.#META_HEADER;
  }

  get appConfig(): IAppConfigRecord {
    return this.#APP_CONFIG;
  }

  set appConfig(config: IAppConfigRecord) {
    if (!config) {
      return;
    }

    this.#APP_CONFIG = config;
  }
}
