import path from 'path';

import { BrowserWindow, nativeTheme } from 'electron';

import { ThemeType } from '../../app/interfaces/theme.type';
import { IAppConfigRecord } from '../interfaces/app-config.interfaces';
import { readConfigFile, saveConfigFile } from './config-manager';
import { CONFIG_APP_PATH } from './path-platforms';

const configFileName = 'appConfig.json';
const configFilePath = path.join(CONFIG_APP_PATH, configFileName);

export const setupAppConfig = async (defaultWindowConfig: IAppConfigRecord): Promise<void> => {
  const appConfig = await loadAppConfig();
  if (appConfig) {
    const {
      windowWidth, windowHeight, windowPosX = 0, windowPosY = 0, isMaximized = false, isFullScreen = false,
      theme = 'system',
    } = appConfig;

    windowWidth && Number(windowWidth) > 1000 && (defaultWindowConfig.windowWidth = Number(windowWidth));
    windowHeight && Number(windowHeight) > 500 && (defaultWindowConfig.windowHeight = Number(windowHeight));
    !isFullScreen && (defaultWindowConfig.windowPosX = Number(windowPosX));
    !isFullScreen && (defaultWindowConfig.windowPosY = Number(windowPosY));

    defaultWindowConfig.isMaximized = isMaximized;
    defaultWindowConfig.isFullScreen = isFullScreen;
    let currentTheme = theme;
    if (currentTheme === 'system') {
      currentTheme = nativeTheme?.shouldUseDarkColors ? 'dark' : 'light';
    }

    const backgroundColor = currentTheme === 'dark' ? '#191A23' : '#F9F9F9';
    defaultWindowConfig.backgroundColor = backgroundColor;
  }
};

export const saveAppConfig = async (window: BrowserWindow, translationData: any, theme: ThemeType): Promise<void> => {
  const configToSave = {} as IAppConfigRecord;

  const { language } = translationData || {};
  language && (configToSave.language = language);

  const { width = 1024, height = 500, x: xCoordinate = 0, y: yCoordinate = 0 } = window && window.getBounds() || {};
  configToSave.windowWidth = width;
  configToSave.windowHeight = height;
  configToSave.windowPosX = xCoordinate;
  configToSave.windowPosY = yCoordinate;

  configToSave.isMaximized = !!window.isMaximized();
  configToSave.isFullScreen = !!window.isFullScreen();
  configToSave.theme = theme;

  await writeToAppConfig(configToSave).catch((error: unknown) => console.log(error));
};

export const loadAppConfig = async (): Promise<IAppConfigRecord|void> => readConfigFile(configFilePath);

export const writeToAppConfig = async (config: IAppConfigRecord): Promise<void> => {
  const { CommonDataManager } = await import('../managers/common-data-manager');
  const CommonDataManagerInst = new CommonDataManager();
  const currentConfig = CommonDataManagerInst.appConfig;

  const configRecords = Object.entries(config);
  if (!configRecords.length) {
    return;
  }

  configRecords.forEach(record => {
    const [key, value = null] = record;
    if (key && value !== null) {
      currentConfig[key] = value;
    }
  });

  CommonDataManagerInst.appConfig = currentConfig;
  await saveConfigFile(configFilePath, currentConfig);
};

export const clearAppConfig = async (): Promise<void> => {
  const { CommonDataManager } = await import('../managers/common-data-manager');
  const CommonDataManagerInst = new CommonDataManager();
  CommonDataManagerInst.appConfig = {};
  await saveConfigFile(configFilePath, CommonDataManagerInst.appConfig);
};
