import { createWriteStream, createReadStream } from 'fs';
import { mkdir, writeFile } from 'fs/promises';
import path from 'path';

import unzipper from 'unzipper';
import yauzl from 'yauzl';

export const extractArchiveAuzl = (pathToZip: string, targetPath: string): Promise<void> => new Promise((resolve, reject) =>
  yauzl.open(pathToZip, { lazyEntries: true, autoClose: false }, (error, zipFile) => {
    if (error) {
      reject(error);

      return;
    }

    if (!zipFile) {
      reject(new Error('no zipFile'));

      return;
    }

    zipFile.on('error', (zipFileError) => {
      console.log('zip File Error');
      zipFile.close();
      reject(zipFileError);
    });

    zipFile.readEntry();
    const entries: any[] = [];

    zipFile.on('entry', (entry) => {
      zipFile.readEntry();
      if (/\/$/.test(entry.fileName)) {
        return;
      }

      entries.push(entry);
    });

    zipFile.on('end', () => {
      console.log('zipFile entries ended');

      return Promise.all(entries.map(async (entry) => mkdir(path.join(targetPath, path.dirname(entry.fileName)), { recursive: true })
        .then(() =>
          new Promise((resolveWrite, rejectWrite) => {
            zipFile.openReadStream(entry, (err, readStream) => {
              if (err) {
                rejectWrite(err);

                return;
              }

              readStream.on('error', (readStreamError) => {
                readStream.destroy();
                rejectWrite(readStreamError);
              });

              const writeStream = createWriteStream(path.join(targetPath, entry.fileName), { mode: 0o666 });
              writeStream.on('error', (writeError) => {
                writeStream.end();
                rejectWrite(writeError);
              });

              writeStream.on('close', resolveWrite);

              readStream.pipe(writeStream);
            });
          }),
        )))
        .then(() => {
          console.log('decompress resolved');
          resolve();
          zipFile.close();
        })
        .catch((promiseError) => {
          console.log('rejected');
          reject(promiseError);
          zipFile.close();
        });
    });
  }),
);

export const extractArchive = (pathToZip: string, targetPath: string): Promise<void> => new Promise((resolve, reject) => {
  const readStream = createReadStream(pathToZip);
  readStream.on('error', reject);

  readStream.pipe(unzipper.Extract({ path: targetPath }))
    .on('close', async () => {
      await writeFile(path.join(targetPath, 'First Run'), '', { mode: 0o666 }).catch(() => null);
      console.log('Extraction complete.');
      resolve();
    })
    .on('error', (err: Error) => {
      reject(err);
    });
});
