import EventEmitter from 'events';
import fs from 'fs';
import { mkdir, readdir, rmdir, unlink } from 'fs/promises';
import path from 'path';

import { EXTENSION_URL } from '../constants/constants';
import { composeExtractionPromises, getExtMetadata, getExtVersion } from '../helpers/helper.functions';
import { IExtCheckResult, IExtsSettings } from '../interfaces/extension.manager.interfaces';
import { downloadWithRetryBuffer } from '../utils/downloadWithRetry';
import { makeRequest } from '../utils/make-request';
import { CHROME_EXTENSIONS_PATH, USER_EXTENSIONS_PATH } from '../utils/path-platforms';
import { sendCommonErrorToSentry, sendErrorToSentry } from '../utils/sentry';
import { CommonDataManager } from './common-data-manager';
import { UserExtensionsManager } from './user.extensions.manager';
import { crxToZip } from './utils/crx-to-zip.converter';

const CommonDataManagerInst = new CommonDataManager();

export class ExtensionsManager extends UserExtensionsManager {
  #existedChromeExtensions: string[] = [];
  #inited = false;
  #deleteProfileExtFolders = false;
  #extensionsUpdating = true;
  #extensionsInProgress: string[] = [];
  #blockEventEmitter = new EventEmitter();
  static instance: any;

  constructor() {
    super();
    if (!ExtensionsManager.instance) {
      ExtensionsManager.instance = this;
    }

    return ExtensionsManager.instance;
  }

  async init(): Promise<void> {
    if (this.#inited) {
      return Promise.resolve();
    }

    const promises = [
      mkdir(CHROME_EXTENSIONS_PATH, { recursive: true })
        .then(() => readdir(CHROME_EXTENSIONS_PATH))
        .then((filesList: string[]) => {
          this.#existedChromeExtensions = filesList.filter(extPath => !extPath.includes('.zip')).map(extFolder => extFolder.split('@')[0]);

          return filesList.map(fileName => fileName.includes('.zip') ?
            unlink(path.join(CHROME_EXTENSIONS_PATH, fileName)) :
            Promise.resolve());
        })
        .then((promisesToDelete: Promise<void>[]) => Promise.all(promisesToDelete))
        .catch((err: unknown) => console.log('ExtensionsManager init error:', err)),
      mkdir(USER_EXTENSIONS_PATH, { recursive: true })
        .then(() => readdir(USER_EXTENSIONS_PATH))
        .then((filesList: string[]) => {
          this.existedUserExtensions = filesList.filter(extPath => !extPath.includes('.zip'));

          return filesList.map(fileName => fileName.includes('.zip') ?
            unlink(path.join(USER_EXTENSIONS_PATH, fileName)) :
            Promise.resolve());
        })
        .then((promisesToDelete: Promise<void>[]) => Promise.all(promisesToDelete))
        .catch(() => null),
    ];

    return Promise.all(promises).then(() => {
      this.#inited = true;
    });
  }

  get isInited(): boolean {
    return this.#inited;
  }

  get deleteProfileExtFolders(): boolean {
    return this.#deleteProfileExtFolders;
  }

  get existedChromeExtensionsList(): string[] {
    return this.#existedChromeExtensions;
  }

  deleteExtIdFromExtensionsInProgress(extIdToDelete: string): void {
    this.#extensionsInProgress = this.#extensionsInProgress.filter(extId => extId !== extIdToDelete);

    if (!this.#extensionsInProgress.length) {
      this.#blockEventEmitter.emit('unblocked');
    }
  }

  async checkChromeExtensions(profileExtensions: string[] = [], isFromExtManager = false): Promise<string[]> {
    if (!(Array.isArray(profileExtensions) && profileExtensions.length)) {
      return [];
    }

    await mkdir(CHROME_EXTENSIONS_PATH, { recursive: true });

    if (!isFromExtManager && this.#extensionsInProgress.length) {
      // if extensions in downloading progress we need to wait until it finishes
      await new Promise(resolve => this.#blockEventEmitter.on('unblocked', resolve));
    }

    const extensionsToDownload = this.#getExtensionsToDownload(profileExtensions);
    // if we dont need to install extension from manager we dont need to return path for orbita
    if (isFromExtManager && !extensionsToDownload.length) {
      return [];
    }

    // if we press install in extensions manager we need to track progress
    if (isFromExtManager) {
      this.#extensionsInProgress.push(profileExtensions[0]);
    }

    const downloadedArchives = await this.downloadChromeExtensions(extensionsToDownload).catch(() => []);
    const filteredArchives = downloadedArchives.filter(Boolean) as string[];
    console.log('splitting downloadedExtensionIds: ', filteredArchives);
    const downloadedExtensionIds = filteredArchives.map((archivePath: string) => {
      const [archiveName] = archivePath.split(path.sep).reverse();
      const [downloadedExtensionId] = archiveName.split('@');

      return downloadedExtensionId;
    });

    this.#existedChromeExtensions = [...this.#existedChromeExtensions, ...downloadedExtensionIds];

    const promises = composeExtractionPromises(filteredArchives);

    await Promise.all(promises).catch(() => null);

    if (isFromExtManager) {
      this.deleteExtIdFromExtensionsInProgress(profileExtensions[0]);
    }

    return this.getExtStrToIncludeAsOrbitaParam(profileExtensions);
  }

  #getExtensionsToDownload(profileExtensions: string[]): string[] {
    const existedOriginalIds = [...this.#existedChromeExtensions, ...this.existedUserExtensions];

    return profileExtensions.reduce((res: string[], val) => {
      console.log('splitting extrensions name: ', profileExtensions);
      const [originalId] = val.split('@');
      const extensionExists = existedOriginalIds.includes(originalId);
      if (!extensionExists) {
        res.push(val);
      }

      return res;
    }, []);
  }

  async downloadChromeExtensions(idsToDownload: string[] = []): Promise<string[]> {
    if (!(Array.isArray(idsToDownload) && idsToDownload.length)) {
      return [];
    }

    console.log('splitting idsToDownload: ', idsToDownload);
    const promises = idsToDownload.map(async (id: string) => {
      const [originalId] = id.split('@');
      const extUrl = EXTENSION_URL.replace('{ext_id}', originalId);

      const uploadedProfileMetadata = await getExtMetadata(extUrl);
      if (!uploadedProfileMetadata) {
        return '';
      }

      const reqPath = uploadedProfileMetadata.req.path;
      const extVer = getExtVersion(reqPath);

      const buffer = await downloadWithRetryBuffer({
        url: extUrl,
      }).catch((err: unknown) => {
        const message = err instanceof Error ? err.message : err;
        sendCommonErrorToSentry({
          message,
          transactionName: 'download-chrome-extension',
        });

        return 'error';
      });

      if (buffer instanceof String && buffer === 'error') {
        return '';
      }

      let zipExt: Buffer;
      try {
        if (buffer instanceof Buffer) {
          zipExt = crxToZip(buffer);
        } else {
          throw Error('error, no buffer');
        }
      } catch (error) {
        const message = error instanceof Error ? error.message : 'unknown';

        if (message === 'error, no buffer') {
          return '';
        }

        if (message === 'unknown') {
          console.log(error);
        }

        if (message.includes('Does not start with Cr24')) {
          return '';
        }

        sendErrorToSentry({
          message,
          transactionName: 'decompress-google-chrome-extension',
          level: 'warning',
          tags: [['scenario', 'download-extensions']],
        });

        return '';
      }

      const archiveZipPath = path.join(CHROME_EXTENSIONS_PATH, originalId + '@' + extVer + '.zip');

      const archiveZip = fs.createWriteStream(archiveZipPath, { mode: 0o666 });
      archiveZip.write(zipExt);
      archiveZip.close();

      return new Promise(resolve => {
        archiveZip.on('close', () => resolve(archiveZipPath));
        archiveZip.on('error', () => resolve(''));
      });
    });

    return Promise.all(promises) as Promise<string[]>;
  }

  async updateExtensions(): Promise<string|void> {
    const fileList = await readdir(CHROME_EXTENSIONS_PATH).catch(() => []);
    if (!fileList.length) {
      return;
    }

    const oldFolders: string[] = [];

    const versionCheckPromises = fileList.map(async (extension: string) => {
      if (!extension.includes('@') || extension.includes('.zip')) {
        return '';
      }

      const [originalId, currentVersion] = extension.split('@');
      const extUrl = EXTENSION_URL.replace('{ext_id}', originalId);
      const uploadedProfileMetadata = await getExtMetadata(extUrl);
      if (!uploadedProfileMetadata) {
        return '';
      }

      const reqPath = uploadedProfileMetadata.req.path;
      const availableVersion = getExtVersion(reqPath);

      if (!uploadedProfileMetadata || currentVersion === availableVersion) {
        return '';
      }

      oldFolders.push(path.join(CHROME_EXTENSIONS_PATH, extension));

      return originalId;
    });

    const extensionsNames = (await Promise.all(versionCheckPromises)).filter(Boolean) as string[];
    const archivesPaths = (await this.downloadChromeExtensions(extensionsNames)).filter(Boolean) as string[];
    const extractionPromises = composeExtractionPromises(archivesPaths);
    await Promise.all(extractionPromises);

    const removeFoldersPromises = oldFolders.map(folder => (
      rmdir(folder, { recursive: true, maxRetries: 3 }).catch(() => void 0)
    ));

    await Promise.all(removeFoldersPromises).then(() => {
      this.#extensionsUpdating = false;
    });
  }

  async insertExtensionsToDb(extensionsIds: string[], pathToExtensions: string = CHROME_EXTENSIONS_PATH): Promise<unknown> {
    if (!extensionsIds?.length) {
      return;
    }

    const checkResponse = await makeRequest<IExtCheckResult>({
      url: `${CommonDataManagerInst.apiBaseUrl}/extensions/check`, method: 'POST', body: { extensionsIds },
    });

    const { extensionsToAdd = [] } = checkResponse;

    if (!extensionsToAdd.length) {
      return;
    }

    const extensionsToUpdate = await this.getExtensionsNameAndImage(extensionsToAdd, pathToExtensions);

    return makeRequest({
      url: `${CommonDataManagerInst.apiBaseUrl}/extensions/create`, method: 'POST', body: { extensionsInfo: extensionsToUpdate },
    });
  }

  getExtensionsToInstall(extensionsFromPref: IExtsSettings, extensionsFromDB: string[]): string[] {
    if (!extensionsFromPref) {
      return [];
    }

    const objectEntries = Object.entries(extensionsFromPref);
    const extensionsInPref = objectEntries?.map(([_, settings]) => {
      const [extFolderName] = settings.path.split(path.sep).reverse();
      const [originalId] = extFolderName.split('@');

      return originalId;
    }) || [];

    return extensionsFromDB.reduce((acc: string[], extension) => {
      const [extFolderName] = extension.split(path.sep).reverse();
      const [originalId] = extFolderName.split('@');
      if (!extensionsInPref.includes(originalId)) {
        acc.push(extension);
      }

      return acc;
    }, []);
  }
}
