import got, { CancelableRequest, Headers, Response } from 'got';

import { IGetOptions, IMakeRequestProps, IMakeRequestPropsOptions, IMakeRequestWithFullResponseProps } from '../interfaces/make-request.interfaces';
import { CommonDataManager } from '../managers/common-data-manager';

const CommonDataManagerInst = new CommonDataManager();

const getBaseHeaders = (commonDataManager: CommonDataManager): Headers => ({
  Authorization: `Bearer ${commonDataManager.accessToken}`,
  'user-agent': commonDataManager.userAgent,
  'x-two-factor-token': commonDataManager.twoFaKey || '',
  'GoLogin-Meta-Header': commonDataManager.metaHeader || '',
});

const getOptions = ({ method, headers = getBaseHeaders(CommonDataManagerInst), timeout, retryLimit }: IGetOptions): IMakeRequestPropsOptions => ({
  method,
  headers,
  timeout: {
    request: timeout,
  },
  retry: {
    limit: retryLimit,
    errorCodes: [
      'ETIMEDOUT',
      'ECONNRESET',
      'EADDRINUSE',
      'ECONNREFUSED',
      'EPIPE',
      'ENOTFOUND',
      'EAI_AGAIN',
    ],
  },
});

export const makeRequest = <T>(props: IMakeRequestProps): Promise<T> => {
  const {
    method = 'GET',
    url,
    body = {},
    headers,
    timeout = 30 * 1000,
    json = true,
    isBuffer = false,
    retryLimit = 3,
  } = props;

  const options = getOptions({ method, headers, timeout, retryLimit });
  if (!['GET', 'HEAD'].includes(method)) {
    options.json = body;
  }

  if (isBuffer) {
    delete options.json;
    options.body = body;
  }

  if (json) {
    return got(url, options).json();
  }

  return got(url, options).then(res => res.body) as Promise<T>;
};

export const makeRequestWithFullResponse = (
  props: IMakeRequestWithFullResponseProps,
): CancelableRequest<Response<string>> => {
  const {
    method = 'GET',
    url,
    body = {},
    headers,
    timeout = 30 * 1000,
    isBuffer = false,
    retryLimit = 3,
  } = props;

  const options = getOptions({ method, headers, timeout, retryLimit });
  if (!['GET', 'HEAD'].includes(method)) {
    options.json = body;
  }

  if (isBuffer) {
    delete options.json;
    options.body = body;
  }

  return got(url, options);
};
