import packageSettings from '../../../package.json';
import { ICheckCompetitorsAppsData } from '../interfaces/check-competitors.interfaces';
import { CommonDataManager } from '../managers/common-data-manager';
import { makeRequest } from '../utils/make-request';

const CommonDataManagerInst = new CommonDataManager();

export const electronSendActionAnalytics = (action: string, extraInfo: any = {}): Promise<any> => {
  const { version } = packageSettings;
  extraInfo.appVersion = version;

  return makeRequest({
    method: 'POST',
    url: `${CommonDataManagerInst.apiBaseUrl}/analytics/add_action_analytics`,
    body: { action, ...extraInfo },
  });
};

export const syncGologinCompetitorsMetadata = (gologinMetadata: ICheckCompetitorsAppsData): Promise<any> => makeRequest({
  method: 'PUT',
  url: `${CommonDataManagerInst.apiBaseUrl}/gologin-settings/gologin-metadata`,
  body: gologinMetadata,
});
