import { makeRequest } from './make-request';

interface IChangableAppConfig {
  cookies: {
    createCookiesTableQuery: string;
  };
}

export const getChangableAppConfigFromBack = async (): Promise<void> => {
  const { CommonDataManager } = await import('../managers/common-data-manager');
  const CommonDataManagerInst = new CommonDataManager();
  const configData =
    await makeRequest<IChangableAppConfig>({ url: `${CommonDataManagerInst.apiBaseUrl}/gologin-global-settings/changable-app-config` });

  if (configData.cookies.createCookiesTableQuery) {
    CommonDataManagerInst.createCookiesTableQuery = configData.cookies.createCookiesTableQuery;
  }
};
