import { createWriteStream } from 'fs';
import { access, mkdir, readdir, readFile, stat } from 'fs/promises';
import path from 'path';

import yazl from 'yazl';

import { extractArchive, extractArchiveAuzl } from './extract-archive';
import { CACHED_PROFILES_FOLDER } from './path-platforms';
import { getDirectoriesToDeleteForNode } from './profile-directories-to-remove';
import { IAddDefaultFolder } from '../interfaces/archiving.interfaces';
import { tempArchiveName } from '../managers/profile/close-orbita';

export const archiveProfile = async (profileFolder = '', profileId: string): Promise<Buffer> => {
  const folderExists = await access(profileFolder).then(() => true, () => false);
  if (!folderExists) {
    throw new Error('Invalid profile folder path: ' + profileFolder);
  }

  const defaultFolderPath = path.join(profileFolder, 'Default');

  const archive = new yazl.ZipFile();
  let isArchiveError = false;
  let archiveError;

  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  archive.once('error', (error: any) => {
    archive.end();
    console.log('archive error', error);
    isArchiveError = true;
    archiveError = error;
  });

  await addDefaultFolderToArchive({ folderPath: defaultFolderPath, archive, relativePath: 'Default' });

  try {
    archive.addFile(path.join(profileFolder, 'First Run'), '/');
  } catch(error: unknown) {
    archive.addBuffer(Buffer.from(''), 'First Run');
  }

  const cachedProfilePath = `${path.join(CACHED_PROFILES_FOLDER, profileId, tempArchiveName)}.zip`;

  if (isArchiveError) {
    throw archiveError;
  }

  await mkdir(path.join(CACHED_PROFILES_FOLDER, profileId), { recursive: true }).catch((error) => {
    console.log('mkdir error', error);
    console.log('cached profile path', cachedProfilePath);
  });

  await new Promise((resolve, reject) => {
    const stream = createWriteStream(cachedProfilePath, { mode: 0o666 });
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    archive.once('error', (error: any) => {
      archive.end();
      stream.destroy();
      console.log('archive error inside promise', error);
      reject(error);
    });

    stream.on('error', (error) => {
      stream.destroy();
      reject(error);
    });
    stream.on('close', () => {
      resolve('');
    });

    archive.end();
    archive.outputStream.pipe(stream);

  });

  // TODO:     ,      
  return readFile(cachedProfilePath);
};

export const decompressProfile = (zipPath = '', profileFolder = ''): Promise<void> => extractArchiveAuzl(zipPath, profileFolder).catch(() => extractArchive(zipPath, profileFolder));

export const getBufferOfArchive = async (profileId: string, archiveName: string): Promise<Buffer> => {
  const zipPath = path.join(CACHED_PROFILES_FOLDER, profileId, archiveName + '.zip');

  return readFile(zipPath).catch((error: unknown) => {
    console.log(error);

    throw Error('zip error');
  });
};

const addDefaultFolderToArchive = async ({ folderPath, archive, relativePath }: IAddDefaultFolder): Promise<void> => {
  const directoriesToNotInclude = getDirectoriesToDeleteForNode();
  const defaultFolderContent = await readdir(folderPath);

  for (const folderContent of defaultFolderContent) {
    const currentFilePath = path.resolve(folderPath, folderContent);
    const folderContentStat = await stat(currentFilePath).catch(() => null);

    if (!folderContentStat) {
      continue;
    }

    if (directoriesToNotInclude.includes(`${path.sep}${path.join(relativePath, folderContent)}`)) {
      continue;
    }

    if (folderContentStat.isDirectory()) {
      archive.addEmptyDirectory(path.join(relativePath, folderContent));
      await addDefaultFolderToArchive({ folderPath: currentFilePath, archive, relativePath: path.join(relativePath, folderContent) });

      continue;
    }

    archive.addFile(currentFilePath, `${relativePath}/${folderContent}`);
  }
};
