import moment from 'moment';

import { setProxyStatuses } from './utils/api';
import { TIMEZONE_CHECK_URL } from '../constants/constants';
import { IProtocol, ICheckProxyResult } from '../interfaces/proxy.service.interfaces';
import { IProfileData } from '../interfaces/start-profile.renderer.interfaces';
import {
  IGetGologinOrTorProxyParams,
  IGetProxyCatchBlockParams,
  IGetProxyResultBlockParams,
  IGetTimezoneResult,
  IProfileTimings,
  ITimezoneManagerConstructorData,
} from '../interfaces/timezone.manager.interfaces';
import { ProxyService } from '../services/proxy.service';
import { makeRequest } from '../utils/make-request';
import { makeProxyRequestWithTimeout } from '../utils/proxy-request-with-timeout';

export class TimezoneManager {
  #proxyInfo;
  #proxyEnabled = false;
  #ProfileReadyChecker;

  constructor(data: ITimezoneManagerConstructorData) {
    this.#proxyInfo = data.proxyInfo;
    this.#proxyEnabled = data.proxyEnabled;
    this.#ProfileReadyChecker = data.ProfileReadyCheckerInst;
  }

  set proxyInfo(data: any) {
    this.#proxyInfo = data;
  }

  set proxyEnabled(state: boolean) {
    this.#proxyEnabled = state;
  }

  async getTimeZone(profileData: IProfileData, timings: IProfileTimings): Promise<IGetTimezoneResult> {
    const startTime = moment.utc();
    const { mode, host, port, username, password } = this.#proxyInfo;

    const result: IGetTimezoneResult = {
      status: 'error',
      body: 'something went wrong',
      type: 'proxy',
    };

    if (!this.#proxyEnabled) {
      await makeRequest<ICheckProxyResult>({ url: TIMEZONE_CHECK_URL, method: 'GET' })
        .then((timezoneInfo) => {
          result.status = 'success';
          result.body = timezoneInfo;

          timings.timeZoneCheck = moment.utc().diff(startTime);
          this.#ProfileReadyChecker.isProxyReady = result;
        })
        .catch((error: unknown) => {
          console.log(error);

          result.status = 'error';
          result.body = 'notifications.error.timezoneRequestError';
          result.type = 'timezone';

          timings.timeZoneCheck = moment.utc().diff(startTime);
          this.#ProfileReadyChecker.isProxyReady = result;
        });
    }

    if (['gologin', 'tor'].includes(mode)) {
      await getGologinTorProxy({
        mode,
        profileData,
        result,
        timings,
        startTime,
        profileReadyChecker: this.#ProfileReadyChecker,
      }).catch((error: unknown) => {
        catchBlock({
          error,
          result,
          timings,
          startTime,
          profileReadyChecker: this.#ProfileReadyChecker,
        });
      });
    }

    if (['socks4', 'socks5', 'possh', 'http', 'geolocation'].includes(mode)) {
      const ping = await ProxyService.getPing(this.#proxyInfo);

      if (!(ping || ping === 0) || ping === 'Check timeout exceeded') {
        return {
          status: 'error',
          type: 'proxy',
          body: 'profilesTable.profileStatusTooltip.proxy.pingFail',
        };
      }

      const isHttpProxyType = ['http', 'geolocation'].includes(mode);
      await makeProxyRequestWithTimeout({
        mode,
        type: isHttpProxyType ? 'http' : mode,
        host: isHttpProxyType ? host.replace(/(http:\/\/|https:\/\/)/g, '') : host,
        port,
        username,
        password,
      })
        .then(({ response: timeZoneResult, protocol }) => {
          const trafficIsOver =
              typeof timeZoneResult === 'string' && timeZoneResult.includes('402') && mode === 'geolocation';

          if (trafficIsOver) {
            result.status = 'error';
            result.body = 'notifications.error.proxyTrafficIsOver';
            result.type = 'proxy';

            timings.timeZoneCheck = moment.utc().diff(startTime);
            this.#ProfileReadyChecker.isProxyReady = result;

            return;
          }

          resultBlock({
            timeZoneResult,
            result,
            timings,
            startTime,
            protocol,
            profileReadyChecker: this.#ProfileReadyChecker,
          });
        })
        .catch((error: unknown) => {
          catchBlock({
            error,
            result,
            timings,
            startTime,
            profileReadyChecker: this.#ProfileReadyChecker,
          });
        });
    }

    return result;
  }

  static updateProxyStatus = async (proxyId: string, timezoneResult: IGetTimezoneResult): Promise<void> => {
    if (!proxyId) {
      return;
    }

    if (typeof timezoneResult.body === 'string') {
      const proxyData = {
        _id: proxyId,
        status: false,
        error: timezoneResult.body,
        checkDate: moment().unix(),
      };

      setProxyStatuses([proxyData]);

      return;
    }

    const { body: timezoneParams } = timezoneResult;
    const proxyData = {
      _id: proxyId,
      status: timezoneResult.status === 'success',
      country: timezoneParams.country,
      city: timezoneParams.city,
      error: timezoneResult.status !== 'success' ? timezoneResult.status : null,
      checkDate: moment().unix(),
      languages: timezoneParams.languages,
      timezone: timezoneParams.timezone,
      lastIp: timezoneParams.ip || '',
    };

    setProxyStatuses([proxyData]);
  };
}

const getGologinTorProxy = async (params: IGetGologinOrTorProxyParams): Promise<void> => {
  const { profileData = { autoProxyServer: '' } } = params;
  const splittedAutoProxyServer = profileData.autoProxyServer.split('://');
  const [type = 'http'] = splittedAutoProxyServer;
  const [freeProxyHost, freeProxyPort] = splittedAutoProxyServer[1].split(':');

  const proxyParams = {
    mode: params.mode,
    type: type as IProtocol,
    host: freeProxyHost,
    port: Number(freeProxyPort),
    username: params.profileData.autoProxyUsername,
    password: params.profileData.autoProxyPassword,
  };

  const ping = await ProxyService.getPing(proxyParams);

  if (!(ping || ping === 0) || ping === 'Check timeout exceeded') {
    throw new Error('profilesTable.profileStatusTooltip.proxy.pingFail');
  }

  await makeProxyRequestWithTimeout(proxyParams)
    .then(({ response: timeZoneResult, protocol }) => {
      if (typeof timeZoneResult === 'string') {
        throw new Error(timeZoneResult);
      }

      params.profileData.gologin.proxy.username = params.profileData.autoProxyUsername;
      params.profileData.gologin.proxy.password = params.profileData.autoProxyPassword;

      params.result.status = 'success';
      params.result.protocol = protocol;
      params.result.body = timeZoneResult;

      params.timings.timeZoneCheck = moment.utc().diff(params.startTime);
      params.profileReadyChecker.isProxyReady = params.result;
    })
    .catch((error: unknown) => {
      catchBlock({
        error,
        result: params.result,
        timings: params.timings,
        startTime: params.startTime,
        profileReadyChecker: params.profileReadyChecker,
      });
    });
};

const resultBlock = (data: IGetProxyResultBlockParams): void => {
  if (typeof data.timeZoneResult === 'string') {
    throw new Error(data.timeZoneResult);
  }

  data.result.status = 'success';
  data.result.protocol = data.protocol;
  data.result.body = data.timeZoneResult;

  data.timings.timeZoneCheck = moment.utc().diff(data.startTime);
  data.profileReadyChecker.isProxyReady = data.result;
};

const catchBlock = (data: IGetProxyCatchBlockParams): void => {
  console.log(data.error);

  data.result.status = 'error';
  data.result.body = data.error instanceof Error ? data.error.message : 'notifications.error.somethingWentWrong';
  data.result.type = 'proxy';

  data.timings.timeZoneCheck = moment.utc().diff(data.startTime);
  data.profileReadyChecker.isProxyReady = data.result;
};
