import { BrowserWindow } from 'electron';

import { shareProfilesViaLink } from './api';
import { PROFILE_SHARED_KEY_NAME } from '../../../../common/constants/constants';
import { PROFILE_SHARE_DEEP_LINK_EVENT_NAME } from '../../../../common/constants/events';
import { PROFILE_LIST_PAGE } from '../../../../common/constants/routes';
import { CommonDataManager } from '../../../managers/common-data-manager';
import { updateDeletedProfiles } from '../../../managers/profile/renderer';
import { sendErrorToSentry } from '../../sentry';
import { ACCESS_TOKEN_STORAGE_KEY, BASE_URL } from '../constants';

const CommonDataManagerInst = new CommonDataManager();

export const handleShare = async (window: BrowserWindow, responseUrl = ''): Promise<void> => {
  const { pathname, searchParams } = new URL(responseUrl);
  const [profileName, linkId] = pathname.split('/').filter(Boolean);
  const accessTokenFromWeb = searchParams.get('access-token');
  if (!(profileName && linkId)) {
    return;
  }

  const accessTokenFromDesktop = CommonDataManagerInst.accessToken;
  try {
    if (!accessTokenFromDesktop && accessTokenFromWeb) {
      CommonDataManagerInst.accessToken = accessTokenFromWeb;
      await window.webContents.executeJavaScript(`
        localStorage.setItem('${ACCESS_TOKEN_STORAGE_KEY}', '${accessTokenFromWeb}');
      `);
    }

    if (!CommonDataManagerInst.accessToken) {
      return;
    }

    const response = await shareProfilesViaLink(profileName, linkId);
    if (!response || typeof response === 'string') {
      return window.webContents.send(PROFILE_SHARE_DEEP_LINK_EVENT_NAME, { message: response });
    }

    updateDeletedProfiles([response.id]);
    const urlCurrent = window.webContents.getURL();
    if (urlCurrent.includes(PROFILE_LIST_PAGE)) {
      return window.webContents.send(PROFILE_SHARE_DEEP_LINK_EVENT_NAME, {
        profile: { ...response, isShared: true },
        isUpdatePage: urlCurrent.includes('/update/'),
      });
    }

    const profileJson = JSON.stringify({ ...response, isShared: true });
    await window.webContents.executeJavaScript(`
      sessionStorage.setItem('${PROFILE_SHARED_KEY_NAME}', '${profileJson}');
    `).catch(console.error);

    return window.loadURL(`${BASE_URL}#${PROFILE_LIST_PAGE}`);
  } catch (error) {
    const message = error instanceof Error ? error.message : JSON.stringify(error);
    sendErrorToSentry({
      message, transactionName: 'share-profiles-via-link-error',
      tags: [['scenario', 'share-profiles-via-link']],
    });
  }
};
