import { Stats } from 'fs';
import { rmdir, readdir, mkdir, writeFile, stat, unlink, rename } from 'fs/promises';
import path from 'path';

import moment from 'moment';

import { IProfileStorageInfo } from '../interfaces/profile-starter.manager.interfaces';
import { checkFileSizeSync } from '../../electron/utils/utils';
import { ICachedProfileDataObject } from '../interfaces/profile.caching.manager.interfaces';
import { CACHED_PROFILES_FOLDER } from '../utils/path-platforms';

export class ProfileCachingManager {
  async init(): Promise<string | null | undefined> {
    return mkdir(CACHED_PROFILES_FOLDER, { recursive: true })
      .catch(() => null);
  }

  async copyClosedProfileToCacheDirectory(fileBuffer: Buffer, profileId: string, profileUid: string): Promise<void> {
    const zipPath = `${path.join(CACHED_PROFILES_FOLDER, profileId, profileUid)}.zip`;
    await rmdir(path.join(CACHED_PROFILES_FOLDER, profileId), { recursive: true }).catch(() => null);
    await mkdir(path.join(CACHED_PROFILES_FOLDER, profileId), { recursive: true });
    await writeFile(zipPath, fileBuffer, { mode: 0o666 });
  }

  checkCachedStorageSize(): Promise<number> {
    return checkFileSizeSync(CACHED_PROFILES_FOLDER);
  }

  async clearCachedStorage(): Promise<void[]> {
    const files = await readdir(CACHED_PROFILES_FOLDER).catch(() => []);

    const promises = files.map(async (file: string) => {
      const filePath = path.join(CACHED_PROFILES_FOLDER, file);
      const isFolder = (await stat(filePath)).isDirectory();

      if (isFolder) {
        return rmdir(filePath, { recursive: true });
      }

      return unlink(filePath);
    });

    return Promise.all(promises);
  }

  async clearProfileCache(profileId: string): Promise<void | null> {
    const folderPath = path.join(CACHED_PROFILES_FOLDER, profileId);

    return rmdir(folderPath, { recursive: true }).catch(() => null);
  }

  async getCachedProfile(storageInfo: IProfileStorageInfo, profileId: string): Promise<ICachedProfileDataObject> {

    const cachedProfileFolder = path.join(CACHED_PROFILES_FOLDER, profileId);
    const [zipFileName] = await readdir(cachedProfileFolder).catch(() => []);

    if (!(zipFileName && zipFileName?.endsWith('.zip'))) {
      return {
        status: false,
      };
    }

    const [cachedUid] = zipFileName.split('.');
    const cachedArchivePath = path.join(cachedProfileFolder, zipFileName);

    if (cachedUid === storageInfo.uid || (zipFileName && storageInfo.isNewProfile)) {
      return {
        status: true,
        cachedArchivePath,
      };
    }

    const stats: Stats | 'error' = await stat(cachedArchivePath).catch(() => 'error');

    if (stats === 'error') {
      return {
        status: false,
      };
    }

    const cachedDate = stats.birthtime;

    const lastUploadDate = moment(storageInfo.lastDate).utc();
    if (moment(lastUploadDate).isAfter(cachedDate)) {
      return {
        status: false,
      };
    }

    return {
      status: true,
      cachedArchivePath,
    };
  }

  async renameCachedProfile(profileId: string, oldProfileUid: string, newProfileUid: string): Promise<void> {
    const curProfilePath = path.join(CACHED_PROFILES_FOLDER, profileId, oldProfileUid + '.zip');
    const newProfilePath = path.join(CACHED_PROFILES_FOLDER, profileId, newProfileUid + '.zip');

    return rename(curProfilePath, newProfilePath).catch((error: unknown) => {
      console.log(error);

      throw Error('renaming zip error');
    });
  }
}
