import * as path from 'path';

const DEFAULT_FOLDER_USELESS_FILE = [
  {
    name: 'Cache',
    subs: [],
    isDirectory: true,
  },
  {
    name: 'DawnCache',
    subs: [],
    isDirectory: true,
  },
  {
    name: 'Extensions',
    subs: [],
    isDirectory: true,
  },
  {
    name: 'GPUCache',
    subs: [],
    isDirectory: true,
  },
  {
    name: 'fonts_config',
    subs: [],
    isDirectory: false,
  },
  {
    name: 'Service Worker',
    subs: [],
    isDirectory: true,
  },
  {
    name: 'Code Cache',
    subs: [],
    isDirectory: true,
  },
  {
    name: 'GPUCache',
    subs: [],
    isDirectory: true,
  },
];

export const getDirectoriesToDeleteForNode = (): string[] =>
  DEFAULT_FOLDER_USELESS_FILE.reduce((res: string[], el) => {
    const basePath = path.sep + 'Default' + path.sep + el.name;
    if (el.subs.length) {
      el.subs.forEach(sub => res.push(basePath + path.sep + sub));
    } else {
      res.push(basePath);
    }

    return res;
  }, []);

export const getDirectoriesForArchiver = (): string[] => DEFAULT_FOLDER_USELESS_FILE.reduce((res: string[], el) => {
  const { name, subs, isDirectory } = el;
  const basePath = 'Default/' + name;

  if (subs.length) {
    subs.forEach((sub) => {
      const resPath = basePath + '/' + (isDirectory ? sub + '/' : sub);
      res.push(resPath);
    });
  } else {
    res.push(basePath + (isDirectory ? '/' : ''));
  }

  return res;
}, []);
