import keytar from 'keytar';

import { sendErrorToSentry } from './sentry';
import { CommonDataManager } from '../managers/common-data-manager';

const CommonDataManagerInst = new CommonDataManager();

const vaultService = 'GoLogin';
const vaultAccessTokenAccount = 'temp_auth_token';
const googleClientIdKey = 'google_client_id';

export const saveAccessToken = (): Promise<void> =>
  writeAccessTokenToVault().catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, transactionName: 'write-app-config-error', level: 'warning',
      tags: [['scenario', 'write-app-config']],
    });
  });

export const saveGoogleClientIdToVault = async (googleClientId: string): Promise<void> => {
  const currentGid = await keytar.getPassword(vaultService, vaultAccessTokenAccount);
  if (currentGid === googleClientId) {
    return;
  }

  await keytar.setPassword(vaultService, googleClientIdKey, googleClientId);
};

export const getGoogleClientIdFromVault = keytar.getPassword(vaultService, googleClientIdKey);

export const checkAccessTokenInVault = async (): Promise<void> => {
  const accessToken = await keytar.getPassword(vaultService, vaultAccessTokenAccount);
  if (!accessToken) {
    return;
  }

  CommonDataManagerInst.accessToken = accessToken;
  keytar.deletePassword(vaultService, vaultAccessTokenAccount).catch(() => null);
};

const writeAccessTokenToVault = async (): Promise<void> =>
  keytar.setPassword(vaultService, vaultAccessTokenAccount, CommonDataManagerInst.accessToken);
