import { execSync } from 'child_process';
import crypto from 'crypto';
import { accessSync } from 'fs';
import os from 'os';
import path from 'path';

import { IUserPaths } from '../interfaces/path-platforms.interfaces';

export const HOMEDIR = os.homedir();
export const PLATFORM = process.platform;
const SEPARATOR = path.sep;
const CHROME_EXT_DIR_NAME = 'chrome-extensions';
const USER_EXT_DIR_NAME = 'user-extensions';
export const GOLOGIN_BASE_FOLDER_NAME = '.gologin';
export const GOLOGIN_TEST_FOLDER_NAME = '.gologin_test';
export const EXTENSIONS_PATH = path.join(HOMEDIR, GOLOGIN_BASE_FOLDER_NAME, 'extensions');
export const CHROME_EXTENSIONS_PATH = path.join(EXTENSIONS_PATH, CHROME_EXT_DIR_NAME);
export const USER_EXTENSIONS_PATH = path.join(EXTENSIONS_PATH, USER_EXT_DIR_NAME);
export const CACHED_PROFILES_FOLDER = path.join(HOMEDIR, GOLOGIN_BASE_FOLDER_NAME, 'gologin-cached-profiles');

export const CONFIG_APP_PATH = path.join(HOMEDIR, GOLOGIN_BASE_FOLDER_NAME);

export const BROWSER_FOLDER_NAME = 'orbita-browser';
export const MAC_ORBITA_DIR_NAME = 'Orbita-Browser.app';
export const BROWSER_PATH = path.join(HOMEDIR, GOLOGIN_BASE_FOLDER_NAME, 'browser');

const generateUserPaths = (paths: IUserPaths): IUserPaths => {
  if (!paths.BASE_FOLDER || PLATFORM === 'win32') {
    return paths;
  }

  const userName = execSync('echo $USER')
    .toString()
    .trim();

  try {
    accessSync(paths.BASE_FOLDER);
  } catch (err) {
    paths.BASE_FOLDER = path.sep + path.join('tmp', 'gologin');
  }

  const userHash = crypto.createHash('sha1').update(userName).digest('hex').substring(0, 10);
  paths.BASE_FOLDER = `${paths.BASE_FOLDER}_${userHash}`;
  paths.PROFILES_DIR = `${paths.PROFILES_DIR.replace('{base_folder}', paths.BASE_FOLDER)}`;
  paths.START_DIR_PATH = `${paths.START_DIR_PATH.replace('{base_folder}', paths.BASE_FOLDER)}`;
  paths.STARTED_DIR_PATH = `${paths.STARTED_DIR_PATH.replace('{base_folder}', paths.BASE_FOLDER)}`;

  return paths;
};

const baseLinPath = {
  BASE_FOLDER: path.sep + path.join('tmp', 'gologin'),
  PROFILES_DIR: '{base_folder}/profiles/',
  START_DIR_PATH: '{base_folder}/start',
  STARTED_DIR_PATH: '{base_folder}/started',
  ROUTE_SLASH: SEPARATOR,
  COOKIES_FILE: path.join(SEPARATOR, 'Default', 'Cookies'),
  COOKIES_FILE_NEW: path.join(SEPARATOR, 'Default', 'Network', 'Cookies'),
  PASSWORDS_FILE: path.join(SEPARATOR, 'Default', 'Login Data'),
  BROWSER_BASE_PATH: BROWSER_PATH,
};

const linuxPath = generateUserPaths({
  ...baseLinPath,
  BROWSER_EXEC_FILE_NAME: ['chrome'],

  DOLPHIN_DIR_PATH: path.join(HOMEDIR, '.config', 'dolphin_anty') + path.sep,
  OCTO_DIR_PATH: path.join(HOMEDIR, '.config', 'Octo Browser') + path.sep,
  MULTILOGIN_DIR_PATH: path.join(HOMEDIR, '.config', 'Multilogin') + path.sep,
  ADSPOWER_DIR_PATH: path.join(HOMEDIR, '.config', 'adspower_global') + path.sep,
  UNDETECTABLE_DIR_PATH: path.join(HOMEDIR, '.config', 'Undetectable') + path.sep,
  INDIGO_DIR_PATH: path.join(HOMEDIR, '.config', 'Indigo') + path.sep,
  INCOGNITION_DIR_PATH: path.join(HOMEDIR, '.config', 'Incogniton') + path.sep,
  MORELOGIN_DIR_PATH: path.join(HOMEDIR, '.config', 'MoreLogin') + path.sep,
});

const macPath = generateUserPaths({
  ...baseLinPath,
  BASE_FOLDER: path.sep + path.join('private', 'tmp', 'gologin'),
  BROWSER_EXEC_FILE_NAME: [MAC_ORBITA_DIR_NAME, 'Contents', 'MacOS', 'Orbita'],
  BROWSER_OLD_EXEC_FILE_NAME: '',
  BROWSER_BASE_PATH: BROWSER_PATH,

  DOLPHIN_DIR_PATH: path.join(HOMEDIR, 'Library', 'Application Support', 'dolphin_anty') + path.sep,
  OCTO_DIR_PATH: path.join(HOMEDIR, '.Octo Browser') + path.sep,
  MULTILOGIN_DIR_PATH: path.join(HOMEDIR, 'Library', 'Application Support', 'Multilogin') + path.sep,
  ADSPOWER_DIR_PATH: path.join(HOMEDIR, 'Library', 'Application Support', 'adspower_global') + path.sep,
  UNDETECTABLE_DIR_PATH: path.join(HOMEDIR, 'Library', 'Application Support', 'Undetectable') + path.sep,
  INDIGO_DIR_PATH: path.join(HOMEDIR, 'Library', 'Application Support', 'Indigo') + path.sep,
  INCOGNITION_DIR_PATH: path.join(HOMEDIR, 'Library', 'Application Support', 'Incogniton') + path.sep,
  MORELOGIN_DIR_PATH: path.join(HOMEDIR, 'Library', 'Application Support', 'MoreLogin') + path.sep,
});

const winPath = {
  BASE_FOLDER: path.join(HOMEDIR, 'AppData', 'Local', 'Temp', 'GoLogin') + path.sep,
  PROFILES_DIR: path.join(HOMEDIR, 'AppData', 'Local', 'Temp', 'GoLogin', 'profiles') + path.sep,
  START_DIR_PATH: path.join(HOMEDIR, 'AppData', 'Local', 'Temp', 'GoLogin', 'start') + path.sep,
  STARTED_DIR_PATH: path.join(HOMEDIR, 'AppData', 'Local', 'Temp', 'GoLogin', 'started') + path.sep,
  ROUTE_SLASH: SEPARATOR,
  BROWSER_EXEC_FILE_NAME: ['chrome.exe'],
  BROWSER_BASE_PATH: BROWSER_PATH,
  COOKIES_FILE: path.join(SEPARATOR, 'Default', 'Cookies'),
  COOKIES_FILE_NEW: path.join(SEPARATOR, 'Default', 'Network', 'Cookies'),
  PASSWORDS_FILE: path.join(SEPARATOR, 'Default', 'Login Data'),

  DOLPHIN_DIR_PATH: path.join(HOMEDIR, 'AppData', 'Roaming', 'dolphin_anty') + path.sep,
  OCTO_DIR_PATH: path.join(HOMEDIR, 'AppData', 'Roaming', 'Octo Browser') + path.sep,
  MULTILOGIN_DIR_PATH: path.join(HOMEDIR, 'AppData', 'Roaming', 'Multilogin') + path.sep,
  ADSPOWER_DIR_PATH: path.join(HOMEDIR, 'AppData', 'Roaming', 'adspower_global') + path.sep,
  UNDETECTABLE_DIR_PATH: path.join(HOMEDIR, '..', '..', 'Program Files', 'Undetectable') + path.sep,
  INDIGO_DIR_PATH: path.join(HOMEDIR, 'AppData', 'Roaming', 'Indigo') + path.sep,
  INCOGNITION_DIR_PATH: path.join(HOMEDIR, 'AppData', 'Roaming', 'Incogniton') + path.sep,
  MORELOGIN_DIR_PATH: path.join(HOMEDIR, 'AppData', 'Roaming', 'MoreLogin') + path.sep,
};

export const getPathOs = (): IUserPaths => {
  if (PLATFORM === 'win32') {
    return winPath;
  }

  if (PLATFORM === 'darwin') {
    return macPath;
  }

  return linuxPath;
};

const pathOs = getPathOs();
export const BASE_PATH_TEMPLATE =
  pathOs.PROFILES_DIR +
  '{profileId}' +
  pathOs.ROUTE_SLASH +
  'Default' +
  pathOs.ROUTE_SLASH;

const ORBITA_CONFIG_FILE_NAME = 'orbita.config';
export const ORBITA_CONFIG_PATH =
  pathOs.PROFILES_DIR +
  '{profileId}' +
  pathOs.ROUTE_SLASH +
  ORBITA_CONFIG_FILE_NAME;

export const MIN_MAJOR_CHROME_VERSION_FOR_CREATION_ORBITA_CONFIG = 121;
