import got, { Headers } from 'got';
import { HttpsProxyAgent } from 'https-proxy-agent';
import { SocksProxyAgent } from 'socks-proxy-agent';

import { sendErrorToSentry } from './sentry';
import { TIMEZONE_CHECK_URL } from '../constants/constants';
import { IProxyRequestParams, IProxyRequestResponse } from '../interfaces/request-with-timeout.interfaces';
import { CommonDataManager } from '../managers/common-data-manager';

const TIMEOUT = 12000;
const HTTP_PROTO = 'http://';
const HTTPS_PROTO = 'https://';
const HTTPS_ERRORS = ['socket hang up', 'read ECONNRESET', 'Proxy connection ended before receiving CONNECT response'];

const CommonDataManagerInst = new CommonDataManager();

export const makeProxyRequestWithTimeout = async (proxyParams: IProxyRequestParams, retries = 2): Promise<IProxyRequestResponse> => {
  let response: any = {};
  let agent: HttpsProxyAgent<string> | SocksProxyAgent;

  const { username, password, mode = '' } = proxyParams;
  let { host = '', port = '', type } = proxyParams;

  let proxyType: string = type;
  if (type.includes('socks')) {
    proxyType = 'socks';
  }

  host = host.trim().replace(' ', '');
  if (host.includes('.')) {
    [host] = host.split(':');
  }

  port = port.toString().trim();
  if (port.includes(':')) {
    [, port] = port.split(':');
  }

  const isFreeProxy = mode.includes('gologin');
  const isTorProxy = mode === 'tor';
  let proxyString = `${proxyType}://`;

  if (username && password) {
    proxyString += `${encodeURIComponent(username)}:${encodeURIComponent(password)}@`;
  }

  proxyString += `${host}:${port}`;

  //  Sentry
  if (!(isFreeProxy || isTorProxy)) {
    console.log('proxyString:', proxyString, 'host:', host, 'port:', port);
  }

  if (proxyString.startsWith('http')) {
    agent = getHttpProxyAgent(proxyString);
  } else {
    agent = new SocksProxyAgent(proxyString);
  }

  const url = TIMEZONE_CHECK_URL;
  let headers: Headers = {
    Authorization: `Bearer ${CommonDataManagerInst.accessToken}`,
    'user-agent': CommonDataManagerInst.userAgent,
    'x-two-factor-token': CommonDataManagerInst.twoFaKey || '',
  };

  if (isFreeProxy || isTorProxy) {
    headers = {};
  }

  while(retries--) {
    const request = got(
      url,
      {
        agent: {
          https: agent,
        },
        headers,
      },
    );

    const timeout = setTimeout(() => request.cancel(), TIMEOUT);
    response = await request.json().catch((error) => error.message);

    clearTimeout(timeout);
    if (HTTPS_ERRORS.includes(response) && proxyString.startsWith(HTTP_PROTO)) {
      type = 'https';
      agent = getHttpProxyAgent(proxyString.replace(HTTP_PROTO, HTTPS_PROTO));

      continue;
    }

    if (response?.ip) {
      return { response, protocol: type };
    }
  }

  const isFloppyData = host.includes('floppydata.com');
  let transactionName = 'check-proxy-response';
  console.log(isFloppyData, 'isFloppyData');
  if (isFloppyData) {
    transactionName = 'check-proxy-response-floppydata';
  }

  sendErrorToSentry({
    message: response, level: 'warning', transactionName,
    tags: [['proxy-mode', mode]],
  });

  return { response, protocol: type };
};

const getHttpProxyAgent = (proxyString: string): HttpsProxyAgent<string> =>
  new HttpsProxyAgent(proxyString, {
    keepAlive: true,
    keepAliveMsecs: 1000,
    maxSockets: 256,
    maxFreeSockets: 256,
    scheduling: 'lifo',
  });
