import { makeRequest } from './make-request';
import { version } from '../../../package.json';
import { CommonDataManager } from '../managers/common-data-manager';

const CommonDataManagerInst = new CommonDataManager();

export const sendActionAnalytics = (action: string, extraInfo: any = {}): void => {
  extraInfo.appVersion = version;

  makeRequest({
    method: 'POST',
    url: `${CommonDataManagerInst.apiBaseUrl}/analytics/add_action_analytics`,
    body: { action, ...extraInfo },
  }).catch(() => null);
};
