import * as os from 'os';
import path from 'path';

import { BrowserWindow } from 'electron';

import {
  getChromeMajorVersion,
  getOldLocalOrbitaVersion,
  getOrbitaDirNameByVersion,
} from './utils/browser-checker.utils';
import { makeRequest } from '../../electron/utils/make-request';
import {
  MAC_ORBITA_DIR_NAME,
  PLATFORM as osPlatform,
  BROWSER_FOLDER_NAME,
  BROWSER_PATH,
  getPathOs,
} from '../../electron/utils/path-platforms';
import { sendCommonErrorToSentry, sendErrorToSentry } from '../../electron/utils/sentry';
import { copyFolder, removeFileOrFolder } from '../../electron/utils/utils';
import { STORAGE_KEY_PREFIX, VERSION_FILE } from '../constants/constants';
import { IOrbitaPath, IOrbitaReleasesList, IOrbitaWinCompatibilityData } from '../interfaces/browser-releases.interfaces';
import { getWinRelease } from '../utils/userOs';
import withRetry from '../utils/with-retry';

const { readdir, rm, readFile, mkdir } = require('fs').promises;

const DEFAULT_VERSION = '0.0.0';
const VERSION_TO_SAVE_FULL_VERSIONS_FROM = 119;

export class BrowserReleasesManager {
  #osPaths = getPathOs();
  #localOrbitaReleasesList: IOrbitaReleasesList = {};
  #localOrbitaVersion = '';
  #supportedOrbitaVersions: string[] = [];
  static instance: any;
  window: BrowserWindow|null = null;

  constructor() {
    if (!BrowserReleasesManager.instance) {
      BrowserReleasesManager.instance = this;
    }

    return BrowserReleasesManager.instance;
  }

  #getSortedOrbitaVersions(): string[] {
    return Object.keys(this.#localOrbitaReleasesList)
      .map(version => Number(version))
      .sort((a, b) => a - b)
      .reverse()
      .map(version => version.toString());
  }

  async getLocalOrbitaReleasesList(): Promise<void> {
    const regex = new RegExp(`^(${BROWSER_FOLDER_NAME}|${BROWSER_FOLDER_NAME}-\\d{2,3})$`);

    this.#localOrbitaReleasesList = (await readdir(BROWSER_PATH).catch(() => []))
      .filter((el: string) => el.includes(MAC_ORBITA_DIR_NAME) || regex.test(el))
      .reduce((res: IOrbitaReleasesList, val = '') => {
        const chromeMajorVersionMatch = val.match(/-(\d+$)/) || [];
        const [_, chromeMajorVersion = '0'] = chromeMajorVersionMatch;
        res[chromeMajorVersion] = val;

        return res;
      }, {} as IOrbitaReleasesList);

    this.updateReleasesListInLocalStorage();
  }

  get localOrbitaReleasesList(): IOrbitaReleasesList {
    return this.#localOrbitaReleasesList;
  }

  async getSupportedOrbitaVersions(API_BASE_URL = ''): Promise<string[]> {
    if (this.#supportedOrbitaVersions.length || !API_BASE_URL) {
      return this.#supportedOrbitaVersions;
    }

    return makeRequest<{ supportedOrbitaVersions: string[] }>({
      url: `${API_BASE_URL}/gologin-global-settings/supported_orbita_versions`,
      method: 'GET',
      timeout: 30 * 1000,
    }).then((res: { supportedOrbitaVersions: string[] }) => {
      this.#supportedOrbitaVersions = res.supportedOrbitaVersions || [];

      return this.#supportedOrbitaVersions;
    }).catch(() => []);
  }

  async checkLocalOrbitaReleases(): Promise<void> {
    await this.getLocalOrbitaReleasesList();
    const orbitaDirName = osPlatform === 'darwin' ? MAC_ORBITA_DIR_NAME : BROWSER_FOLDER_NAME;

    if (!this.#localOrbitaReleasesList['0']) {
      return;
    }

    const localOrbitaVersion = (await getOldLocalOrbitaVersion()) || '';
    const chromeMajorVersion = getChromeMajorVersion(localOrbitaVersion);
    const newOrbitaDirName = getOrbitaDirNameByVersion(chromeMajorVersion);

    let sourcePath = path.join(BROWSER_PATH, orbitaDirName);
    if (osPlatform === 'darwin') {
      sourcePath = BROWSER_PATH;
      await mkdir(path.join(sourcePath, newOrbitaDirName)).catch(() => null);
    }

    return withRetry({
      fn() {
        if (osPlatform === 'darwin') {
          return readdir(sourcePath)
            .then((files: string[]) => Promise.all(files.map((file) => {
              if (file.includes('dylib') || file.includes('version') || file.includes(MAC_ORBITA_DIR_NAME)) {
                return copyFolder(path.join(sourcePath, file), path.join(sourcePath, newOrbitaDirName, file));
              }

              return null;
            })))
            .then(() => readdir(sourcePath))
            .then((files: string[]) => Promise.all(files.map((file) => {
              if (file.includes(BROWSER_FOLDER_NAME)) {
                return null;
              }

              return removeFileOrFolder(path.join(sourcePath, file));
            })));
        }

        return copyFolder(sourcePath, path.join(BROWSER_PATH, newOrbitaDirName));
      },
    })
      .then(() => {
        if (osPlatform === 'darwin') {
          return null;
        }

        return rm(path.join(BROWSER_PATH, BROWSER_FOLDER_NAME), { recursive: true, maxRetries: 5, retryDelay: 300 });
      })
      .then(() => this.getLocalOrbitaReleasesList())
      .catch((error: unknown) => {
        const message = error instanceof Error ? error.message : error;
        sendCommonErrorToSentry({
          message, transactionName: 'check-downloaded-orbita-versions',
          tags: [['scenario', 'check-multicore'], ['name', 'check-downloaded-orbita-versions']],
        });
      });
  }

  async getLocalOrbitaVersionByPath(browserPath = '', forSentry = false): Promise<string> {
    if (forSentry && this.#localOrbitaVersion) {
      return this.#localOrbitaVersion;
    }

    const localBrowsers = Object.keys(this.#localOrbitaReleasesList);
    if (!localBrowsers.length) {
      return DEFAULT_VERSION;
    }

    let formattedBrowserPath = browserPath;
    if (!formattedBrowserPath) {
      const [newestOrbita] = this.#getSortedOrbitaVersions();
      const orbitaFolderPath = this.#localOrbitaReleasesList[newestOrbita];
      formattedBrowserPath = path.join(this.#osPaths.BROWSER_BASE_PATH, orbitaFolderPath);
    }

    let versionFilePath = path.join(formattedBrowserPath, 'version');
    if (osPlatform === 'darwin') {
      versionFilePath = path.join(formattedBrowserPath, 'version', VERSION_FILE);
    }

    return readFile(versionFilePath, { encoding: 'utf-8' })
      .then((res: string) => {
        this.#localOrbitaVersion = res;

        return res;
      })
      .catch(() => DEFAULT_VERSION);
  }

  getOrbitaPathToStart(majorVer = ''): IOrbitaPath {
    const localBrowsers = this.#getSortedOrbitaVersions();
    if (!localBrowsers.length) {
      throw new Error('No installed Orbita');
    }

    let pickedMajorVer = majorVer;
    let orbitaFolderPath = this.#localOrbitaReleasesList[pickedMajorVer];
    if (!orbitaFolderPath) {
      [pickedMajorVer] = this.#getSortedOrbitaVersions();

      orbitaFolderPath = this.#localOrbitaReleasesList[pickedMajorVer];
    }

    return {
      pathToLaunch: path.join(this.#osPaths.BROWSER_BASE_PATH, orbitaFolderPath, ...this.#osPaths.BROWSER_EXEC_FILE_NAME),
      browserWorkingDir: path.join(this.#osPaths.BROWSER_BASE_PATH, orbitaFolderPath),
      pickedMajorVer,
    };
  }

  getMajorVersionByUA(ua = ''): string {
    const chromeVersionMatch = ua.match(/[\s\S\w]+Chrome\/(\d+)/) || [];
    if (!chromeVersionMatch) {
      return '';
    }

    const [_, version = ''] = chromeVersionMatch;

    if (version === '100') {
      return '10';
    }

    if (version === '103') {
      return '13';
    }

    return version;
  }

  getOrbitaVersionToDownload({ key, value }: IOrbitaWinCompatibilityData): string {
    let uaMajorVersion = key === 'userAgent' ? this.getMajorVersionByUA(value) : value;

    const isVersionCompatible = this.checkOrbitaWinCompatible({ key, value });
    const hasLocalOrbita = this.#localOrbitaReleasesList[uaMajorVersion];
    const hasSupportedVersions = this.#supportedOrbitaVersions.includes(uaMajorVersion);
    if (!(isVersionCompatible && uaMajorVersion && hasSupportedVersions) || hasLocalOrbita) {
      return '';
    }

    if (uaMajorVersion === '10') {
      uaMajorVersion = '100';
    }

    if (uaMajorVersion === '13') {
      uaMajorVersion = '103';
    }

    return uaMajorVersion.toString();
  }

  checkOrbitaWinCompatible({ key, value, ProfileStatusManagerInst }: IOrbitaWinCompatibilityData): boolean {
    if (os.platform() !== 'win32') {
      return true;
    }

    let releaseVersionRaw: string | number = value || 'latest';
    if (key === 'userAgent') {
      const majorChromeVersion = this.getMajorVersionByUA(value);
      const { pathToLaunch } = this.getOrbitaPathToStart(majorChromeVersion);

      const orbitaVersion = pathToLaunch.match(/orbita-browser-(\d+)/) || [];
      releaseVersionRaw = orbitaVersion[1] || '110';
    }

    const releaseVersion = +releaseVersionRaw;
    if (+releaseVersion < 110) {
      return true;
    }

    const winVersion = +getWinRelease();
    if (winVersion < 10) {
      ProfileStatusManagerInst && ProfileStatusManagerInst.addStatus({
        status: 'error',
        statusMessage: 'profileStatusMessages.windowsVersionNotSupported',
      });

      return false;
    }

    return true;
  }

  checkOrbitasAvailable(orbitasMajorVersions: number[]): number[] {
    let orbitasVersionsAvailable = orbitasMajorVersions;
    const orbitasDownloadedLocally = Object.keys(this.#localOrbitaReleasesList).map(Number);
    orbitasVersionsAvailable = orbitasVersionsAvailable.filter((version) => orbitasDownloadedLocally.includes(version));
    if (os.platform() !== 'win32') {
      return orbitasVersionsAvailable;
    }

    const winVersion = +getWinRelease();
    if (winVersion < 10) {
      return orbitasVersionsAvailable.filter((version) => version <= 109);
    }

    return orbitasVersionsAvailable;
  }

  async updateVersionFullInLocalStorage(window: BrowserWindow, localBrowsers: string[]): Promise<void> {
    const localBrowserReleases = this.localOrbitaReleasesList;
    const localBrowsersToCheckVersionsFull = localBrowsers.filter(versionMajor => Number(versionMajor) >= VERSION_TO_SAVE_FULL_VERSIONS_FROM);
    const versionsFullForSquareIconsPromises = localBrowsersToCheckVersionsFull.map((versionMajor) => {
      const currentOrbitaPath = localBrowserReleases[versionMajor];
      const baseOrbitaPath = path.join(BROWSER_PATH, currentOrbitaPath);

      return this.getLocalOrbitaVersionByPath(baseOrbitaPath);
    });

    const versionsFullForSquareIconsSettled = await Promise.allSettled(versionsFullForSquareIconsPromises);
    const versionsFullForSquareIcons = versionsFullForSquareIconsSettled.map((result) => {
      if (result.status === 'rejected') {
        return null;
      }

      return result.value;
    }).filter(Boolean).join(',');

    return window.webContents.executeJavaScript(`
      localStorage.setItem('${STORAGE_KEY_PREFIX}:browsersVersionsFull', '${versionsFullForSquareIcons}');
    `).catch(console.error);
  }

  async updateReleasesListInLocalStorage(window?: BrowserWindow): Promise<void> {
    const localBrowsers = this.#getSortedOrbitaVersions();
    let browserWindow = this.window;
    if (window) {
      browserWindow = window;
      this.window = window;
    }

    if (!browserWindow) {
      return;
    }

    const versionsMajorPromise = browserWindow.webContents.executeJavaScript(`
      localStorage.setItem('${STORAGE_KEY_PREFIX}:browsersLocal', '${localBrowsers.join(',')}');
    `).catch(console.error);

    const versionFullPromise = this.updateVersionFullInLocalStorage(browserWindow, localBrowsers);
    await Promise.allSettled([versionsMajorPromise, versionFullPromise]);
  }
}
