/* eslint-disable camelcase */
import { mainLocaleList } from '../../constants/constants';
import { IIntlProfileConfig } from '../../interfaces/intl-profile-config.interfaces';
import { ICheckProxyResult } from '../../interfaces/proxy.service.interfaces';
import { IProfileData } from '../../interfaces/start-profile.renderer.interfaces';

export const getIntlProfileConfig = (profileData: IProfileData, timezoneCheckResult: ICheckProxyResult): IIntlProfileConfig => {
  if (!profileData.autoLang) {
    return checkBrowserLang(profileData);
  }

  let timezoneLang = '';
  const { country: timezoneCountry = '', languages } = timezoneCheckResult;
  if (!languages) {
    return checkBrowserLang(profileData);
  }

  const [firstDetectedLangLocale] = languages.split(',');
  //   languages,    timezoneCountry,   languages    
  timezoneLang = `${firstDetectedLangLocale}-${timezoneCountry}` || '';

  let resultLangsArr: string[] = [];
  const [lang = '', country = ''] = timezoneLang.split('-');
  if (country) {
    resultLangsArr.push([lang, country].join('-'));
  }

  resultLangsArr.push(lang, 'en-US', 'en');
  resultLangsArr = [...new Set(resultLangsArr)];

  const mainLanguage = getMainLanguage(resultLangsArr);

  return {
    accept_languages: resultLangsArr.join(','),
    selected_languages: resultLangsArr.join(','),
    app_locale: mainLanguage,
    forced_languages: [
      mainLanguage,
    ],
  };
};

const checkBrowserLang = (profileData: IProfileData, defaultLocale = 'en-US'): IIntlProfileConfig => {
  let userLangs = '';
  let userMainLang = '';

  if (profileData.gologin.languages) {
    try {
      const userLangsArr = profileData.gologin.languages.split(',');

      userMainLang = getMainLanguage(userLangsArr);

      userLangs = profileData.gologin.languages;
    } catch (error) {
      void 0;
    }
  }

  return {
    accept_languages: userLangs || defaultLocale,
    selected_languages: userLangs || defaultLocale,
    app_locale: userMainLang || defaultLocale,
    forced_languages: [
      userMainLang || defaultLocale,
    ],
  };
};

const getMainLanguage = (langArr: string[]): string => {
  for (const lang of langArr) {
    if (mainLocaleList.includes(lang)) {
      return lang;
    }

    const [locale] = lang.split('-');
    if (mainLocaleList.includes(locale)) {
      return locale;
    }
  }

  return '';
};
