import { ChildProcessWithoutNullStreams, SpawnOptionsWithoutStdio, spawn } from 'child_process';
import { rm } from 'fs/promises';
import pathNode from 'path';

import moment from 'moment';

import { closeAndUploadProfile } from './close-orbita';
import {
  BRAVE_PROCESSES,
  deletedProfiles, loadingProfiles, syncingProfiles,
} from './renderer';
import packageSettings from '../../../../package.json';
import { IGeolocation } from '../../../app/interfaces';
import { OpenProfileViaLink } from '../../../common/constants/analytics';
import { makeRequest } from '../../../electron/utils/make-request';
import { sendCommonErrorToSentry, sendErrorToSentry } from '../../../electron/utils/sentry';
import { sentryTransactionDefaultObject } from '../../helpers/helper.functions';
import { ORBITA_SELF_CLOSE_MS, sendOrbitaErrorToSentry } from '../../helpers/orbita-errors';
import { ILaunchOrbitaProps, IProfilePreparingRes } from '../../interfaces/launch-orbita.interfaces';
import { apiStatusUpdater } from '../../server/src/services/profiles-timer';
import { downloadBrowser } from '../../utils/check-browser';
import { getPathOs } from '../../utils/path-platforms';
import { sendActionAnalytics } from '../../utils/send-action-analytics';
import { removeItemFromArray } from '../../utils/utils';
import { pidList } from '../../window-raiser/focus-orbita-utils';
import { BrowserReleasesManager } from '../browser-releases.manager';
import { CommonDataManager } from '../common-data-manager';
import { ProfileStarterManager } from '../start-profile-manager';

const pathOs = getPathOs();
const ProfileStarterManagerInst = new ProfileStarterManager();
const CommonDataManagerInst = new CommonDataManager();

export const launchOrbita = async (props: ILaunchOrbitaProps): Promise<string> => {
  const {
    timezoneCheckResult, startProfileTimestamp, profileOptions,
    remoteCb, profileData, automation, args, channel, passwordsTemporaryToken, currentMs, ProfileReadyEventEmitterInst,
    profilePreparingRes, timings,
    startProfileTransactionSentry = sentryTransactionDefaultObject,
    ProfileStatusManagerInst,
    ProfileStatusManagerInstances,
    deepLinkProtocol,
  } = props;

  const { profileId } = profileData;
  const { timezone: TZ, ll = ['0', '0'], accuracy = 100, ip, country } = timezoneCheckResult;
  if (!(ip && TZ)) {
    const statusMessage = 'Timezone request error.';
    console.log(timezoneCheckResult);
    sendErrorToSentry({
      message: `Timezone request error - ${timezoneCheckResult}`, transactionName: 'timezone-request-error', level: 'fatal',
      tags: [['scenario', 'run-profile'], ['profileId', profileId]],
    });

    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage,
      startProfileTimestamp,
      profileOptions,
    });

    remoteCb && remoteCb(null, { status: 403, message: statusMessage });

    return 'error';
  }

  profileOptions.proxyCountry = country;

  try {
    const {
      profileExtensionPathRes,
      profileUserExtensionPathRes,
    } = profilePreparingRes.reduce((acc: IProfilePreparingRes, doc: any) => {
      if ('profileExtensionsCheckRes' in doc) {
        acc.profileExtensionPathRes = doc;
      }

      if ('profileUserExtensionsCheckRes' in doc) {
        acc.profileUserExtensionPathRes = doc;
      }

      return acc;
    }, { profileExtensionPathRes : {}, profileUserExtensionPathRes: {} });

    const [latitude, longitude] = ll;
    console.log('Getting timezone:', TZ);

    const tz = profileData.fillProxyByIp ? TZ : profileData.timezoneValue;
    const profileGeolocation = profileData.gologin.geoLocation;
    const tzGeoLocation = {
      latitude,
      longitude,
      accuracy,
    };

    profileData.gologin.geoLocation = getGeolocationParams(profileGeolocation, tzGeoLocation);
    profileData.gologin.timezone = { id: tz };

    const profileExtensionsCheckRes =
      (profileExtensionPathRes?.profileExtensionsCheckRes || []).concat(profileUserExtensionPathRes?.profileUserExtensionsCheckRes || []);

    console.log('checkRes', profileExtensionsCheckRes);
    const updateBrowserSettings = startProfileTransactionSentry.startChild({ op: 'write-preferences' });
    await ProfileStarterManagerInst.updatingBrowserSettings({ profileData, publicUserIp: ip, profileExtensionsCheckRes, args, timezoneCheckResult });
    updateBrowserSettings.finish();
  } catch (error: unknown) {
    const message = error instanceof Error ? error.message : error;
    let statusMessage = 'profileStatusMessages.defectiveProfileError';
    let transactionName = 'update-preferences-file';

    if (typeof message === 'string' && message.includes('ENOSPC')) {
      transactionName = 'no-disk-space';
      statusMessage = 'profileStatusMessages.discSpaceError';
    }

    sendErrorToSentry({
      message, transactionName, level: 'fatal',
      tags: [['scenario', 'run-profile'], ['profileId', profileId]],
    });
    startProfileTransactionSentry.setStatus('aborted');
    startProfileTransactionSentry.finish();
    ProfileStatusManagerInst.addStatus({
      status: 'error',
      startProfileTimestamp,
      statusMessage,
      profileOptions,
    });

    remoteCb && remoteCb(null, { status: 403, message: statusMessage });

    return 'error';
  }

  let braveProc: any;

  ProfileReadyEventEmitterInst.removeListener('profile-ready', () => {
    console.log('listener removed');
  });

  if (!BRAVE_PROCESSES[profileId]) {
    BRAVE_PROCESSES[profileId] = {};
  }

  await removeLockFiles(profileId).catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, transactionName: 'remove-lock-files-error', level: 'fatal',
      tags: [['scenario', 'run-profile'], ['profileId', profileId]],
    });
  });

  // const { userOs } = CommonDataManagerInst;

  // if (['mac', 'lin'].includes(userOs)) {
  //   args.push('--enable-logging=stderr --v=1');
  // }

  // user-data-dir should be always last
  const [userDataDirArg] = args;
  args[0] = args[args.length - 1];
  args[args.length - 1] = userDataDirArg;

  console.log('args', args);
  console.log('Spawning browser process');

  const spawningBrowserSentry = startProfileTransactionSentry.startChild({ op: 'spawn-browser' });
  let majorChromeVersion = '';
  try {
    const ua = profileData.gologin?.userAgent || '';
    majorChromeVersion = new BrowserReleasesManager().getMajorVersionByUA(ua);
    braveProc = await launchBrowser(args, ua);
    spawningBrowserSentry.finish();
    startProfileTransactionSentry.finish();
    if (deepLinkProtocol) {
      sendActionAnalytics(OpenProfileViaLink.opened, { mode: deepLinkProtocol, profile: profileId, actionInfo: profileData.gologin?.name || '' });
    }
  } catch (error: unknown) {
    const message = error instanceof Error ? error.message : error;
    const statusMessage = `launchBrowser error ${profileId}. ${message}`;

    sendCommonErrorToSentry({
      message, transactionName: 'spawn-orbita-process',
      tags: [['scenario', 'run-profile'], ['profileId', profileId], ['name', 'spawn-orbita-process']],
    });

    downloadBrowser(channel, { orbitaVersion: majorChromeVersion });

    startProfileTransactionSentry.setStatus('aborted');
    startProfileTransactionSentry.finish();
    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage: 'profilesTable.profileStatusTooltip.noInstalledOrbita',
      startProfileTimestamp,
      profileOptions,
    });

    remoteCb && remoteCb(null, { status: 403, message: statusMessage });

    return 'error';
  }

  const orbitaConsoleContainer: string[] = [];
  removeItemFromArray<string>(loadingProfiles, profileId);

  pidList[profileId] = braveProc?.pid;
  console.log(`profile ${profileId} orbita pid:`, braveProc?.pid);

  try {
    BRAVE_PROCESSES[profileId].startProfileTimestamp = moment.now();
    timings.overallTime = moment.now() - startProfileTimestamp;
    profileOptions.proxyMode = profileData.proxy.mode;
    BRAVE_PROCESSES[profileId].startMode = profileOptions.startMode;

    //    
    //   
    //  
    BRAVE_PROCESSES[profileId].orbitaProc = braveProc;

    if (profileData.lockEnabled) {
      apiStatusUpdater(profileId, { running: true });
      BRAVE_PROCESSES[profileId].bigTimer =
        setInterval(() => apiStatusUpdater(profileId, {}), 3 * 60 * 1000);
    }
  } catch {
    console.log('error assigning startProfileTimestamp');
  }

  //       
  ProfileStatusManagerInst.addStatus({
    status: 'running',
    startProfileTimestamp,
    profileOptions,
    timings,
  });
  ProfileStatusManagerInst.removeStatus({ status: 'preparing_profile' });

  const addDataToConsoleContainer = (output: string): void => {
    try {
      orbitaConsoleContainer.unshift(output.toString().replace(/\n/g, '').replace(/\s\s+/g, ' '));
      if (orbitaConsoleContainer.length > 50) {
        orbitaConsoleContainer.length = 50;
      }
    } catch(err) {
      console.log(err);
    }
  };

  try {
    braveProc && braveProc.stdout.on('data', (data: string) => {
      // console.log(`STDOUT: ${data}`);
      addDataToConsoleContainer(data);
    });

    braveProc && braveProc.stderr.on('data', (data: string) => {
      // console.error(`STDERR: ${data}`);
      addDataToConsoleContainer(data);
    });

    braveProc && braveProc.on('error', async (err: unknown) => {
      const message = err instanceof Error ? err.message : err;
      console.log(err);
      downloadBrowser(channel, { orbitaVersion: majorChromeVersion });
      ProfileStatusManagerInst.addStatus({
        status: 'error',
        statusMessage: 'profilesTable.profileStatusTooltip.noInstalledOrbita',
      });

      sendErrorToSentry({
        message, transactionName: 'no-installed-orbita', level: 'fatal',
        tags: [['scenario', 'close-profile'], ['profileId', profileId]],
      });

      BRAVE_PROCESSES[profileId]?.bigTimer && clearInterval(BRAVE_PROCESSES[profileId].bigTimer);
      delete BRAVE_PROCESSES[profileId];
    });
  } catch(err) {
    console.log(err);
  }

  braveProc && braveProc.on('exit', async (code: number, signal: string) => {
    console.log('closing profile', profileId);
    if (profileData.lockEnabled) {
      apiStatusUpdater(profileId, { running: false });
    }

    const processDurationMs = moment.now() - BRAVE_PROCESSES[profileId]?.startProfileTimestamp;

    BRAVE_PROCESSES[profileId]?.bigTimer && clearInterval(BRAVE_PROCESSES[profileId].bigTimer);
    delete BRAVE_PROCESSES[profileId];

    if (deletedProfiles.includes(profileId) || loadingProfiles.includes(profileId) || syncingProfiles.includes(profileId)) {
      return;
    }

    const message = `child process exited with code ${code} and signal ${signal}`;
    ProfileStatusManagerInst.addStatus({ status: 'archiving' });
    ProfileStatusManagerInst.removeStatus({ status: 'running' });
    console.log(message);
    if (![0, null].includes(code) || signal === 'SIGABRT' || processDurationMs < ORBITA_SELF_CLOSE_MS) {
      sendOrbitaErrorToSentry({ orbitaConsoleContainer, message, profileData, processDurationMs, code, signal, ProfileStatusManagerInst, channel })
        .catch(() => null);
    }

    const browserVersion = await new BrowserReleasesManager().getLocalOrbitaVersionByPath()
      .catch((err) => {
        console.log('Error in reading browser version', err);

        return '';
      });

    console.log('braveProc browserVersion:', browserVersion);

    const { userOs } = CommonDataManagerInst;

    const body = {
      type: 'browser-close',
      status: code === 0 || code === null ? 'success' : 'error',
      profile: profileId,
      time: Date.now() - startProfileTimestamp,
      message: `${code}`,
      os: userOs,
      browserVersion,
      appVersion: packageSettings.version,
      proxyMode: profileData.proxy.mode,
    };

    makeRequest({ url: `${CommonDataManagerInst.apiBaseUrl}/analytics`, method: 'POST', body })
      .catch(err => console.log(err));

    makeRequest({
      url: `${CommonDataManagerInst.apiBaseUrl}/profiles-passwords/temporary-token`,
      method: 'DELETE',
      headers: {
        Authorization: `Bearer ${passwordsTemporaryToken}`,
        'user-agent': CommonDataManagerInst.userAgent,
        'x-two-factor-token': CommonDataManagerInst.twoFaKey || '',
      },

    }).catch(err => console.log(err));

    const apiTokenForClose = CommonDataManagerInst.accessToken;
    const twoFaKeyForClose = CommonDataManagerInst.twoFaKey;

    await closeAndUploadProfile({
      profileId,
      currentMs,
      debugMode: profileData.debugMode,
      localStorageEnabled: profileData.localStorageEnabled,
      indexedDbEnabled: profileData.indexedDbEnabled,
      channel,
      os: profileData.os,
      ProfileStatusManagerInst,
      ProfileStatusManagerInstances,
      automation,
      apiTokenForClose,
      twoFaKeyForClose,
      vaultToken: profileData.vaultToken,
    }).catch((error: unknown) => {
      removeItemFromArray(syncingProfiles, profileId);
      const errMessage = error instanceof Error ? error.message : error;
      let statusMessage = `profile close error ${profileId}. ${errMessage}`;
      let sendToSentry = true;
      if (errMessage === 'The profile has been deleted') {
        statusMessage = 'notifications.error.profileAlreadyDeleted';
        sendToSentry = false;
      }

      if (errMessage === 'profile archive is not found') {
        sendToSentry = false;
      }

      if (errMessage === 'Too many requests') {
        statusMessage = 'profiles.profileStatusMessages.errorTooManyRequests';
      }

      ProfileStatusManagerInst.addStatus({ status: 'error', statusMessage });

      sendToSentry && sendCommonErrorToSentry({
        message: errMessage, transactionName: 'close-profile-global',
        tags: [['scenario', 'close-profile'], ['profileId', profileId], ['name', 'close-profile-global']],
      });
    });

    ProfileStatusManagerInst.removeStatus({ status: 'syncing' });
  });

  remoteCb && remoteCb(null, null);

  return 'success';
};

const getGeolocationParams = (profileGeolocationParams: IGeolocation, tzGeolocationParams: IGeolocation): IGeolocation => {
  if (profileGeolocationParams.fillBasedOnIp) {
    return {
      mode: profileGeolocationParams.mode,
      latitude: Number(tzGeolocationParams.latitude),
      longitude: Number(tzGeolocationParams.longitude),
      accuracy: Number(tzGeolocationParams.accuracy),
    };
  }

  return {
    mode: profileGeolocationParams.mode,
    latitude: profileGeolocationParams.latitude,
    longitude: profileGeolocationParams.longitude,
    accuracy: profileGeolocationParams.accuracy,
  };
};

const launchBrowser = async (args: string[], ua = ''): Promise<unknown> => {
  const majorChromeVersion = new BrowserReleasesManager().getMajorVersionByUA(ua);
  const {
    pathToLaunch,
    browserWorkingDir,
  } = new BrowserReleasesManager().getOrbitaPathToStart(majorChromeVersion);

  console.log('majorChromeVersion:', majorChromeVersion);
  console.log('pathToLaunch:', pathToLaunch);
  console.log('browserWorkingDir:', browserWorkingDir);

  const options: SpawnOptionsWithoutStdio = {
    shell: true,
    cwd: browserWorkingDir,
    detached: true,
    // stdio: 'ignore',
    stdio: 'pipe',
  };

  const spawnedProcess: ChildProcessWithoutNullStreams = spawn(`"${pathToLaunch}"`, args, options);
  spawnedProcess.unref();

  return spawnedProcess;
};

const removeLockFiles = (profileId: string): Promise<void[]> => {
  const promises = ['SingletonLock', 'SingletonSocket', 'SingletonCookie', 'RunningChromeVersion'].map(async (fileName) => {
    const filePath = pathNode.resolve(pathOs.PROFILES_DIR, profileId, fileName);
    console.log(filePath);

    return rm(filePath, { force: true });
  });

  return Promise.all(promises);
};
