/* eslint-disable camelcase */
/* eslint-disable max-lines */
import { access, readFile, unlink, writeFile } from 'fs/promises';

import { BrowserReleasesManager } from './browser-releases.manager';
import { BrowserUserDataManager } from './browser.user-data.manager';
import { CommonDataManager } from './common-data-manager';
import { CookiesManager } from './cookies-manager';
import { ExtensionsManager } from './extensions.manager';
import { getProfileParamsToken } from './utils/api';
import { getAutomationStartupUrl } from './utils/automation.utils';
import { getIntlProfileConfig } from './utils/browser-locales.utils';
import { createProfileExt } from './utils/extract.downloaded.extensions';
import { IGeolocation, IProxy } from '../../app/interfaces';
import { AUTOMATION_EXTENSION_WEBSTORE_ID } from '../constants/constants';
import { zeroProfilePreferences } from '../constants/zero-profile-preferences';
import { sentryTransactionDefaultObject } from '../helpers/helper.functions';
import { ITransactionObject } from '../interfaces/helper.functions.interfaces';
import {
  IPrepareGologinExtsProps,
  IProfileAudioContext,
  IProfileMediaDevices,
  IProfilePlugins,
  IProfileWebGL,
  IProfileWebGLMetadata,
  IProfileWebGLParams,
  IStartProfilePaths,
  IUpdateBrowserSettignsProps,
  IPrepareProfileToLaunch,
} from '../interfaces/profile-starter.manager.interfaces';
import {
  IGologinExtPathRes,
  IProfileData,
  IProfileNavigator,
} from '../interfaces/start-profile.renderer.interfaces';
import { saveConfigFile } from '../utils/config-manager';
import { BASE_PATH_TEMPLATE, MIN_MAJOR_CHROME_VERSION_FOR_CREATION_ORBITA_CONFIG, ORBITA_CONFIG_PATH } from '../utils/path-platforms';
import { sendErrorToSentry } from '../utils/sentry';
import { removeFileOrFolder } from '../utils/utils';

const ExtensionsManagerInst = new ExtensionsManager();
const CommonDataManagerInst = new CommonDataManager();
const BrowserUserDataManagerInst = new BrowserUserDataManager();

export class ProfileStarterManager {
  static instance: ProfileStarterManager;

  paths: IStartProfilePaths = {};
  apiUrl = '';

  constructor() {
    if (!ProfileStarterManager.instance) {
      ProfileStarterManager.instance = this;
    }

    return ProfileStarterManager.instance;
  }

  init(paths = {}, apiUrl = ''): void {
    this.paths = paths;
    this.apiUrl = apiUrl;
  }

  async prepareProfileToLaunch({ profileFingerprint, profileId, automation }: IPrepareProfileToLaunch): Promise<IProfileData> {
    const token = CommonDataManagerInst.accessToken;

    const {
      name = '',
      devicePixelRatio,
      owner,
      profile = '',
      debugMode = false,
      fonts,
      os,
      osSpec,
      osProfileRunPrefs,
      isM1 = false,
      navigator: profileNavigator = {} as IProfileNavigator,
      checkCookies = true,
      clientRects = {
        mode: 'noise',
        noise: 0,
      },
      chromeExtensions = [],
      userChromeExtensions = [],
      cookies = {
        clearCookies: false,
        userCookies: false,
      },
      storageInfo,
      startUrl = '',
      useCookiesExt = true,
      vaultToken = '',
      bookmarks,
      isAutoGenerated = false,
      autoLang = true,
      webRTC,
      isBookmarksSynced,
      lockEnabled = false,
    } = profileFingerprint;

    const isArm = (os === 'mac' && osSpec?.includes('M')) || isM1;

    if (startUrl.includes('iphey.com/{compare_profile}')) {
      const profileParamsToken = await getProfileParamsToken(this.apiUrl);

      profileFingerprint.startUrl = profileFingerprint.startUrl.replace(
        'iphey.com/{compare_profile}',
        `iphey.com/fingerprint_params?token=${profileParamsToken?.token}&profile_id=${profileId}`,
      );
    }

    const {
      platform = 'win32',
      resolution = '1920x1080',
      userAgent,
      doNotTrack = false,
      language: langHeader = 'en-US,en;q=0.9',
      hardwareConcurrency = 2,
      deviceMemory = 2,
      maxTouchPoints = 0,
    } = profileNavigator;

    const [screenWidth, screenHeight] = resolution.split('x');

    const { canvas } = profileFingerprint;
    const { mode: canvasMode, noise: canvasNoise } = canvas;

    const {
      proxy: profileProxy = {} as IProxy,
      proxyEnabled = false,
    } = profileFingerprint;

    const {
      username = '',
      password = '',
      mode: proxyMode = 'none',
      autoProxyRegion = 'us',
      torProxyRegion = 'us',
    } = profileProxy;

    const {
      autoProxyServer = '',
      autoProxyUsername = '',
      autoProxyPassword = '',
    } = profileFingerprint;

    const { webGL: profileWebGL = {} as IProfileWebGL } = profileFingerprint;
    const { mode: webGLMode = 'noise', noise: webGLNoise, getClientRectsNoise: webGLClientRectNoise = 0 } = profileWebGL;
    const clientRectsNoise = clientRects.noise || webGLClientRectNoise;

    const { webGLMetadata: profileWebGLMetadata = {} as IProfileWebGLMetadata, webglParams = {} as IProfileWebGLParams } = profileFingerprint;
    const { vendor = '', renderer = '', mode: wegGLMetadataMode = 'mask' } = profileWebGLMetadata;

    const { mediaDevices: profileMediaDevices = {} as IProfileMediaDevices } = profileFingerprint;
    const {
      enableMasking = true,
      uid,
      audioInputs = 1,
      videoInputs = 1,
      audioOutputs = 1,
    } = profileMediaDevices;

    const { plugins: profilePlugins = {} as IProfilePlugins } = profileFingerprint;
    const {
      enableVulnerable: all_enable = true,
      enableFlash: flash_enable = true,
    } = profilePlugins;

    const { storage = {} } = profileFingerprint;
    const {
      local = true,
      bookmarks: isAbleToEditBookmarks = true,
      history = true,
      passwords = true,
      extensions = true,
      session = true,
      indexedDb = true,
      enableExternalExtensions = false,
    } = storage;

    const { timezone: profileTimezone } = profileFingerprint;
    const { timezone: timezoneValue, fillBasedOnIp: fillProxyByIp = false } = profileTimezone;

    const { audioContext: profileAudioCtx = {} as IProfileAudioContext } = profileFingerprint;
    const { mode: audioCtxMode = 'off', noise: audioCtxNoise } = profileAudioCtx;
    const [splittedLangs] = langHeader.split(';');
    const [browserLang] = splittedLangs.split(',');

    let languages = splittedLangs;
    const langsParams = langHeader.split(',');
    langsParams.forEach((param: string, index: number) => {
      if (!index) {
        languages = param;

        return;
      }

      const [lang] = param.split(';');
      languages += `,${lang}`;
    });

    const { geolocation: geoLocation = {} as IGeolocation } = profileFingerprint;

    const dns = profileFingerprint.dns || '';
    const { createdInOs } = profileFingerprint;

    let startupUrl: string, startupUrls: string[];
    if (automation?.workflowId && automation?.extensionId) {
      const automationStartUrl = getAutomationStartupUrl({ extensionId: automation.extensionId, workflowId: automation.workflowId });
      console.info('automationStartUrl:', automationStartUrl);
      [startupUrl, startupUrls] = [automationStartUrl, [automationStartUrl]];
    } else {
      startupUrl = this.getFirstStartupUrl(profileFingerprint.startUrl) || '';
      startupUrls = (profileFingerprint.startUrl || '').split(',').map((url: string) => url.trim());
    }

    const deviceScaleFactorCeil = Math.ceil(devicePixelRatio || 3.5);
    let deviceScaleFactor = devicePixelRatio;
    if (deviceScaleFactorCeil === devicePixelRatio) {
      deviceScaleFactor += 0.00000001;
    }

    const chromeExtensionsUpdated = automation?.workflowId
      ? [...new Set([...chromeExtensions, AUTOMATION_EXTENSION_WEBSTORE_ID])]
      : chromeExtensions;

    return {
      profileId,
      lockEnabled,
      vaultToken,
      storageInfo,
      useCookiesExt,
      accessToken: token,
      proxy: profileProxy,
      profile,
      owner,
      os,
      osSpec,
      autoLang,
      proxyEnabled,
      autoProxyRegion,
      torProxyRegion,
      autoProxyServer,
      autoProxyUsername,
      autoProxyPassword,
      region: 'US',
      timezoneValue,
      browserLang,
      fillProxyByIp,
      debugMode,
      session,
      createdInOs,
      checkCookies,
      isAutoGenerated,
      bookmarks,
      isBookmarksSynced,
      bookmarksSettings: {
        editing_enabled: isAbleToEditBookmarks,
      },
      history: {
        saving_disabled: !history,
      },
      credentials_enable_service: passwords,
      extensionsEnabled: extensions,
      localStorageEnabled: local,
      indexedDbEnabled: indexedDb,
      enableExternalExtensions,
      fonts,
      chromeExtensions: chromeExtensionsUpdated,
      userChromeExtensions,
      cookies,
      gologin: {
        profile_id: profileId,
        name,
        is_m1: isArm,
        ...osProfileRunPrefs,
        geoLocation,
        navigator: {
          platform,
          max_touch_points: maxTouchPoints,
        },
        dns,
        proxy: {
          username: proxyMode !== 'none' ? username : '',
          password: proxyMode !== 'none' ? password : '',
        },
        screenHeight: parseInt(screenHeight, 10),
        screenWidth: parseInt(screenWidth, 10),
        userAgent,
        webGl: {
          vendor,
          renderer,
          mode: wegGLMetadataMode === 'mask',
        },
        webRTC,
        webgl: {
          metadata: {
            vendor,
            renderer,
            mode: wegGLMetadataMode === 'mask',
          },
        },
        mobile: {
          enable: os === 'android',
          width: parseInt(screenWidth, 10),
          height: parseInt(screenHeight, 10),
          device_scale_factor: deviceScaleFactor,
        },
        webglParams,
        webgl_noice_enable: webGLMode === 'noise',
        webglNoiceEnable: webGLMode === 'noise',
        webgl_noise_enable: webGLMode === 'noise',
        webgl_noise_value: webGLNoise,
        webglNoiseValue: webGLNoise,
        getClientRectsNoice: clientRectsNoise,
        get_client_rects_noise: clientRectsNoise,
        client_rects_noise_enable: clientRects.mode === 'noise',
        mediaDevices: {
          enable: enableMasking,
          uid,
          audioInputs,
          audioOutputs,
          videoInputs,
        },
        doNotTrack,
        plugins: {
          all_enable,
          flash_enable,
        },
        storage: {
          enable: local,
        },
        audioContext: {
          enable: audioCtxMode !== 'off',
          noiseValue: audioCtxNoise,
        },
        languages,
        langHeader,
        canvasMode,
        canvasNoise,
        hardwareConcurrency,
        deviceMemory: deviceMemory * 1024,
        startupUrl,
        startup_urls: startupUrls,
      },
    };
  }

  async updatingBrowserSettings(props: IUpdateBrowserSettignsProps): Promise<void> {
    const { profileData, publicUserIp, profileExtensionsCheckRes = [], timezoneCheckResult, args } = props;
    const ua = profileData.gologin?.userAgent || '';
    const majorChromeVersion = new BrowserReleasesManager().getMajorVersionByUA(ua);
    if (Number(majorChromeVersion || 120) >= MIN_MAJOR_CHROME_VERSION_FOR_CREATION_ORBITA_CONFIG) {
      const localeConfig = getIntlProfileConfig(profileData, timezoneCheckResult);
      const intl = {
        intl: localeConfig,
      };

      await saveConfigFile(ORBITA_CONFIG_PATH.replace('{profileId}', profileData.profileId), intl);
    }

    const { profileId } = profileData;

    const pathToPreferences = BASE_PATH_TEMPLATE.replace('{profileId}', profileId) + 'Preferences';

    console.log('path to preferences: ', pathToPreferences);

    const settingsFile = await readFile(pathToPreferences, 'utf-8').catch(() => {
      console.log('no pref file');

      return JSON.stringify(zeroProfilePreferences);
    });

    let settings;
    try {
      settings = JSON.parse(settingsFile);
    } catch (error) {
      console.log(settingsFile);
      throw error;
    }

    settings.gologin = profileData.gologin;
    settings.gologin.unpinable_extension_names = ['passwords-ext'];
    settings.bookmarks = profileData.bookmarksSettings;
    settings.history = profileData.history;
    settings.credentials_enable_service = profileData.credentials_enable_service;
    settings.extensions.block_external_extensions = !profileData.enableExternalExtensions;

    settings.gologin.webrtc = {
      enable: profileData.gologin.webRTC.enable,
      should_fill_empty_ice_list: profileData.gologin.webRTC.isEmptyIceList,
      mode: profileData.gologin.webRTC.mode,
    };
    delete settings.gologin.webRTC;

    const extSettings = await BrowserUserDataManagerInst.setExtPathsAndRemoveDeleted({
      settings,
      profileExtensionsCheckRes,
      profileId,
    });

    let extensionsToInstall = ExtensionsManagerInst.getExtensionsToInstall(extSettings, profileExtensionsCheckRes);
    const automationExtensionPath = profileExtensionsCheckRes
      .find((extensionPath: string) => extensionPath.includes(AUTOMATION_EXTENSION_WEBSTORE_ID));

    if (automationExtensionPath) {
      extensionsToInstall.push(automationExtensionPath);
      extensionsToInstall = [...new Set(extensionsToInstall)];
    }

    console.log('extensionsToInstall:', extensionsToInstall);

    const extensionArgIndex: number = args.findIndex(arg => arg.includes('--load-extension')) || 0;
    if (extensionsToInstall.length && extensionArgIndex > 0) {
      args[extensionArgIndex] = args[extensionArgIndex].slice(0, -1) + ',' + extensionsToInstall.join(',') + '"';
    }

    if (extSettings) {
      const currentExtSettings = settings.extensions || {};
      currentExtSettings.settings = extSettings;
      settings.extensions = currentExtSettings;
    }

    await writeFile(pathToPreferences, JSON.stringify(settings), { mode: 0o666 });
    console.log('Preferences has been successfully updated');
  }

  async prepareCookies(profileId: string, cookiesData: any, startProfileTransactionSentry = sentryTransactionDefaultObject): Promise<void> {
    const prepareCookiesSentrySpan = startProfileTransactionSentry.startChild({ op: 'prepare-cookies' }) || { finish: () => null };
    let cookiesFilePath = this.paths.PROFILES_DIR + profileId + this.paths.COOKIES_FILE;
    await access(cookiesFilePath).catch(() => {
      cookiesFilePath = this.paths.PROFILES_DIR + profileId + this.paths.COOKIES_FILE_NEW;
    });
    console.log('startProf cookiesFilePath:', cookiesFilePath);

    return CookiesManager.prepareCookies(profileId, cookiesData, cookiesFilePath).finally(() => prepareCookiesSentrySpan.finish());
  }

  async prepareGologinExts(props: IPrepareGologinExtsProps): Promise<IGologinExtPathRes | void> {
    const { name, profileId, owner, useCookiesExt, vaultToken, startProfileTransactionSentry } = props;
    const prepareGologinExtsSpan = startProfileTransactionSentry.startChild({ op: 'prepare-gologin-extensions' });
    const passwordsTemporaryToken = vaultToken;
    // console.log('passwordsTemporaryToken:', passwordsTemporaryToken);
    const paramsForExt = {
      name,
      profileId,
      password: owner,
      token: passwordsTemporaryToken,
      useCookiesExt,
    };

    return createProfileExt(paramsForExt, false, startProfileTransactionSentry)
      .then(res => ({
        profileGologinExtensionPath: res.map(el => el?.extPath || '').join(','),
        passwordsTemporaryToken,
      }) as IGologinExtPathRes)
      .catch((error: unknown) => {
        const message = error instanceof Error ? error.message : error;
        sendErrorToSentry({
          message, transactionName: 'creating-profile-extensions-error',
          tags: [['scenario', 'run-profile'], ['profileId', profileId]],
        });
      }).finally(() => prepareGologinExtsSpan.finish());
  }

  async checkTempFiles(profileId: string, startProfileTransactionSentry: ITransactionObject): Promise<void[]> {
    const checkFilesSentrySpan = startProfileTransactionSentry.startChild({ op: 'check-temp-files' });
    console.log('Removing stuck files');

    const tempFiles = [
      `${this.paths.ROUTE_SLASH}SingletonLock`, `${this.paths.ROUTE_SLASH}RunningChromeVersion`,
      `${this.paths.ROUTE_SLASH}SingletonCookie`, `${this.paths.ROUTE_SLASH}SingletonSocket`,
    ];

    const deletePromises =
      tempFiles
        .map((value) => unlink(this.paths.PROFILES_DIR + profileId + value).catch(() => Promise.resolve()));

    return Promise.all(deletePromises).catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      sendErrorToSentry({
        message, transactionName: 'check-temp-files', level: 'warning',
        tags: [['scenario', 'run-profile'], ['profileId', profileId]],
      });
      throw error;
    }).finally(() => checkFilesSentrySpan.finish());
  }

  removeUselessFolders(
    profileId: string,
    os: string,
    startProfileTransactionSentry = sentryTransactionDefaultObject,
  ): Promise<void | void[]> {
    console.log('Removing useless folders');
    const removeUselessFoldersSpan = startProfileTransactionSentry.startChild({ op: 'remove-useless-folders' });

    if (os === 'android') {
      return removeFileOrFolder(this.paths.PROFILES_DIR + profileId + `${this.paths.ROUTE_SLASH}Default${this.paths.ROUTE_SLASH}Sessions`)
        .catch((error: unknown) => {
          const message = error instanceof Error ? error.message : error;
          sendErrorToSentry({
            message, transactionName: 'remove-useless-folder', level: 'warning',
            tags: [['scenario', 'run-profile'], ['profileId', profileId]],
          });
        }).finally(() => removeUselessFoldersSpan.finish());

    }

    return Promise.resolve();
  }

  getFirstStartupUrl(startUpUrls: string): string {
    if (!startUpUrls) {
      return '';
    }

    const [firstUrl] = startUpUrls.split(',');

    return firstUrl.trim();
  }
}
