import { access } from 'fs/promises';

import moment from 'moment';

import { getCurrentMetadata, saveMetadata } from './check-competitors-config.utils';
import { ICompetitorCheckResult } from './interfaces/check-result.interface';
import { E_ACTIONS_WITH_COMPETITORS_APPS, ICheckCompetitorsAppsData } from '../../interfaces/check-competitors.interfaces';
import { CommonDataManager } from '../../managers/common-data-manager';
import { getPathOs } from '../../utils/path-platforms';
import { sendErrorToSentry } from '../../utils/sentry';
import { decryptCompetitorName, encryptCompetitorName } from '../crypto.utils';
import { electronSendActionAnalytics, syncGologinCompetitorsMetadata } from '../utils';

const CommonDataManagerInst = new CommonDataManager();
const pathOS = getPathOs();
const {
  DOLPHIN_DIR_PATH,
  OCTO_DIR_PATH,
  MULTILOGIN_DIR_PATH,
  ADSPOWER_DIR_PATH,
  UNDETECTABLE_DIR_PATH,
  INCOGNITION_DIR_PATH,
  INDIGO_DIR_PATH,
  MORELOGIN_DIR_PATH,
} = pathOS;

const pathsData: { [name: string]: string } = {
  DOLPHIN_DIR_PATH,
  OCTO_DIR_PATH,
  MULTILOGIN_DIR_PATH,
  ADSPOWER_DIR_PATH,
  UNDETECTABLE_DIR_PATH,
  INCOGNITION_DIR_PATH,
  INDIGO_DIR_PATH,
  MORELOGIN_DIR_PATH,
};

const checkCompetitorsApps = async (): Promise<ICompetitorCheckResult[]> => {
  const promises = Object.keys(pathsData).map((pathToApp: string): Promise<ICompetitorCheckResult> => {
    const [COMPETITOR_NAME] = pathToApp.split('_');

    return access(pathsData[pathToApp]).then(() => ({ [COMPETITOR_NAME]: true }), () => ({ [COMPETITOR_NAME]: false }));
  });

  return Promise.all(promises);
};

export const checkCompetitorsMetadata = async (gologinMetadata: ICheckCompetitorsAppsData): Promise<void> => {
  const { lastCheckDate = '' } = gologinMetadata;
  let isSyncError = false;

  const newMetadata: any = {};
  let currentMetadata: any = {};

  try {
    const isAbleToCheckMetadata =
      !lastCheckDate ||
      moment(lastCheckDate).utc().add(1, 'd').valueOf() < moment().utc().valueOf();

    if (!(isAbleToCheckMetadata && CommonDataManagerInst.accessToken)) {
      return;
    }

    const checkInstallCompetitorsResults = await checkCompetitorsApps();
    checkInstallCompetitorsResults.forEach((checkInstallCompetitorResult) => {
      Object.keys(checkInstallCompetitorResult).forEach(key => {
        newMetadata[encryptCompetitorName(key)] = checkInstallCompetitorResult[key];
      });
    });

    currentMetadata = await getCurrentMetadata();
    const { data: currentCompetitorsData = {} } = currentMetadata;
    const competitorsNames = Object.keys(newMetadata);

    const promises = competitorsNames.map((competitor): Promise<string> | null => {
      const isInstalled = !currentCompetitorsData[competitor] && newMetadata[competitor];
      const isDeleted = currentCompetitorsData[competitor] && !newMetadata[competitor];
      const decryptedName = decryptCompetitorName(competitor).toLowerCase();

      if (!(isInstalled || isDeleted)) {
        return null;
      }

      const action = isInstalled ? E_ACTIONS_WITH_COMPETITORS_APPS.INSTALLED : E_ACTIONS_WITH_COMPETITORS_APPS.UNINSTALLED;

      return sendCompetitorAnalytics({ action, actionId: currentMetadata.fingerprint, decryptedName });
    });

    await Promise.all(promises)
      .then((results) => {
        if (results.includes('error')) {
          isSyncError = true;
        }
      })
      .catch((error: unknown) => {
        isSyncError = true;
        const message = error instanceof Error ? error.message : error;
        sendErrorToSentry({
          message, tags: [['scenario', 'check-competitors']],
          transactionName: 'check-competitors-apps-states-error', level: 'warning',
        });
      });
  } catch (error: unknown) {
    isSyncError = true;
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, tags: [['scenario', 'check-competitors']],
      transactionName: 'in-check-process-error', level: 'warning',
    });
  }

  if (isSyncError) {
    return;
  }

  const metadata = {
    ...currentMetadata,
    data: newMetadata,
  };

  await saveMetadata(metadata).catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, tags: [['scenario', 'check-competitors']],
      transactionName: 'save-metadata-error', level: 'warning',
    });
  });

  syncGologinCompetitorsMetadata({
    lastCheckDate: moment().utc().toDate(),
  }).catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, tags: [['scenario', 'check-competitors']],
      transactionName: 'sync-metadata-with-db-error', level: 'warning',
    });
  });
};

const sendCompetitorAnalytics = ({
  action,
  actionId,
  decryptedName,
}: {
  action: E_ACTIONS_WITH_COMPETITORS_APPS;
  actionId: string;
  decryptedName: string;
}): Promise<string> =>
  electronSendActionAnalytics(`${action} - ${decryptedName}`, {
    actionInfo: decryptedName,
    actionId,
  })
    .then(() => 'ok')
    .catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      sendErrorToSentry({
        message, tags: [['scenario', 'check-competitors'], ['competitor', decryptedName]],
        transactionName: `sending-analytics-${action}-error`, level: 'warning',
      });

      return 'error';
    });
