import { unlink, writeFile, mkdir, readdir } from 'fs/promises';
import * as pathNodeJs from 'path';

import { SeverityLevel } from '@sentry/electron';
import moment from 'moment';

import { STORAGE_GATEWAY_BASE_URL } from '../constants/constants';
import { zeroProfileBookmarks } from '../constants/zero-profile-bookmarks';
import { zeroProfilePreferences } from '../constants/zero-profile-preferences';
import { IExtractProfileResult, IArchiveProfileProps, IUnzipProfileProps } from '../interfaces/profile-extractor.interfaces';
import { ProfileCachingManager } from '../managers/profile.caching.manager';
import { createDBFile } from './crypto/cookies-decryptor';
import { downloadWithRetry } from './downloadWithRetry';
import { getPathOs } from './path-platforms';
import { decompressProfile } from './profile-archiver';
import { sendCommonErrorToSentry } from './sentry';
import { checkFileSizeSync, removeFileOrFolder } from './utils';
import withRetry from './with-retry';

const path = getPathOs();

const BYTES_IN_MB = 1024 * 1024;

const ProfileCachingManagerInst = new ProfileCachingManager();
ProfileCachingManagerInst.init().catch(() => null);

export const unzipProfile = async (props: IUnzipProfileProps): Promise<string | void> => {
  const {
    timings,
    remoteCb,
    profilePath,
    getProfileArchiveOptions,
  } = props;

  const {
    storageInfo,
    profileOptions,
    startProfileTimestamp,
    startProfileTransactionSentry,
    ProfileStatusManagerInst,
  } = getProfileArchiveOptions;

  const timeBeforeAws = moment.utc().valueOf();
  const zipPath = await getProfileArchive(getProfileArchiveOptions);

  startProfileTransactionSentry.tags = { ...startProfileTransactionSentry.tags, 'profile-mode': profileOptions.startMode };

  if (zipPath === 'error') {
    return 'error';
  }

  if (zipPath === 'no need to decompress') {
    ProfileStatusManagerInst.addStatus({ status: 'preparing_profile' });
    ProfileStatusManagerInst.removeStatus({ status: 'downloading_profile_init' });

    return;
  }

  let zipSize = 0;
  checkFileSizeSync(zipPath).then((fileSize: number) => {
    zipSize = fileSize;
    startProfileTransactionSentry.tags = { ...startProfileTransactionSentry.tags, 'zip-size-MB': fileSize / BYTES_IN_MB };
  }).catch(() => null);

  timings.awsRequest = moment.utc().valueOf() - timeBeforeAws;

  await removeFileOrFolder(profilePath).catch(console.log);

  const timeBeforeDecompress = moment.utc().valueOf();
  const decompressSentrySpan = startProfileTransactionSentry.startChild({ op: 'decompress-profile' });
  //     /tmp/gologin/
  const decompressRes = await extractProfile(zipPath, profilePath);

  if (decompressRes.status === 'error') {
    let statusMessage = 'profileStatusMessages.errorDefectiveProfile';
    let sendError = true;

    if (decompressRes.error.message.includes('ENOSPC')) {
      statusMessage = 'profileStatusMessages.errorDiscSpace';
    } else if (!storageInfo.isNewProfile) {
      getProfileArchiveOptions.onlyAws = true;
      console.log('try to download from aws');
      const zipPathDownloaded = await getProfileArchive(getProfileArchiveOptions);

      const decompressAwsRes = await extractProfile(zipPathDownloaded, profilePath);
      if (decompressAwsRes.status !== 'error') {
        sendError = false;
      }
    }

    if (sendError) {
      sendCommonErrorToSentry({
        message: decompressRes.error.message, transactionName: 'extract-profile', level: 'fatal',
        tags: [['scenario', 'run-profile'], ['name', 'extract-profile']],
      });

      ProfileStatusManagerInst.addStatus({
        status: 'error',
        startProfileTimestamp,
        errorMessage: 'profileStatusMessages.archiveDecompressError',
        profileOptions,
      });

      remoteCb && remoteCb(null, { status: 403, message: statusMessage });

      return 'error';
    }
  }

  console.log('Extracting profile zip-archive completed');
  ProfileStatusManagerInst.addStatus({ status: 'preparing_profile' });
  ProfileStatusManagerInst.removeStatus({ status: 'downloading_profile_init' });

  if (!(zipPath.includes('gologin-cached-profiles') || zipPath.includes('zero_profile'))) {
    //  zip-,   
    console.log('Removing profile zip-archive');
    unlink(zipPath).catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      console.error('Error in removing zip and folders:', (message || 'no error'));
    });
  }

  startProfileTransactionSentry.tags = {
    ...startProfileTransactionSentry.tags,
    'internet-speed-mbs': timings.awsRequest / 1000 / (zipSize / BYTES_IN_MB) * 8,
  };

  timings.decompress = moment.utc().valueOf() - timeBeforeDecompress;
  decompressSentrySpan.finish();
};

const extractProfile = async (zipPath = '', profilePath = ''): Promise<IExtractProfileResult> => {
  console.log(`zipPath: ${zipPath}, profilePath: ${profilePath}`);
  const decompressRes = await withRetry<IExtractProfileResult>({
    fn() {
      return decompressProfile(zipPath, profilePath)
        .then(() => ({ status: 'success' }));
    },
    retryDelay: 300,
    limit: 2,
  }).catch(async (error: Error) => {
    readdir(pathNodeJs.join(profilePath, 'Default')).then(console.log).catch(console.log);

    sendCommonErrorToSentry({ message: error.message, transactionName: 'decompress-profile' });

    return { status: 'error', error };
  }) || {};

  return decompressRes;
};

const getProfileArchive = async (options: IArchiveProfileProps): Promise<string> => {
  const {
    zipPath,
    profileId,
    startProfileTimestamp,
    startProfileTransactionSentry,
    storageInfo,
    profileOptions,
    ProfileStatusManagerInst,
    onlyAws = false,
  } = options;

  const cacheCheckResponse = await ProfileCachingManagerInst.getCachedProfile(storageInfo, profileId);

  if (cacheCheckResponse.status && !onlyAws) {
    profileOptions.startMode = 'cached';

    return cacheCheckResponse.cachedArchivePath || '';
  }

  if (storageInfo.isNewProfile) {
    profileOptions.startMode = 'zero profile';

    return createZeroProfile({ profileId }).catch((error: unknown) => {
      console.log(error);
      const message = error instanceof Error ? error.message : error;
      const transactionName = 'create-zero-profile';
      const level: SeverityLevel = 'fatal';
      if (typeof message === 'string' && message.includes('table cookies already exists')) {
        return 'no need to decompress';
      }

      if (typeof message === 'string' && (message.includes('ENOENT') || message.includes('SQLITE_FULL'))) {
        ProfileStatusManagerInst.addStatus({
          status: 'error',
          statusMessage: 'profileStatusMessages.discSpaceError',
          startProfileTimestamp,
        });
      }

      sendCommonErrorToSentry({
        message, transactionName, level,
        tags: [['scenario', 'run-profile'], ['name', transactionName]],
      });

      return 'error';
    });
  }

  const downloadProfileSentrySpan = startProfileTransactionSentry.startChild({ op: 'download-profile-from-aws' });

  const downloadResult = await downloadWithRetry({
    url: storageInfo.storageLink || `${STORAGE_GATEWAY_BASE_URL}/download`,
    destFile: zipPath,
    profileId,
  }).catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    const transactionName = 'download-profile-from-aws';
    let statusMessage = 'profileStatusMessages.errorProfileLoading';

    if (typeof message === 'string' && message.includes('ENOSPC')) {
      statusMessage = 'profileStatusMessages.discSpaceError';
    }

    if (typeof message === 'string' && message.includes('404')) {
      statusMessage = 'profileStatusMessages.profileDeleted';
    }

    sendCommonErrorToSentry({
      message, transactionName, tags: [['name', transactionName]],
    });

    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage,
      startProfileTimestamp,
    });

    return 'error';
  });

  downloadProfileSentrySpan.finish();

  if (downloadResult === 'error') {
    const errorMessage = 'Error downloading profile. Check your internet connection.';

    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage: 'profileStatusMessages.errorProfileLoading',
      startProfileTimestamp,
      errorMessage,
    });

    return 'error';
  }

  console.log('Profile zip-archive writing completed');

  return zipPath;
};

export const createZeroProfile = async ({ profileId }: any): Promise<string> => {
  const profilePath = path.PROFILES_DIR + profileId;
  const preferencesFilePath = pathNodeJs.resolve(profilePath, 'Default', 'Preferences');
  const bookmarksFilePath = pathNodeJs.resolve(profilePath, 'Default', 'Bookmarks');
  const networkFolderPath = pathNodeJs.resolve(profilePath, 'Default', 'Network');
  const cookiesFilePath = pathNodeJs.resolve(profilePath, 'Default', 'Network', 'Cookies');
  const secondCookiesFilePath = pathNodeJs.resolve(profilePath, 'Default', 'Cookies');

  await mkdir(networkFolderPath, { recursive: true });

  const promises = [
    writeFile(preferencesFilePath, JSON.stringify(zeroProfilePreferences), { mode: 0o666 }),
    writeFile(bookmarksFilePath, JSON.stringify(zeroProfileBookmarks), { mode: 0o666 }),
    createDBFile(cookiesFilePath, secondCookiesFilePath),
  ];

  await Promise.all(promises);

  return 'no need to decompress';
};
