import { exec } from 'child_process';
import * as os from 'os';
import { promisify } from 'util';

import packageSettings from '../../../package.json';
import { UserOS } from '../interfaces/common-data-manager.interfaces';

const doExec = promisify(exec);
const { version, subVersion } = packageSettings;

export const osPlatform = process.platform;

export const getOS = async (): Promise<UserOS> => {
  if (process.platform === 'win32') {
    return 'win';
  }

  if (process.platform === 'darwin') {
    const m1 = await isM1();

    return m1 ? 'macM1' : 'mac';
  }

  return 'lin';
};

const isM1 = async (): Promise<boolean> => {
  const { stdout } = await doExec('sysctl machdep.cpu')
    .catch((error: unknown) => {
      console.log('error in M1 detection ', error);

      return { stdout: '' };
    });

  return /Apple M\d/.test(stdout);
};

export const getWinRelease = (): number => {
  const winRelease = os.release() || '';
  const [winVersion = '10', _versionMinor, buildVersion] = winRelease.split('.');
  if (buildVersion.startsWith('2')) {
    return 11;
  }

  return +winVersion;
};


export const getGologinMetaHeader = async (): Promise<string> => {
  const typeOS = await getOS();
  const [osRelease = ''] = os.release();
  const appRelease = `${version} ${subVersion}`;
  if (typeOS === 'win') {
    return `${appRelease}-${typeOS}-${getWinRelease()}`;
  }

  return `${appRelease}-${typeOS}-${osRelease}}`;
};
