import { mkdir } from 'fs/promises';
import path from 'path';

import { v4 as uuidv4 } from 'uuid';

import { IAppConfigRecord } from '../../interfaces/app-config.interfaces';
import { readConfigFile, saveConfigFile } from '../../utils/config-manager';
import { CONFIG_APP_PATH } from '../../utils/path-platforms';
import { sendErrorToSentry } from '../../utils/sentry';

const pathToMetadataFolder = path.join(CONFIG_APP_PATH, path.sep, 'gologin-metadata');
const metadataFileName = 'data.json';
const configFilePath = path.join(pathToMetadataFolder, path.sep, metadataFileName);

export const getCurrentMetadata = async (): Promise<any> => {
  const currentMetadata = await readConfigFile(configFilePath, 'metadata');
  if (!(currentMetadata && !currentMetadata.fingerprint)) {
    return currentMetadata;
  }

  const defaultMetadata = {
    fingerprint: uuidv4(),
    data: {},
  };

  await mkdir(pathToMetadataFolder, { recursive: true })
    .then(() => saveConfigFile(configFilePath, defaultMetadata, 'metadata'))
    .catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      sendErrorToSentry({
        message, tags: [['scenario', 'check-competitors']],
        transactionName: 'saveing-competitor-check-metadata-error', level: 'warning',
      });
    });

  return defaultMetadata;
};

export const saveMetadata = async (data: IAppConfigRecord): Promise<any> =>
  saveConfigFile(configFilePath, data, 'metadata');
