export let deletedProfiles: string[] = [];

export const updateDeletedProfiles = (profileIdsToRestore: string[]): void => {
  deletedProfiles = deletedProfiles.filter(deletedProfile => !profileIdsToRestore.includes(deletedProfile));
};

export const loadProfile = async (props: ILoadProfileDataProps): Promise<string|void|null> => {
  const {
    profileId,
    channel,
    remoteApiOptions,
    proxyInfo = {},
    proxyEnabled = false,
    automation = { workflowId: '' },
    launchViaRestApi = false,
    icons = { pngs: [], design: null },
    traceId,
    deepLinkProtocol,
  } = props;

  console.log('load profile start', profileId);
  const isProfileRunning = Object.prototype.hasOwnProperty.call(BRAVE_PROCESSES, profileId);
  if (deepLinkProtocol) {
    const actionName = isProfileRunning ? OpenProfileViaLink.startedFocusing : OpenProfileViaLink.startedOpening;
    sendActionAnalytics(actionName, { mode: deepLinkProtocol, profile: profileId });
  }

  if (isProfileRunning) {
    return focusOrbitaWindow(profileId, deepLinkProtocol).catch(() => null);
  }

  const ProfileStatusManagerInst = new ProfileStatusManager(channel, profileId);

  return requestProfileFromDbAndPrepare({
    profileId,
    channel,
    remoteApiOptions,
    proxyInfo,
    proxyEnabled,
    ProfileStatusManagerInst,
    automation,
    launchViaRestApi,
    icons,
    traceId,
    deepLinkProtocol,
  }).catch((error: unknown) => {
    console.log(error);
    const message = error instanceof Error ? error.message : error;
    sendCommonErrorToSentry({
      message, tags: [['scenario', 'run-profile'], ['profileId', profileId]],
      transactionName: 'profile-prepare-error', level: 'fatal',
    });

    ProfileStatusManagerInst.addStatus({
      status: 'error', errorMessage: 'profileStatusMessages.prepareProfileError',
    });
  });
};

const requestProfileFromDbAndPrepare = async (props: IRequestProfileFromDBProps): Promise<string | void | null> => {
  const {
    profileId,
    channel,
    remoteApiOptions,
    proxyInfo = {},
    proxyEnabled = false,
    ProfileStatusManagerInst,
    automation,
    launchViaRestApi,
    icons = { pngs: [], design: null },
    traceId,
    deepLinkProtocol,
  } = props;

  const { cb, sync } = remoteApiOptions || {};
  if (profileId.length !== 24) {
    cb && cb(null, { status: 400, message: 'invalid profile id' });

    return 'error';
  }

  const startProfileTimestamp = moment.utc().valueOf();
  const ProfileReadyEventEmitterInst = new EventEmitter();
  const ProfileReadyCheckerInst = new ProfileReadyChecker(ProfileReadyEventEmitterInst);

  const TimezoneManagerInst = new TimezoneManager({
    proxyInfo, proxyEnabled, ProfileReadyCheckerInst,
  });

  ProfileStatusManagerInstances[profileId] = ProfileStatusManagerInst;

  if (loadingProfiles.includes(profileId) || syncingProfiles.includes(profileId)) {
    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage: 'Profile is still syncing',
    });

    return 'error';
  }

  loadingProfiles.push(profileId);

  const startProfileTransactionSentry =
    Sentry.startTransaction({ name: 'run-profile', traceId }) as ITransactionObject || sentryTransactionDefaultObject;

  const profileRequestFromDbSentrySpan = startProfileTransactionSentry.startChild({ op: 'request-profile-from-db' });
  const proxyCheckSpan = startProfileTransactionSentry.startChild({ op: 'check-proxy' });
  const timings: IProfileTimings = {};
  const timeBeforeProfileRequest = moment.utc();

  const osArch = process.arch;

  if (['x32', 'ia32'].includes(osArch)) {
    sendErrorToSentry({
      message: '32-bit operating system', tags: [['scenario', 'run-profile'], ['is-32-bit', true]],
      transactionName: 'open-orbita-32-bit-system', level: 'warning',
    });

    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage: 'profileStatusMessages.32bitError',
    });

    return 'error';
  }

  ProfileStatusManagerInst.addStatus({ status: 'downloading_profile_init' });

  if (proxyEnabled) {
    ProfileStatusManagerInst.addStatus({ status: 'checking_proxy' });
  }

  const isFreeProxy = ['gologin', 'tor'].includes(proxyInfo.mode || '');
  startProfileTransactionSentry.tags = { ...startProfileTransactionSentry.tags, 'proxy-mode': proxyInfo.mode, profileId };
  if (!(isFreeProxy || launchViaRestApi)) {
    TimezoneManagerInst.getTimeZone({} as IProfileData, timings)
      .then((res) => {
        if (res.status !== 'error') {
          return;
        }

        sendTimezoneStatus(res as ITimeZoneError, ProfileStatusManagerInst, startProfileTimestamp);
      })
      .finally(() => {
        proxyCheckSpan.finish();
        ProfileStatusManagerInst.removeStatus({ status: 'checking_proxy' });
      });
  }

  if (isStoppingProfiles[profileId]) {
    delete isStoppingProfiles[profileId];
    cb && cb(null, { status: 403, message: 'Profiles is not stopped yet' });

    return null;
  }

  const profileFromDb = await requestProfileFingerprint(profileId).catch((error: any) => {
    const backendErrorHeader = 'backend@error::';
    const message = error instanceof Error ? error.message : error;
    let statusMessage = 'profileStatusMessages.profileRequestError';
    let sendToSentry = true;
    console.log(error?.response?.body);
    if (typeof message === 'string' && message.includes('429')) {
      statusMessage = 'profileStatusMessages.tooManyRequestsError';
    }

    if (typeof message === 'string' && message.includes('404')) {
      statusMessage = 'profileStatusMessages.profileDeleted';
      sendToSentry = false;
    }

    if (typeof message === 'string' && message.includes('402')) {
      statusMessage = 'profileStatusMessages.paymentRequired';
    }

    if (typeof message === 'string' && message.includes('403')) {
      statusMessage = 'profileStatusMessages.noRights';
      sendToSentry = false;
    }

    sendToSentry && sendCommonErrorToSentry({
      message, tags: [['scenario', 'run-profile'], ['profileId', profileId], ['name', 'request-profile-from-db']],
      transactionName: 'request-profile-from-db',
    });

    const errorContent = error.response?.body || '';
    if (errorContent.includes(backendErrorHeader)) {
      const errorData =
        errorContent
          .replace(backendErrorHeader, '')
          .slice(1, -1); //    

      statusMessage = errorData;
    }

    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage,
    });

    cb && cb(null, { status: 403, message: 'Error in getting profile info' });
    startProfileTransactionSentry.setStatus('aborted');
    startProfileTransactionSentry.finish();

    return 'error';
  });

  if (typeof profileFromDb === 'string') {
    startProfileTransactionSentry.setStatus('aborted');
    startProfileTransactionSentry.finish();

    return 'error';
  }

  const { canBeRunning = false } = profileFromDb;
  if (!canBeRunning) {
    cb && cb(null, { status: 403, message: 'Profile is already running' });
    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage: 'notifications.error.runByAnotherUser',
    });

    return 'cantBeRunning';
  }

  const isOrbitaCompatible = new BrowserReleasesManager().checkOrbitaWinCompatible({
    key: 'userAgent',
    value: profileFromDb.navigator.userAgent,
    ProfileStatusManagerInst,
  });

  if (!isOrbitaCompatible) {
    sendErrorToSentry({
      message: 'windows version is not supported',
      tags: [['scenario', 'run-profile']],
      transactionName: 'orbita-compatibility-with-windows-error',
    });

    return 'error';
  }

  profileRequestFromDbSentrySpan.finish();

  timings.profileRequest = moment.utc().diff(timeBeforeProfileRequest);
  const isAutomationExtensionSavedInDb = profileFromDb.chromeExtensions && profileFromDb.chromeExtensions.includes(AUTOMATION_EXTENSION_WEBSTORE_ID);

  if (automation.workflowId) {
    const publicKey = generatePublicKey();
    const extensionId = convertPublicKeyToExtensionId(publicKey);

    automation.publicKey = publicKey;
    automation.extensionId = extensionId;
  }

  const profileData = await ProfileStarterManagerInst.prepareProfileToLaunch({
    profileFingerprint: profileFromDb,
    profileId,
    automation,
  });

  //      REST API
  if (launchViaRestApi) {
    const proxyTypes = ['gologin', 'tor', 'socks4', 'socks5', 'possh', 'http', 'geolocation'];
    const isFreeProxyType = ['gologin', 'tor'].includes(profileData.proxy.mode || '');
    const isProxyEnabled = proxyTypes.includes(profileData.proxy.mode || '');
    if (isProxyEnabled) {
      ProfileStatusManagerInst.addStatus({ status: 'checking_proxy' });
    }

    TimezoneManagerInst.proxyInfo = profileData.proxy;
    TimezoneManagerInst.proxyEnabled = isProxyEnabled;
    TimezoneManagerInst.getTimeZone(isFreeProxyType ? profileData : {} as IProfileData, timings)
      .then((res) => {
        if (res.status !== 'error') {
          return;
        }

        sendTimezoneStatus(res as ITimeZoneError, ProfileStatusManagerInst, startProfileTimestamp);
      })
      .finally(() => {
        proxyCheckSpan.finish();
        ProfileStatusManagerInst.removeStatus({ status: 'checking_proxy' });
      });
  }

  const freeProxyCheckSpan = startProfileTransactionSentry.startChild({ op: 'check-proxy' });

  if (isFreeProxy && !launchViaRestApi) {
    TimezoneManagerInst.getTimeZone(profileData, timings)
      .then((res) => {
        if (res.status !== 'error') {
          return;
        }

        sendTimezoneStatus(res as ITimeZoneError, ProfileStatusManagerInst, startProfileTimestamp);
      })
      .finally(() => {
        freeProxyCheckSpan.finish();
        ProfileStatusManagerInst.removeStatus({ status: 'checking_proxy' });
      });
  }

  //   
  const foldersToCreate = [path.PROFILES_DIR, path.START_DIR_PATH, path.STARTED_DIR_PATH];

  const promises = foldersToCreate.map((elem) =>
    access(elem).then(Promise.resolve).catch(() => mkdir(elem, { recursive: true })),
  );

  await Promise.all(promises).catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    console.log('Error in working folder init:', (message || 'no error'));
    sendErrorToSentry({
      message, tags: [['scenario', 'run-profile'], ['profileId', profileId]],
      transactionName: 'profile-folder-init-error', level: 'warning',
    });
  });

  !sync && cb && cb(null, null);

  return startProf({
    profileData,
    channel,
    startProfileTimestamp,
    remoteApiOptions,
    ProfileReadyCheckerInst,
    timings,
    startProfileTransactionSentry,
    ProfileStatusManagerInst,
    automation: { ...automation, isAutomationExtensionSavedInDb },
    icons,
    deepLinkProtocol,
  });
};

export function updateUseragent(ua: string): void {
  CommonDataManagerInst.userAgent = ua;

  console.log('Useragent has been updated successfully:', ua);
}

export function updateToken(token: string, twoFa: string): void {
  if (!CommonDataManagerInst.accessToken) {
    getChangableAppConfigFromBack().catch(console.log);
  }

  CommonDataManagerInst.accessToken = token;
  CommonDataManagerInst.twoFaKey = twoFa;
}

export function killProfile(profileId: string): void {
  isStoppingProfiles[profileId] = true;
  try {
    if (profileId in BRAVE_PROCESSES) {
      if (osPlatform === 'win32') {
        getWinOrbitaRealPid(BRAVE_PROCESSES[profileId].orbitaProc?.pid)
          .then((realPid) => {
            if (!realPid) {
              return;
            }

            exec(`taskkill /pid ${realPid}`);
          }).catch((error: unknown) => {
            const message = error instanceof Error ? error.message : error;
            console.log(1, profileId, BRAVE_PROCESSES);
            sendCommonErrorToSentry({
              message, tags: [['scenario', 'close-profile'], ['profileId', profileId], ['name', 'kill-orbita-process-error']],
              transactionName: 'kill-orbita-process-error',
            });
          });
      } else if (BRAVE_PROCESSES[profileId].orbitaProc?.pid) {
        kill(BRAVE_PROCESSES[profileId].orbitaProc?.pid, 'SIGTERM');
      }
    }
  } catch (error: unknown) {
    const message = error instanceof Error ? error.message : error;
    console.log(1, profileId, BRAVE_PROCESSES);
    sendCommonErrorToSentry({
      message, tags: [['scenario', 'close-profile'], ['profileId', profileId], ['name', 'kill-orbita-process-error']],
      transactionName: 'kill-orbita-process-error',
    });
  }

  delete isStoppingProfiles[profileId];
}

