export const closeAllProfiles = async (): Promise<void> => {
  const processes: string[] = [];
  for (const processId in BRAVE_PROCESSES) {
    processes.push(processId);
    BRAVE_PROCESSES[processId] && kill(BRAVE_PROCESSES[processId].orbitaProc?.pid, 'SIGTERM');
    console.log('process', processId, 'has been stopped');
  }

  const promises = processes.map(procId =>
    makeRequest({ url: `${CommonDataManagerInst.apiBaseUrl}/browser/${procId}/run-sync`, method: 'PATCH', body: { running: false } })
      .catch(() => null),
  );

  await Promise.all(promises).catch(() => null);
};

export const checkProfiles = (): void => {
  const profiles = Object.keys(ProfileStatusManagerInstances);
  for (const profile of profiles) {
    const manager = ProfileStatusManagerInstances[profile];
    if (!manager) {
      continue;
    }

    manager.refreshStatus();
  }
};

export const startProf = async (props: IStartProfileProps): Promise<string> => {
  const {
    profileData,
    channel,
    startProfileTimestamp,
    remoteApiOptions,
    ProfileReadyCheckerInst,
    timings,
    startProfileTransactionSentry = sentryTransactionDefaultObject,
    ProfileStatusManagerInst,
    automation,
    icons = { pngs: [], design: null },
    deepLinkProtocol,
  } = props;

  const { profileId, storageInfo, owner, fonts, os, useCookiesExt, vaultToken } = profileData;
  const { cb: remoteCb, debuggingPort = null } = remoteApiOptions || {};
  const name = profileData.gologin?.name || '';
  const currentMs = new Date().getMilliseconds();
  const profilePath = path.PROFILES_DIR + profileId;

  const fontsSupported = ['linux', 'win32', 'darwin'].includes(osPlatform);
  const isWin7 = (CommonDataManagerInst.userAgent || '').includes('Windows NT 6.1');
  const families = fonts.families || [];
  const composeFonts = fontsSupported && fonts.enableMasking && !isWin7;

  if (composeFonts && !families.length) {
    sendErrorToSentry({
      message: 'No fonts list provided. Please select at least 1', transactionName: 'prepare-fonts-not-provided', level: 'fatal',
      tags: [['scenario', 'run-profile'], ['profileId', profileId]],
    });

    const statusMessage = 'profileStatusMessages.noFontsProvided';
    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage,
      startProfileTimestamp,
    });
    startProfileTransactionSentry.setStatus('aborted');
    startProfileTransactionSentry.finish();
    remoteCb && remoteCb(null, { status: 403, message: statusMessage });

    return 'error';
  }

  const profileOptions = {};

  const zipPath = `${path.PROFILES_DIR}${profileId}_${currentMs}.zip`;
  const getProfileArchiveOptions: IArchiveProfileProps = {
    profileId,
    currentMs,
    storageInfo,
    startProfileTimestamp,
    zipPath,
    profileOptions,
    ProfileStatusManagerInst,
    startProfileTransactionSentry,
  };

  const unzipProfileResult = await unzipProfile({
    timings,
    remoteCb,
    profilePath,
    getProfileArchiveOptions,
  });

  if (unzipProfileResult === 'error') {
    startProfileTransactionSentry.setStatus('aborted');
    startProfileTransactionSentry.finish();

    return 'error';
  }

  //     /,      
  const promises: Promise<
    ICheckLocalUserChromeExtensionsResult |
    ICheckChromeExtensionsResult |
    IComposeFontsErrorData |
    IGologinExtPathRes |
    void | void[]
    >[] =
    [
      ProfileStarterManagerInst.checkTempFiles(profileId, startProfileTransactionSentry).catch(() => void 0),
      ProfileStarterManagerInst.removeUselessFolders(profileId, os, startProfileTransactionSentry).catch(() => void 0),
    ];

  //    ,      (  ,       )
  const cookiesData = profileData.cookies || { checkCookies: profileData.checkCookies };
  promises.push(ProfileStarterManagerInst.prepareCookies(profileId, cookiesData, startProfileTransactionSentry).catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, transactionName: 'cookies-global-error', level: 'error',
      tags: [['scenario', 'run-profile'], ['profileId', profileId]],
    });
  }));

  //     
  const args = [
    `--user-data-dir="${profilePath}"`,
    '--disable-encryption',
    `--donut-pie=${profileData.gologin.hashValues}`,
    '--webrtc-ip-handling-policy=default_public_interface_only',
  ];

  if (debuggingPort) {
    args.push(`--remote-debugging-port=${debuggingPort}`);
  }

  let fontArg = '--font-masking-mode=1';
  if (composeFonts) {
    const differentOs = os !== 'android' && (
      osPlatform === 'win32' && os !== 'win' ||
      osPlatform === 'darwin' && os !== 'mac' ||
      osPlatform === 'linux' && os !== 'lin'
    );

    fontArg = '--font-masking-mode=2';
    if (differentOs) {
      fontArg = '--font-masking-mode=3';
    }

    ProfileStatusManagerInst.addStatus({ status: 'downloading_profile_fonts' });
    //    ,    ,   
    //  ,    
    promises.push(BrowserUserDataManagerInst.composeFonts({ fontsList: families, profilePath, differentOs, startProfileTransactionSentry })
      .catch((error: unknown) => {
        const message = error instanceof Error ? error.message : error;
        let errorTxt = 'profileStatusMessages.fontsError';
        let level: Sentry.SeverityLevel = 'fatal';
        let transactionName = 'prepare-fonts';

        if (typeof message === 'string' && message.includes('ENOSPC')) {
          errorTxt = 'profileStatusMessages.discSpaceError';
          level = 'warning';
          transactionName = 'no-disc-space';
        }

        sendErrorToSentry({
          message, transactionName, level,
          tags: [['scenario', 'run-profile'], ['profileId', profileId]],
        });

        return {
          isError: true,
          type: 'fonts-error',
          error,
          errorTxt,
        };
      }).finally(() => ProfileStatusManagerInst.removeStatus({ status: 'downloading_profile_fonts' })));
  }

  args.push(fontArg);
  //      (  ),
  //      (  ,       )
  promises.push(ProfileStarterManagerInst.prepareGologinExts({
    name,
    profileId,
    owner,
    useCookiesExt,
    vaultToken,
    startProfileTransactionSentry,
  }));

  const chromeExtensions = profileData.chromeExtensions || [];
  const userChromeExtensions = profileData.userChromeExtensions || [];

  startProfileTransactionSentry.tags = {
    ...startProfileTransactionSentry.tags,
    'extensions-count': chromeExtensions.length + userChromeExtensions.length,
  };

  const checkChromeExtensionsSpan = startProfileTransactionSentry.startChild({ op: 'download-chrome-extensions' });

  if (chromeExtensions.length) {
    ProfileStatusManagerInst.addStatus({ status: 'downloading_profile_chrome_ext' });
    //        ,
    //      (  ,       )
    promises.push(ExtensionsManagerInst.checkChromeExtensions(chromeExtensions)
      .then(async (res) => {
        const automationExtensionPath = res.find((extensionPath) => extensionPath.includes(AUTOMATION_EXTENSION_WEBSTORE_ID));
        if (automationExtensionPath) {
          const checkAutomationChromeExtensionSpan = startProfileTransactionSentry.startChild({ op: 'modify-automation-chrome-extension' });
          // We need to replace `manifest.json` to ensure the first run of automation
          // runs correctly, without eternal spinner or 'chrome-extension://invalid' or 'ERR_BLOCKED_BY_CLIENT'.

          // Anyway, this is a temprory solution and must be replaced with custom logic
          // of downloading and unpacking a stable version of Goless extension from s3.

          const manifestPath = pathNodeJs.join(automationExtensionPath, 'manifest.json');
          let manifestToReplace;
          if (automation.publicKey) {
            automationExtensionManifest.key = automation.publicKey;
          }

          try {
            manifestToReplace = JSON.stringify(automationExtensionManifest);
          } catch (error) {
            console.error('Automation error: replacing manifest.json', error);
            checkAutomationChromeExtensionSpan.setStatus('aborted');
          }

          if (manifestToReplace) {
            await writeFile(manifestPath, manifestToReplace, { mode: 0o666 }).catch((error) => {
              console.error('Automation: ERROR while replacing manifest.json', error);
              sendErrorToSentry({
                message: error.message, transactionName: 'automation-error', level: 'error',
                tags: [['scenario', 'run-profile'], ['profileId', profileId]],
              });
            });
          }

          checkAutomationChromeExtensionSpan.finish();
        }

        return { profileExtensionsCheckRes: res };
      })
      .catch((error: unknown) => {
        const message = error instanceof Error ? error.message : error;
        sendErrorToSentry({
          message, transactionName: 'check-chrome-extensions-at-profile-start', level: 'error',
          tags: [['scenario', 'run-profile'], ['profileId', profileId]],
        });

        console.error('checkChromeExtensions error: ', error);

        return { profileExtensionsCheckRes: [] };
      }).finally(() => {
        checkChromeExtensionsSpan.finish();
        ProfileStatusManagerInst.removeStatus({ status: 'downloading_profile_chrome_ext' });
      }));
  }

  const checkUserExtensionsSpan = startProfileTransactionSentry.startChild({ op: 'download-user-extensions' });

  if (userChromeExtensions.length) {
    ProfileStatusManagerInst.addStatus({ status: 'downloading_profile_user_ext' });
    promises.push(ExtensionsManagerInst.checkLocalUserChromeExtensions(userChromeExtensions, profileId)
      .then(res => ({ profileUserExtensionsCheckRes: res }))
      .catch((error: unknown) => {
        const message = error instanceof Error ? error.message : error;
        sendErrorToSentry({
          message, transactionName: 'check-user-extensions-error', level: 'error',
          tags: [['scenario', 'run-profile'], ['profileId', profileId]],
        });

        return [];
      }).finally(() => {
        checkUserExtensionsSpan.finish();
        ProfileStatusManagerInst.removeStatus({ status: 'downloading_profile_user_ext' });
      }),
    );
  }

  const pathToPreferences = BASE_PATH_TEMPLATE.replace('{profileId}', profileId) + 'Preferences';
  const pathToBookmarks = BASE_PATH_TEMPLATE.replace('{profileId}', profileId) + 'Bookmarks';

  promises.push(access(pathToPreferences).catch(() =>
    writeFile(pathToPreferences, JSON.stringify(zeroProfilePreferences), { mode: 0o666 })
      .catch((error: unknown) => {
        const message = error instanceof Error ? error.message : error;
        sendCommonErrorToSentry({
          message, transactionName: 'write-preferences-before-profile-start',
          tags: [['scenario', 'write-preferences'], ['profileId', profileId]],
        });
      })));

  // eslint-disable-next-line camelcase
  let { bookmarks = { bookmark_bar: null } } = profileData;
  promises.push(checkBookmarks(profileId, bookmarks, pathToBookmarks)
    .then((bookmarksData) => {
      bookmarks = bookmarksData;
      const profileBookmarks = {
        ...zeroProfileBookmarks,
        roots: bookmarks,
      };

      return writeFile(pathToBookmarks, JSON.stringify(profileBookmarks), { mode: 0o666 });
    }).catch(() => void 0));

  const winVersionMajor = getWinRelease();
  const osFormatted = formatOs(osPlatform, winVersionMajor);
  if (osFormatted && icons.design === '2:green-square') {
    const { scaleFactor = 1 } = screen.getPrimaryDisplay();
    const saveBrowserIconsSpan = startProfileTransactionSentry.startChild({ op: 'save-browser-icons' });
    const browserIconsDataToSave: ISaveBrowserIconsInProfileDirectoryData = {
      iconsPngs: icons.pngs,
      os: osFormatted,
      profilePath,
      scaleFactor,
      iconDesignVersion: icons.design,
    };

    promises.push(saveBrowserIconsInProfileDirectory(browserIconsDataToSave, saveBrowserIconsSpan));
    let browserIconsSizes: number[] = [];
    switch (true) {
      case osFormatted === 'mac':
        browserIconsSizes = BROWSER_ICONS_SIZES_FOR_MAC;
        break;
      case osFormatted === 'win10':
        browserIconsSizes = BROWSER_ICONS_SIZES_FOR_WIN10;
        break;
      case osFormatted === 'win11':
        browserIconsSizes = BROWSER_ICONS_SIZES_FOR_WIN11;
        break;
      default:
        break;
    }

    const browserIconsPaths: IBrowserIconsPaths = browserIconsSizes
      .map(size => size * scaleFactor)
      .reduce<IBrowserIconsPaths>((acc, size) => {
        acc[`${size}`] = `${size}.png`;

        return acc;
      }, {});

    // eslint-disable-next-line camelcase
    let avatar: IBrowserIconAvatar = { enabled: true, full_size: true };
    if (icons.design === '2:green-square') {
      // eslint-disable-next-line camelcase
      avatar = { enabled: true, windows_icons: browserIconsPaths };
    }

    profileData.gologin.icon = { avatar };
  }

  profileData.chromeExtensions = [];
  //    
  const timeBeforePromises = moment.utc();
  const profilePreparingRes = (await Promise.all(promises)).flat().filter(Boolean);
  timings.promises = moment.utc().diff(timeBeforePromises);

  // console.log('profilePreparingRes:', profilePreparingRes);
  //    1   , -        
  const errorStageObj = profilePreparingRes.find((el: any) => Object.prototype.hasOwnProperty.call(el, 'isError'));
  if (errorStageObj) {
    const { errorTxt = '' } = errorStageObj as IComposeFontsErrorData;
    startProfileTransactionSentry.setStatus('aborted');
    startProfileTransactionSentry.finish();
    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage: errorTxt,
      startProfileTimestamp,
      profileOptions,
    });

    remoteCb && remoteCb(null, { status: 403, message: errorTxt });

    return 'error';
  }

  let extParam = '--load-extension=';
  const { extensionsEnabled } = profileData;

  if (!extensionsEnabled) {
    extParam = '--disable-extensions-except=';
  }

  const profileGologinExtensionPathRes = profilePreparingRes.find((el: any) => 'profileGologinExtensionPath' in el) as IGologinExtPathRes;

  const extensionsStrToIncludeAsOrbitaParam = profileGologinExtensionPathRes?.profileGologinExtensionPath || '';
  const passwordsTemporaryToken = profileGologinExtensionPathRes?.passwordsTemporaryToken || '';

  args.push(`${extParam}"${extensionsStrToIncludeAsOrbitaParam}"`);

  if (profileData.debugMode) {
    if (osPlatform === 'win32') {
      args.push('--enable-logging=stderr');
    } else {
      args.push('--enable-logging');
    }

    args.push('--v=1');
    args.push('--vmodule=metrics=2');
    args.push('--no-sandbox');
  }

  const { webglParams } = profileData;
  if (!profileData.gologin.webgl.metadata.mode) {
    args.push('--disable-webgl');
    args.push('--disable-webgl2');
  } else if (webglParams && webglParams.glCanvas === 'webgl') {
    args.push('--disable-webgl2');
  }

  if (
    webglParams &&
    Object.prototype.hasOwnProperty.call(webglParams, 'antialiasing') &&
    !webglParams.antialiasing
  ) {
    args.push('--webgl-antialiasing-mode=none');
  }

  if (profileData.session && !automation?.workflowId) {
    args.push('--restore-last-session');
  }

  const {
    autoProxyServer,
  } = profileData;

  const { mode = 'none', host: proxyHost = '', port = 80 } = profileData.proxy;
  const proxyMode = mode.toLowerCase();
  //   
  //     brave  
  if (profileData.proxyEnabled) {
    let server = ['gologin', 'tor'].includes(proxyMode) ? autoProxyServer : proxyHost;
    let protocol = '';

    if (proxyMode === 'http') {
      protocol = 'http://';
    }

    if (proxyMode === 'socks4') {
      protocol = 'socks4://';
    }

    if (proxyMode === 'socks5') {
      protocol = 'socks5://';
    }

    if (server.includes('http://')) {
      [, server] = server.split('http://');
      protocol = 'http://';
    }

    if (server.includes('https://')) {
      [, server] = server.split('https://');
      protocol = 'https://';
    }

    if (server.includes('socks5://')) {
      [, server] = server.split('socks5://');
      protocol = 'socks5://';
    }

    if (server.includes('socks4://')) {
      [, server] = server.split('socks4://');
      protocol = 'socks4://';
    }

    if (['gologin', 'tor'].includes(proxyMode)) {
      console.log('autoProxyServer', autoProxyServer);
      const splittedAutoProxyServer = autoProxyServer.split('://');
      const splittedProxyAddress = splittedAutoProxyServer[1].split(':');
      const [host, _port] = splittedProxyAddress;

      args.push(
        // `--proxy-server=${splittedAutoProxyServer[0]}://${host}:${port}`,
        `--proxy-server=${host}:${_port}`,
      );
      if (!protocol.includes('socks4')) {
        args.push(`--host-resolver-rules="MAP * 0.0.0.0 , EXCLUDE ${host}"`);
      }
    } else {
      args.push(`--proxy-server=${protocol}${server}:${+port}`);
      if (!protocol.includes('socks4')) {
        args.push(`--host-resolver-rules="MAP * 0.0.0.0 , EXCLUDE ${server}"`);
      }
    }
  }

  const ProfileReadyEventEmitterInst = ProfileReadyCheckerInst.eventEmitter;

  ProfileReadyEventEmitterInst.on('profile-ready', async (timezoneCheckResult: IGetTimezoneResult) => {
    if (profileData.proxyEnabled) {
      TimezoneManager.updateProxyStatus(profileData.proxy.id || '', timezoneCheckResult);
    }

    //    === string,   status -> 'error',      
    if (typeof timezoneCheckResult.body === 'string') {
      let errorMessage = timezoneCheckResult.status === 'error' ? timezoneCheckResult.body : 'notifications.error.timezoneRequestError';
      if (errorMessage === 'Promise was canceled') {
        errorMessage = 'profilesTable.error.proxyNonfunctional';
      }

      ProfileStatusManagerInst.addStatus({
        status: 'error',
        statusMessage: errorMessage,
        startProfileTimestamp,
        profileOptions,
      });

      remoteCb && remoteCb(null, { status: 403, message: errorMessage });

      return 'error';
    }

    if (timezoneCheckResult.protocol === 'https') {
      const proxyOptionIndex = args.findIndex((arg) => arg.startsWith('--proxy-server'));
      args[proxyOptionIndex] = args[proxyOptionIndex].replace('http://', 'https://');
    }

    const isMAC = osPlatform === 'darwin';
    const checkAutoLangResult = checkAutoLang(profileData, timezoneCheckResult.body);
    const browserLang = isMAC ? 'en-US' : checkAutoLangResult;
    args.push(`--lang=${browserLang}`);
    console.log('args:', args);

    launchOrbita({
      timezoneCheckResult: timezoneCheckResult.body, startProfileTimestamp, profileOptions,
      remoteCb, profileData, automation, args, channel, passwordsTemporaryToken, currentMs,
      ProfileReadyEventEmitterInst, profilePreparingRes, timings, startProfileTransactionSentry,
      ProfileStatusManagerInst, ProfileStatusManagerInstances, deepLinkProtocol,
    }).catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      let statusMessage = 'profileStatusMessages.prepareProfileError';

      if (message === 'No installed Orbita') {
        statusMessage = 'profileStatusMessages.noOrbita';
      }

      sendErrorToSentry({
        message, tags: [['scenario', 'run-profile'], ['profileId', profileId]],
        transactionName: 'prepare-orbita-launch-error', level: 'fatal',
      });

      startProfileTransactionSentry.setStatus('aborted');
      startProfileTransactionSentry.finish();
      ProfileStatusManagerInst.addStatus({
        status: 'error', statusMessage,
      });
    });
  });

  ProfileReadyCheckerInst.isProfilePrepared = true;

  return 'success';
};

export const openLink = ({ link = '' }): void => {
  open(link).catch(() => null);
};

export const syncProfile = async (props: ISyncProfileProps): Promise<void> => {
  const {
    profileId,
    channel,
    onlyUpload = false,
  } = props;

  let ProfileStatusManagerInst = ProfileStatusManagerInstances[profileId];

  if (!ProfileStatusManagerInst) {
    ProfileStatusManagerInst = new ProfileStatusManager(channel, profileId);
    ProfileStatusManagerInstances[profileId] = ProfileStatusManagerInst;
  }

  const status = onlyUpload ? 'cloud_uploading' : 'archiving';

  ProfileStatusManagerInst.addStatus({ status });
  ProfileStatusManagerInst.unblock();
  ProfileStatusManagerInst.removeStatus({ status: 'error' });

  const { s3SignedUrl = { url: '' }, profileArchiveBuffer, stopProfileTransactionSentry } = (await updateProfileAndGetUploadUrl({
    profileId,
    channel,
    onlyUpload,
    ProfileStatusManagerInst,
    vaultToken: '',
  }).catch((error: unknown) => {
    console.log(error);
    const message = error instanceof Error ? error.message : JSON.stringify(error);
    if (message === 'archiving profile error') {
      return;
    }

    sendErrorToSentry({
      message, transactionName: 'sync-process-status-error', level: 'fatal',
      tags: [['scenario', 'close-profile'], ['profileId', profileId]],
    });
  })) || {};

  if (!profileArchiveBuffer) {
    console.log('error, no buffer (sync)');

    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage: 'Archive with profile not found, synchronization is not possible',
    });

    return;
  }

  await uploadingProfileProcess({
    profileId,
    s3SignedUrl,
    profileArchiveBuffer,
    ProfileStatusManagerInst,
    ProfileStatusManagerInstances,
    stopProfileTransactionSentry,
  });
};

const sendTimezoneStatus = (res: ITimeZoneError, ProfileStatusManagerInst: ProfileStatusManager, startProfileTimestamp: number): void => {
  const errorMessage: string = getProxyErrorMessage(res.body);
  if (res.type === 'timezone') {
    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage: {
        errorType: 'notifications.error.timezoneRequestError',
        errorMessage: 'error',
        prefix: 'timezone',
      },
      startProfileTimestamp,
    });

    return;
  }

  if (res.type === 'proxy') {
    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage: {
        errorType: 'notifications.error.proxyError',
        errorMessage,
        prefix: 'proxy',
      },
      startProfileTimestamp,
    });

    return;
  }

  ProfileStatusManagerInst.addStatus({
    status: 'error',
    statusMessage: {
      errorType: 'notifications.error.somethingWentWrong',
      errorMessage,
      prefix: 'proxy',
    },
    startProfileTimestamp,
  });
};

const getProxyErrorMessage = (rawMessage: string): string => {
  switch (true) {
    case rawMessage === 'Promise was canceled':
      return 'profilesTable.error.proxyNonfunctional';
    default:
      return rawMessage;
  }
};

export const checkAutoLang = (profileData: any, timezoneCheckResult: ICheckProxyResult): string => {
  if (!profileData.autoLang) {
    return checkBrowserLang(profileData);
  }

  let timezoneLang = '';
  const { country: timezoneCountry = '', languages } = timezoneCheckResult || {} as ICheckProxyResult;
  if (languages) {
    const [firstDetectedLangLocale] = languages.split(',');
    //   languages,    timezoneCountry,   languages    
    timezoneLang = `${firstDetectedLangLocale}-${timezoneCountry}` || '';

    let resultLangsArr: string[] = [];
    const [lang = '', country = ''] = timezoneLang.split('-');
    if (country) {
      resultLangsArr.push([lang, country].join('-'));
    }

    resultLangsArr.push(lang, 'en-US', 'en');
    resultLangsArr = [...new Set(resultLangsArr)];

    const gologinLangsArr: string[] = [];
    const result = resultLangsArr.reduce((acc: string, cur: string, index: number) => {
      if (!index) {
        acc += `${cur},`;
        gologinLangsArr.push(cur);

        return acc;
      }

      const qualityParam = 10 - index;
      if (qualityParam > 0) {
        const separator = (resultLangsArr.length - index) < 2 ? '' : ',';
        gologinLangsArr.push(cur);
        acc += `${cur};q=${Number(qualityParam * 0.1).toFixed(1)}${separator}`;
      }

      return acc;
    }, '');

    [profileData.browserLang] = resultLangsArr;
    profileData.gologin.languages = gologinLangsArr.join(',');
    profileData.gologin.langHeader = result;

    return profileData.browserLang;
  }

  return checkBrowserLang(profileData);
};

const checkBrowserLang = (profileData: any, defaultLocale = 'en-US'): string => {
  if (profileData.gologin.langHeader) {
    return profileData.browserLang;
  }

  profileData.browserLang = defaultLocale;
  profileData.gologin.languages = defaultLocale;
  profileData.gologin.langHeader = defaultLocale;

  return defaultLocale;
};
