import axios        from 'axios';

import { IServerApiRequestOptions, IServerApiRequestProps } from '../../../interfaces/server.api.interfaces';
import { CommonDataManager } from '../../../managers/common-data-manager';
import { makeRequest } from '../../../utils/make-request';

axios.defaults.headers['Content-Type'] = 'application/json';

const CommonDataManagerInst = new CommonDataManager();

export const apiRequest = (props: IServerApiRequestProps): Promise<unknown> => {
  const { endpoint, method, headers, body } = props;

  const requestOpts: IServerApiRequestOptions = {
    url: `${CommonDataManagerInst.apiBaseUrl}${endpoint}`,
    method,
    headers: {
      ...headers,
      Authorization: `Bearer ${CommonDataManagerInst.accessToken}`,
      'user-agent': CommonDataManagerInst.userAgent,
      'x-two-factor-token': CommonDataManagerInst.twoFaKey,
    },
  };

  if (method !== 'GET') {
    requestOpts.body = body;
  }

  return makeRequest(requestOpts).catch(() => null);
};
