import { BrowserWindow } from 'electron';

import { BASE_URL } from './constants';
import { handleOAuth } from './handle-o-auth';
import { handleProfileRun } from './handle-profile-run';
import { handleShare } from './handle-share';

export const handleDeepLink = async (window: BrowserWindow|null, responseUrl = ''): Promise<void> => {
  if (!(window && responseUrl.includes('gologin://'))) {
    console.info('Skipping handling a deep link');

    return;
  }

  const [_protocolName, argument1, argument2, argument3] = responseUrl.split('/').filter(Boolean);
  switch (true) {
    case argument1.startsWith('o-auth'):
      return handleOAuth(window, responseUrl);
    case argument1.startsWith('redirect') && argument2.startsWith('after-payment'): {
      const absolutePathUrl = new URL(`${BASE_URL}#/profileList`);
      window.loadURL(absolutePathUrl.toString());

      break;
    }
    case argument2 && argument3 && argument1 === 'share':
      return handleShare(window, responseUrl);
    case argument1 === 'open' || (argument1 && !argument2):
      return handleProfileRun(window, responseUrl);
    default:
      return console.info('Pathname is not recognised');
  }
};
