/* eslint-disable max-lines */
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
import { access, mkdir, readdir, readFile, rm, writeFile, rename } from 'fs/promises';
import os from 'os';
import path from 'path';
import util from 'util';

import decompress from 'decompress';
import decompressTarGz from 'decompress-targz';
import decompressUnzip from 'decompress-unzip';

import { downloadPersistentWithProgress } from './downloadWithRetry';
import { makeRequest } from './make-request';
import { BROWSER_FOLDER_NAME, PLATFORM, BROWSER_PATH } from './path-platforms';
import { sendCommonErrorToSentry, sendErrorToSentry } from './sentry';
import { getOS, getWinRelease } from './userOs';
import { removeFileOrFolder, copyFolder } from './utils';
import withRetry from './with-retry';
import {
  MAC_VERSION_FILE_URL,
  MAC_ARM_VERSION_FILE_URL,
  DEB_VERSION_FILE_URL,
  WIN_VERSION_FILE_URL,
  BROWSER_ARCHIVE_NAME,
  MAC_BROWSER_LINK,
  MAC_ARM_BROWSER_LINK,
  DEB_BROWSER_LINK,
  WIN_BROWSER_LINK,
  MAC_HASH_FILE,
  MAC_ARM_HASH_FILE,
  DEB_HASH_FILE,
  WIN_HASH_FILE,
  MAC_HASHFILE_LINK,
  MAC_ARM_HASHFILE_LINK,
  DEB_HASHFILE_LINK,
  WIN_HASHFILE_LINK,
  FAIL_SUM_MATCH_MESSAGE,
  EXTRACTED_FOLDER,
  VERSION_FILE,
  DEB_BROWSER_LINK_S3, WIN_BROWSER_LINK_S3, MAC_BROWSER_LINK_S3, MAC_ARM_BROWSER_LINK_S3,
} from '../constants/constants';
import { BrowserReleasesManager } from '../managers/browser-releases.manager';
import { getChromeMajorVersion } from '../managers/utils/browser-checker.utils';

const exec = util.promisify(require('child_process').exec);

const INIT_PROGRESS_STATE = {
  step: 0,
  inProgress: true,
};

let EVENT: any = null;
let progressState: any = {
  ...INIT_PROGRESS_STATE,
};

const checkBrowserArchive = (orbitaVersion = ''): Promise<void> => {
  console.log('IN checkBrowserArchive');
  progressState = { ...progressState, step: 1 };

  EVENT && EVENT.sender.send('download-browser', {
    progressState,
    result: false,
    orbitaVersion,
  });

  const archivePath = path.join(BROWSER_PATH, BROWSER_ARCHIVE_NAME).replace('{{orbita-version}}', '');

  return withRetry({
    fn() {
      return access(archivePath);
    },
    retryDelay: 100,
  });
};

const downloadNewBrowser = async (orbitaVersion = ''): Promise<void> => {
  console.log('IN downloadNewBrowser', { orbitaVersion });
  EVENT && EVENT.sender.send('download-browser', {
    progressState,
    result: false,
    orbitaVersion,
  });

  const pathStr = path.join(BROWSER_PATH, BROWSER_ARCHIVE_NAME).replace('{{orbita-version}}', '');
  await removeFileOrFolder(pathStr).catch(() => null);
  const localBrowserReleases = new BrowserReleasesManager().localOrbitaReleasesList;
  let resultOrbitaVersion = orbitaVersion;
  let replacedValue = resultOrbitaVersion ? `-${resultOrbitaVersion}` : '';

  let link = DEB_BROWSER_LINK;
  if (PLATFORM === 'win32') {
    const winVersion = getWinRelease();
    if (winVersion < 10) {
      if (resultOrbitaVersion && Number(resultOrbitaVersion) >= 110) {
        EVENT && EVENT.sender.send('download-browser', {
          progressState,
          result: true,
          unsupportedWinVersion: true,
        });

        throw new Error('notifications.error.unsupportedWinVersion');
      }

      if (!(resultOrbitaVersion || localBrowserReleases['109'])) {
        resultOrbitaVersion = '109';
        replacedValue = '-109';
      }
    }

    link = WIN_BROWSER_LINK;
  }

  if (PLATFORM === 'darwin') {
    link = MAC_BROWSER_LINK;
    const userOs = await getOS();
    if (userOs === 'macM1') {
      link = MAC_ARM_BROWSER_LINK;
    }
  }

  link = link.replace('{{orbita-version}}', replacedValue);

  await withRetry({
    fn() {
      return mkdir(BROWSER_PATH, { recursive: true });
    },
  }).catch((error) => {
    console.log('error in making browser dir');
    throw error;
  });

  console.log('orbita download started');

  await downloadPersistentWithProgress({
    url: link,
    destFile: pathStr,
    progressEvent: EVENT,
    progressEventName: 'download-browser',
    progressState,
    isContinueStream: true,
    retryDelay: 5000,
    retryLimit: 100,
    orbitaVersion,
  });

  console.log('orbita download finished');

  await checkBrowserArchive(orbitaVersion);

  await installBrowser(resultOrbitaVersion);
};

const downloadHashFile = async (orbitaVersion = ''): Promise<void> => {
  console.log('IN downloadHashFile');
  const platform = PLATFORM;
  let hashLink = DEB_HASHFILE_LINK;

  let resultPath = path.join(BROWSER_PATH, DEB_HASH_FILE);
  if (platform === 'win32') {
    return Promise.resolve();

    // eslint-disable-next-line no-unreachable
    hashLink = WIN_HASHFILE_LINK;
    resultPath = path.join(BROWSER_PATH, WIN_HASH_FILE);
  }

  if (platform === 'darwin') {
    hashLink = MAC_HASHFILE_LINK;
    resultPath = path.join(BROWSER_PATH, MAC_HASH_FILE);

    const userOs = await getOS();
    if (userOs === 'macM1') {
      hashLink = MAC_ARM_HASHFILE_LINK;
      resultPath = path.join(BROWSER_PATH, MAC_ARM_HASH_FILE);
    }
  }

  const replacedValue = orbitaVersion ? `-${orbitaVersion}` : '';
  hashLink = hashLink.replace('{{orbita-version}}', replacedValue);
  resultPath = resultPath.replace('{{orbita-version}}', replacedValue);

  return withRetry({
    fn() {
      return makeRequest<string | Uint8Array>({ url: hashLink, timeout: 30 * 1000, method: 'GET', json: false })
        .then(content => writeFile(resultPath, content, { mode: 0o666 }))
        .then(() => access(resultPath))
        .then(
          () => Promise.resolve(),
          () => Promise.reject(new Error('Hash file has not been downloaded')),
        );
    },
    retryDelay: 1000,
    limit: 10,
  });
};

const checkBrowserSum = (orbitaVersion = ''): Promise<void> => downloadHashFile(orbitaVersion)
  .then(() => {
    console.log('IN checkBrowserSum');
    const platform = PLATFORM;
    const extractedFolderPath = path.join(BROWSER_PATH, EXTRACTED_FOLDER);
    const replacedValue = orbitaVersion ? `-${orbitaVersion}` : '';
    if (PLATFORM === 'win32') {
      return Promise.resolve();

      // eslint-disable-next-line no-unreachable
      const winHashFilePath = path.join(BROWSER_PATH, WIN_HASH_FILE).replace('{{orbita-version}}', replacedValue);

      return withRetry({
        fn() {
          return exec(`type "${winHashFilePath}"`, { maxBuffer: 1024 * 9000 }).then(
            (res: any) => {
              const serverRes = (res.stdout || '')
                .toString()
                .trim()
                .replace(/(\r\n|\n|\r)/gm, '');

              console.log('serverRes:', serverRes);

              return exec(
                // eslint-disable-next-line max-len
                `cd "${BROWSER_PATH}" && CertUtil -hashfile ${BROWSER_ARCHIVE_NAME.replace('{{orbita-version}}', '')} SHA256 | find /i /v "SHA256" | find /i /v "certutil"`,
                { maxBuffer: 1024 * 9000 },
              ).then((userCheckRes: any) => {
                const userRes = (userCheckRes.stdout || '')
                  .toString()
                  .trim()
                  .replace(/(\r\n|\n|\r)/gm, '');

                console.log('userRes:', userRes);
                const formattedUserRes = userRes.replace(/\s/g, '');
                if (userRes !== serverRes && formattedUserRes !== serverRes) {
                  return Promise.reject(new Error('Error in sum matching'));
                }

                return Promise.resolve();
              });
            },
          );
        },
      });
    }

    if (platform === 'darwin') {
      const macOrbitaPath = path.join(BROWSER_PATH, EXTRACTED_FOLDER, 'Orbita-Browser.app');
      const macHashFilePath = path.join(BROWSER_PATH, MAC_HASH_FILE).replace('{{orbita-version}}', replacedValue);

      return withRetry({
        fn() {
          return exec(`mtree -p ${macOrbitaPath} < ${macHashFilePath} || echo ${FAIL_SUM_MATCH_MESSAGE}`)
            .then((res= '') => {
              const checkedRes = res.toString().trim();
              if (checkedRes.includes(FAIL_SUM_MATCH_MESSAGE)) {
                const release = os.release();
                const isDebian = release.toLowerCase().includes('debian');

                if (isDebian) {
                  return Promise.reject(new Error('Debian os is not supported'));
                }

                return Promise.reject(new Error('Error in sum matching'));
              }

              return Promise.resolve();
            });
        },
      });
    }

    const debHashFile = path.join(BROWSER_PATH, DEB_HASH_FILE).replace('{{orbita-version}}', replacedValue);

    return withRetry({
      fn() {
        return readFile(debHashFile, 'utf-8').then((res = '') => {
          let serverRes = res.trim();
          [serverRes] = res.split(' ');
          serverRes = serverRes.replace(/(\r\n|\n|\r)/gm, '');

          return exec(
            `cd ${extractedFolderPath} && find orbita-browser -type f -print0 | sort -z | \
             xargs -0 sha256sum > ${BROWSER_PATH}/calculatedFolderSha.txt`,
          )
            .then(() => exec(`cd ${BROWSER_PATH} && sha256sum calculatedFolderSha.txt`))
            .then((userCheckRes: any) => {
              let userRes = (userCheckRes.stdout || '').toString().trim();
              [userRes] = userRes.split(' ');
              if (userRes !== serverRes) {
                return Promise.reject(new Error('Error in sum matching'));
              }

              return Promise.resolve();
            });
        });
      },
    });
  });

const replaceBrowser = async (orbitaVersion = ''): Promise<unknown> => {
  console.log('IN replaceBrowser');
  progressState = { ...progressState, step: 2 };
  EVENT && EVENT.sender.send('download-browser', {
    progressState,
    result: false,
    orbitaVersion,
  });

  const latestBrowserVersion = await getLatestBrowserVersion(orbitaVersion).catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    sendCommonErrorToSentry({
      message, transactionName: 'request-last-orbita-version-error', level: 'fatal',
      tags: [['scenario', 'download-orbita'], ['name', 'request-last-orbita-version-error']],
    });

    throw error;
  });

  const [majorV] = latestBrowserVersion.split('.');
  const browserPath = path.join(BROWSER_PATH, BROWSER_FOLDER_NAME + '-' + majorV);
  console.log('browserPath:', browserPath);
  const folderPathToCopy = PLATFORM === 'darwin' ?
    path.join(BROWSER_PATH, EXTRACTED_FOLDER) :
    path.join(BROWSER_PATH, EXTRACTED_FOLDER, BROWSER_FOLDER_NAME);

  await rm(browserPath, { recursive: true }).catch(() => null);
  await rename(folderPathToCopy, browserPath).catch(console.log);

  if (PLATFORM !== 'darwin') {
    return;
  }

  return withRetry({
    fn() {
      return mkdir(path.join(browserPath, 'version'), { recursive: true })
        .then(() => writeFile(
          path.join(browserPath, 'version', VERSION_FILE),
          latestBrowserVersion,
          { mode: 0o666 },
        ));
    },
    retryDelay: 100,
  }).catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, transactionName: 'writing-orbita-version-file-error',
      tags: [['scenario', 'download-orbita']],
    });
  });
};

const installBrowser = async (orbitaVersion = ''): Promise<void> => {
  await decompressTar(orbitaVersion);
  await checkBrowserArchive();
  await checkBrowserSum(orbitaVersion);
  await replaceBrowser(orbitaVersion);

  progressState = { ...progressState, step: 3 };
  EVENT && EVENT.sender.send('download-browser', {
    progressState,
    result: false,
  });
};

const decompressTar = async (orbitaVersion: string): Promise<void> => {
  console.log('IN extractBrowser');
  console.log(`extractBrowser absolutePath: ${BROWSER_PATH}`);

  console.log('creating folder for extraction');
  const extractedFolderPath = path.join(BROWSER_PATH, EXTRACTED_FOLDER);
  const browserPath = path.join(BROWSER_PATH, BROWSER_ARCHIVE_NAME).replace('{{orbita-version}}', '');

  await withRetry({
    fn() {
      return mkdir(extractedFolderPath, { recursive: true });
    },
    retryDelay: 100,
  });
  console.log('starting decompress');

  if (PLATFORM === 'darwin') {
    await exec(`tar -xzf "${browserPath}" -C "${extractedFolderPath}"`).catch((error: Error) => {
      if (error.message?.includes('Ignoring malformed pax extended attribute')) {
        return;
      }

      throw error;
    });

    return;
  }

  const decompressOpts = {
    plugins: [decompressTarGz()],
    filter: (file: any): boolean => !file.path.endsWith('/'),
  };

  if (PLATFORM === 'win32') {
    decompressOpts.plugins[0] = decompressUnzip();
  }

  const decompressResult = await withRetry({
    fn() {
      return decompress(browserPath, extractedFolderPath, decompressOpts);
    },
  }).catch(() => 'decompress error');

  if (decompressResult === 'decompress error') {
    await rm(browserPath, { recursive: true });
    const link = await getS3BrowserUrl(orbitaVersion);
    console.log(link);
    await downloadPersistentWithProgress({
      url: link,
      destFile: browserPath,
      progressEvent: EVENT,
      progressEventName: 'download-browser',
      progressState,
      isContinueStream: true,
      retryDelay: 5000,
      retryLimit: 100,
      orbitaVersion,
    });

    await withRetry({
      fn() {
        return decompress(browserPath, extractedFolderPath, decompressOpts);
      },
    });
  }
};

const getLatestBrowserVersion = async (orbitaVersion = ''): Promise<string> => {
  const platform = PLATFORM;
  let url = DEB_VERSION_FILE_URL;
  if (platform === 'win32') {
    url = WIN_VERSION_FILE_URL;
  } else if (platform === 'darwin') {
    url = MAC_VERSION_FILE_URL;
    const userOs = await getOS();
    if (userOs === 'macM1') {
      url = MAC_ARM_VERSION_FILE_URL;
    }
  }

  const replacedValue = orbitaVersion ? `-${orbitaVersion}` : '';
  url = url.replace('{{orbita-version}}', replacedValue);

  return withRetry<string>({
    fn() {
      return makeRequest<string>({ url, timeout: 10 * 1000, json: false })
        .then((response) => {
          if (!response) {
            return Promise.reject(new Error('Latest Version has not been provided'));
          }

          return response.replace(/(\r\n|\n|\r)/gm, '');
        });
    },
    retryDelay: 300,
  });
};

export const checkBrowser = (): Promise<{needDownloadNewBrowser: boolean}|void> => mkdir(BROWSER_PATH, { recursive: true }).catch(console.log)
  .then(() => getLatestBrowserVersion())
  .then((response) => {
    let latestBrowserVersion = response;
    let majorChromeVersion = getChromeMajorVersion(latestBrowserVersion);
    console.log('majorChromeVersion:', majorChromeVersion);

    const localBrowserReleases = new BrowserReleasesManager().localOrbitaReleasesList;
    console.log('localBrowserReleases:', localBrowserReleases);

    if (PLATFORM === 'win32') {
      const winVersion = getWinRelease();

      if (winVersion < 10 && Number(majorChromeVersion) >= 110 && localBrowserReleases['109']) {
        return { needDownloadNewBrowser: false };
      }

      if (winVersion < 10 && !localBrowserReleases['109']) {
        latestBrowserVersion = '109.1.2.9';
        majorChromeVersion = '109';
      }
    }

    const currentOrbitaPath = localBrowserReleases[majorChromeVersion];
    if (!localBrowserReleases[majorChromeVersion]) {
      return { needDownloadNewBrowser: true };
    }

    const baseOrbitaPath = path.join(BROWSER_PATH, currentOrbitaPath);
    let executableFilePath = path.join(baseOrbitaPath, 'chrome');
    if (PLATFORM === 'darwin') {
      executableFilePath = path.join(baseOrbitaPath, 'Orbita-Browser.app');
    } else if (PLATFORM === 'win32') {
      executableFilePath = path.join(baseOrbitaPath, 'chrome.exe');
    }

    console.log('executableFilePath:', executableFilePath);

    return withRetry({
      fn() {
        return access(executableFilePath);
      },
      limit: 3,
    })
      .then(() => true, () => false)
      .then((browserFolderExists) => {
        if (!browserFolderExists) {
          return removeFileOrFolder(path.join(baseOrbitaPath)).then(() => ({ needDownloadNewBrowser: true }));
        }

        return withRetry({
          fn() {
            return new BrowserReleasesManager().getLocalOrbitaVersionByPath(baseOrbitaPath);
          },
        })
          .then((currentVersion) => {
            console.log('latestBrowserVersion:', latestBrowserVersion);
            console.log('currentVersion:', currentVersion);
            console.log(
              'latestBrowserVersion === currentVersion:',
              latestBrowserVersion === currentVersion,
            );
            if (latestBrowserVersion === currentVersion) {
              return { needDownloadNewBrowser: false };
            }

            return { needDownloadNewBrowser: true, browserExist: true };
          });
      });
  })
  .catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    sendCommonErrorToSentry({
      message, transactionName: 'check-orbita',
      tags: [['scenario', 'download-orbita'], ['name', 'check-orbita']],
    });
  });

const deleteOldFiles = (orbitaVersion = '', finalTry = false): Promise<void> =>
  withRetry({
    fn() {
      return rm(path.join(BROWSER_PATH, EXTRACTED_FOLDER), { recursive: true, force: true })
        .then(() => readdir(BROWSER_PATH))
        .then((files: string[]) => {
          const promises: Promise<unknown>[] = [];
          files.forEach((filename: string) => {
            if (filename.match(/.*\.txt$/)) {
              promises.push(rm(path.join(BROWSER_PATH, filename), { recursive: true, force: true }));
            }
          });

          return Promise.all(promises);
        })
        .then(async () => {
          if (PLATFORM === 'darwin') {
            const replacedValue = orbitaVersion ? `-${orbitaVersion}` : '';
            const userOs = await getOS();
            if (userOs === 'macM1') {
              return rm(path.join(BROWSER_PATH, MAC_ARM_HASH_FILE).replace('{{orbita-version}}', replacedValue), { recursive: true, force: true });
            }

            return rm(path.join(BROWSER_PATH, MAC_HASH_FILE).replace('{{orbita-version}}', replacedValue), { recursive: true, force: true });
          }
        })
        .catch((error: unknown) => {
          const message = error instanceof Error ? error.message : error;

          if (typeof message === 'string' && message.includes('ENOENT')) {
            return;
          }

          if (typeof message === 'string' && message.includes('ENOTEMPTY')) {
            throw error;
          }

          sendCommonErrorToSentry({
            message, transactionName: 'delete-orbita-installation-files', level: 'warning',
            tags: [['scenario', 'download-orbita'], ['name', 'delete-orbita-installation-files']],
          });

          return Promise.resolve();
        });
    },
    retryDelay: 300,
  });

export const downloadBrowser = (event: any, args = { orbitaVersion: '' }): Promise<unknown> => {
  let { orbitaVersion } = args;
  EVENT = event;

  if (orbitaVersion === '100') {
    orbitaVersion = '10';
  }

  if (orbitaVersion === '103') {
    orbitaVersion = '13';
  }

  return downloadNewBrowser(orbitaVersion)
    .then(() => new BrowserReleasesManager().checkLocalOrbitaReleases())
    .then(() => deleteOldFiles(orbitaVersion))
    .catch((err: unknown) => {
      const message = err instanceof Error ? err.message : err;

      if (typeof message === 'string' && message.includes('ENOSPC')) {
        EVENT && EVENT.sender.send('download-browser', {
          progressState,
          result: true,
          isDiscSpaceError: true,
        });
      }

      progressState = { ...progressState, errorMessage: message };

      const errorMessage = typeof message === 'string' ? message : 'deleting old files error';

      return deleteOldFiles(orbitaVersion).finally(() => Promise.reject(new Error(errorMessage)));
    })
    .finally(() => {
      const objToReturn = {
        progressState: {
          ...progressState,
          inProgress: false,
        },
        result: !progressState.errorMessage,
        errorMessage: progressState.errorMessage || '',
      };

      progressState = { ...INIT_PROGRESS_STATE };
      EVENT && EVENT.sender.send('download-browser', objToReturn);
    });
};

const getS3BrowserUrl = async (resultOrbitaVersion: string): Promise<string> => {
  let link = DEB_BROWSER_LINK_S3;
  if (PLATFORM === 'win32') {
    link = WIN_BROWSER_LINK_S3;
  }

  if (PLATFORM === 'darwin') {
    link = MAC_BROWSER_LINK_S3;
    const userOs = await getOS();
    if (userOs === 'macM1') {
      link = MAC_ARM_BROWSER_LINK_S3;
    }
  }

  return link.replace('{{orbita-version}}', resultOrbitaVersion ? `-${resultOrbitaVersion}` : '');
};
