import { readFile } from 'fs/promises';

import { zeroProfileBookmarks } from '../../constants/zero-profile-bookmarks';
import { IBookmarkFoldersObj } from '../../interfaces/bookmarks-utils.interfaces';
import { sendErrorToSentry } from '../../utils/sentry';

export const checkBookmarks = async (profileId: string, bookmarks: any, pathToLocalBookmarks: string): Promise<IBookmarkFoldersObj> => {
  if (bookmarks.bookmark_bar) {
    return bookmarks;
  }

  const userBookmarksFromProfileFolder = await readFile(pathToLocalBookmarks, 'utf8')
    .catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      if (typeof message === 'string' && message.includes('ENOENT')) {
        return '';
      }

      sendErrorToSentry({
        message, transactionName: 'read-bookmarks-before-profile-start',
        tags: [['scenario', 'read-bookmarks'], ['profileId', profileId]],
      });

      return '';
    });

  if (!userBookmarksFromProfileFolder) {
    return zeroProfileBookmarks.roots as IBookmarkFoldersObj;
  }

  try {
    const bookmarksData = JSON.parse(userBookmarksFromProfileFolder);
    bookmarks = bookmarksData.roots;
  } catch (error: unknown) {
    bookmarks = zeroProfileBookmarks.roots;
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, transactionName: 'parse-bookmarks-before-profile-start',
      tags: [['scenario', 'parse-bookmarks'], ['profileId', profileId]],
    });
  }

  return bookmarks;
};
