import { IOptions } from '../interfaces/with-retry.interfaces';

const withRetry = <T>(optionsOrUndefined: IOptions): Promise<T> => {
  const opts = optionsOrUndefined || {};
  const {
    retryDelay = 0,
    limit: callLimit = 5,
    fn: fnToProducePromise,
    callCounter = 1,
    onRetry,
    stopRetryErrorPatterns = [404, 403, 429, 500, 401],
  } = opts;

  delete opts.callCounter;

  return fnToProducePromise(opts).catch((err: unknown) => {
    const message = err instanceof Error ? err.message : 'unknown';
    const isStopRetryError = stopRetryErrorPatterns.some((pattern: number) => message.includes(pattern.toString()));
    const skipRetry = callCounter >= callLimit || isStopRetryError || process.env.NODE_ENV === 'test';
    if (skipRetry) {
      return Promise.reject(err);
    }

    onRetry && onRetry();

    opts.callCounter = callCounter + 1;

    const resolver = retryDelay ?
      // eslint-disable-next-line no-unused-vars
      (resolve: (value: unknown) => void): Promise<unknown> => new Promise(() => setTimeout(resolve, retryDelay)) :
      // eslint-disable-next-line no-unused-vars
      (resolve: (value: unknown) => void): void => process.nextTick(resolve);

    return new Promise(resolve => resolver(resolve)).then(() => withRetry(opts));
  }) as Promise<T>;
};

export default withRetry;
