import bodyParser from 'body-parser';
import * as cors from 'cors';
import express from 'express';
import * as isPortReachable from 'is-port-reachable';

import { sendCommonErrorToSentry } from '../../electron/utils/sentry';
import Router from './routes';

const app = express();

const corsOptions = {
  origin: /.*/,
  optionsSuccessStatus: 200,
};

app.use(cors(corsOptions));
app.use(bodyParser.json({ limit: '10mb' }));
app.use(Router);

export async function init(global: any): Promise<void> {
  let port = 36912;
  let isPortBusy = await isPortReachable(port);
  while (isPortBusy) {
    isPortBusy = await isPortReachable(++port);
  }

  const server = app.listen(port, () => {
    console.log(`Local server started on port ${port}`);
  });

  global.appPort = port;
  server.on('error', (error) => {
    if (error.message.includes('EADDRINUSE')) {
      init(global);

      return;
    }

    sendCommonErrorToSentry({
      message: error.message, transactionName: 'local-server-init-error',
      tags: [['name', 'local-server-init-error']],
    });
  });
}
