export const tempArchiveName = 'prepared_to_upload';

export const closeAndUploadProfile = async (props: ICloseAndUpdateProfileProps): Promise<void> => {
  const {
    profileId,
    currentMs,
    localStorageEnabled,
    indexedDbEnabled,
    debugMode = false,
    channel,
    os,
    ProfileStatusManagerInst,
    ProfileStatusManagerInstances,
    automation,
    apiTokenForClose,
    twoFaKeyForClose,
    vaultToken,
  } = props;

  const stopProfileTransactionSentry = Sentry.startTransaction({ name: 'stop-profile' }) || sentryTransactionDefaultObject;
  syncingProfiles.push(profileId);

  await cleanDirs({
    profileId,
    currentMs,
    debugMode,
    localStorageEnabled,
    indexedDbEnabled,
    stopProfileTransactionSentry,
  });
  await Promise.all([
    removeLocalStorage({
      profileId,
      localStorageEnabled,
      indexedDbEnabled,
      stopProfileTransactionSentry,
    }),
    ProfileStarterManagerInst.removeUselessFolders(profileId, os),
  ]).catch(() => null);

  const { s3SignedUrl = { url: '' }, profileArchiveBuffer } = await updateProfileAndGetUploadUrl({
    profileId,
    channel,
    ProfileStatusManagerInst,
    automation,
    apiTokenForClose,
    twoFaKeyForClose,
    vaultToken,
  }).catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;

    stopProfileTransactionSentry.setStatus('aborted');
    stopProfileTransactionSentry.finish();
    removeItemFromArray(syncingProfiles, profileId);

    return { s3SignedUrl: { url: message } } as IUpdateProfileDataObject;
  });

  if (s3SignedUrl.url === 'error') {
    throw new Error('The profile has been deleted');
  }

  if (s3SignedUrl.url === 'error-429') {
    throw new Error('Too many requests');
  }

  if (!profileArchiveBuffer) {
    console.log('error, no buffer');
    removeItemFromArray(syncingProfiles, profileId);
    stopProfileTransactionSentry.setStatus('aborted');
    stopProfileTransactionSentry.finish();

    throw new Error('profile archive is not found');
  }

  await uploadingProfileProcess({
    profileId,
    s3SignedUrl,
    profileArchiveBuffer,
    ProfileStatusManagerInst,
    ProfileStatusManagerInstances,
    stopProfileTransactionSentry,
    apiTokenForClose,
    twoFaKeyForClose,
  });
};

// eslint-disable-next-line id-length
const getProfileDataForUploadAndUpdate = async (props: IUploadAndUpdateProfileInDBProps): Promise<IUploadAndUpdateProfileResult> => {
  const {
    profileId,
    channel,
    pathToExtensions,
    pathToPreferences,
    pathToBookmarks,
    ProfileStatusManagerInst,
    stopProfileTransactionSentry,
    automation,
    apiTokenForClose,
    twoFaKeyForClose,
    vaultToken,
  } = props;

  const readingCookiesSentrySpan = stopProfileTransactionSentry.startChild({ op: 'upload-cookies' });
  const uploadPasswordsSentrySpan = stopProfileTransactionSentry.startChild({ op: 'upload-passwords' });
  const extensionsUploadSentrySpan = stopProfileTransactionSentry.startChild({ op: 'updating-extensions' });

  const arrayPromises = [
    loadCookiesPromise(profileId, readingCookiesSentrySpan).catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      sendErrorToSentry({
        message, transactionName: 'get-cookies-file-error',
        tags: [['scenario', 'close-profile'], ['profileId', profileId]],
      });

      readingCookiesSentrySpan.setStatus('aborted');
      readingCookiesSentrySpan.finish();

      const errorCookiesArr = [{ domain: 'error' }];

      return { cookies: errorCookiesArr };
    }),
    vaultToken ? loadPasswordsPromise(profileId, uploadPasswordsSentrySpan) : Promise.resolve([{ domain: 'error' }]),
    getProfileExtPromise(pathToPreferences, extensionsUploadSentrySpan, profileId),
    getProfileBookmarksPromise(pathToBookmarks, profileId),
  ];

  const profileDataResult = await Promise.allSettled(arrayPromises);
  const parsedDataResult = profileDataResult.map((result) => {
    if (result.status !== 'fulfilled') {
      console.log(result.reason);

      return {
        value: '',
      };
    }

    return result;
  });

  const [
    { value: cookies = { cookies: [] } },
    { value: passwords },
    { value: extensionsIds },
    { value: bookmarks },
  ] = parsedDataResult;

  let extensionsIdsFiltered = extensionsIds;
  const shouldRemoveAutomationExtension = automation?.workflowId && !automation?.isAutomationExtensionSavedInDb;
  if (shouldRemoveAutomationExtension) {
    extensionsIdsFiltered = extensionsIdsFiltered.filter((id: string) => id !== AUTOMATION_EXTENSION_WEBSTORE_ID);
  }

  const zippingSentrySpan = stopProfileTransactionSentry.startChild({ op: 'zipping-profile' });
  const buffer = await getProfileArchiveBufferPromise(profileId, zippingSentrySpan).catch(console.log);

  if (!buffer) {
    stopProfileTransactionSentry.finish();
    removeItemFromArray(syncingProfiles, profileId);

    makeRequest({
      method: 'POST',
      url: `${CommonDataManagerInst.apiBaseUrl}/analytics/add_action_analytics`,
      body: {
        action: 'synced unsuccessfully',
      },
    }).catch(() => null);

    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage: {
        errorType: 'archiving',
        errorMessage: 'notifications.error.archivingProfileError',
        prefix: 'resync',
      },
    });
    ProfileStatusManagerInst.removeStatus({ status: 'archiving' });

    throw Error('archiving profile error');
  }

  console.log('upload cookies.length:', cookies.cookies.length);
  if (!cookies.cookies.length) {
    console.log(`Got empty cookies. profile ${profileId}`);
  }

  try {
    channel.sender.send('update-extensions-on-close', {
      extensionsIds: extensionsIdsFiltered,
      profileId,
    });
  } catch (error) {
    console.log(error);
  }

  const cachingSentrySpan = stopProfileTransactionSentry.startChild({ op: 'caching-profile-before-upload' });
  await ProfileCachingManagerInst.copyClosedProfileToCacheDirectory(buffer, profileId, tempArchiveName)
    .catch((error) => {
      const transactionName = 'cache-profile-after-close';

      sendCommonErrorToSentry({
        message: error.message, transactionName,
        tags: [['scenario', 'close-profile'], ['profileId', profileId], ['name', transactionName]],
      });
    })
    .finally(() => cachingSentrySpan.finish());

  const fileSize = Buffer.byteLength(buffer);
  const s3 = await uploadDataAndGetS3Link({
    profileId,
    passwords,
    cookies,
    extensionsIds: extensionsIdsFiltered,
    bookmarks,
    fileSize,
    pathToExtensions,
    stopProfileTransactionSentry,
    apiTokenForClose,
    twoFaKeyForClose,
  });

  return {
    s3,
    buffer,
  };
};

export const updateProfileAndGetUploadUrl = async (props: IUpdateProfileAndGetUIploadURLProps): Promise<IUpdateProfileDataObject> => {
  const {
    profileId,
    channel,
    onlyUpload = false,
    ProfileStatusManagerInst,
    automation,
    vaultToken,
  } = props;

  const stopProfileTransactionSentry = Sentry.startTransaction({ name: 'stop-profile' }) || sentryTransactionDefaultObject;
  stopProfileTransactionSentry.tags = { ...stopProfileTransactionSentry.tags, profileId };

  const pathToPreferences = `${BASE_PATH_TEMPLATE.replace('{profileId}', profileId)}Preferences`;
  const pathToExtensions = `${BASE_PATH_TEMPLATE.replace('{profileId}', profileId)}Extensions`;

  const pathToBookmarks = `${BASE_PATH_TEMPLATE.replace('{profileId}', profileId)}Bookmarks`;

  const s3SignedUrl = { url: '', isCf: false };
  let profileArchiveBuffer: Buffer | null = null;

  if (onlyUpload) {
    profileArchiveBuffer = await getBufferOfArchive(profileId, tempArchiveName)
      .catch((error: any) => {
        console.log(error);

        return null;
      });
  } else {
    await getProfileDataForUploadAndUpdate({
      profileId,
      channel,
      pathToPreferences,
      pathToExtensions,
      pathToBookmarks,
      ProfileStatusManagerInst,
      stopProfileTransactionSentry,
      automation,
      vaultToken,
    })
      .then((data) => {
        const { s3 = { url: '', isCf: false }, buffer } = data;
        s3SignedUrl.url = s3.url;
        s3SignedUrl.isCf = s3.isCf || false;
        profileArchiveBuffer = buffer;
      });
  }

  return {
    s3SignedUrl,
    profileArchiveBuffer,
    stopProfileTransactionSentry,
  };
};

