export const uploadingProfileProcess = async (props: IUploadProfileProcessProps): Promise<void> => {
  const {
    profileId,
    s3SignedUrl,
    profileArchiveBuffer,
    ProfileStatusManagerInst,
    ProfileStatusManagerInstances,
    stopProfileTransactionSentry = sentryTransactionDefaultObject,
    apiTokenForClose,
    twoFaKeyForClose,
  } = props;

  console.log('Sending analytics before upload to s3');
  const { userOs } = CommonDataManagerInst;
  const timeBeforeUpload = moment().toDate().valueOf();
  const deduplicationKey = uuidv4();

  console.log('Uploading zip file');
  ProfileStatusManagerInst.addStatus({ status: 'cloud_uploading' });
  ProfileStatusManagerInst.removeStatus({ status: 'archiving' });

  const profileUid = await uploadProfile({
    fileBuff: profileArchiveBuffer,
    profileId,
    preloadedS3SignedUrl: s3SignedUrl,
    stopProfileTransactionSentry,
    apiTokenForClose,
    twoFaKeyForClose,
  }).catch((error: unknown) => {
    removeItemFromArray(syncingProfiles, profileId);
    let statusMessage = 'notifications.error.uploadingProfileError';
    let sendToSentry = true;
    const message = error instanceof Error ? error.message : error;
    const errorIsString = typeof message === 'string';
    if (errorIsString && message === 'profile deleted') {
      statusMessage = 'notifications.error.profileAlreadyDeleted';
      sendToSentry = false;
    }

    if (errorIsString && message === 'do not send to sentry') {
      sendToSentry = false;
    }

    if (errorIsString && message === 'no url') {
      sendToSentry = false;
    }

    if (errorIsString && message === 'Too many requests') {
      statusMessage = 'profiles.profileStatusMessages.errorTooManyRequests';
    }

    sendToSentry && sendCommonErrorToSentry({
      message, transactionName: 'upload-profile-global',
      tags: [['scenario', 'close-profile'], ['profileId', profileId], ['name', 'upload-profile-global']],
    });

    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage,
    });
  });

  if (!profileUid) {
    removeItemFromArray(syncingProfiles, profileId);
    ProfileStatusManagerInst.addStatus({
      status: 'error',
      statusMessage: 'notifications.error.uploadingProfileError',
    });
    ProfileStatusManagerInst.removeStatus({ status: 'cloud_uploading' });

    return;
  }

  await ProfileCachingManagerInst.renameCachedProfile(profileId, tempArchiveName, profileUid)
    .catch((renameError) => {
      console.log(renameError);
      const cachingSentrySpan = stopProfileTransactionSentry.startChild({ op: 'caching-profile' });

      return ProfileCachingManagerInst.copyClosedProfileToCacheDirectory(profileArchiveBuffer, profileId, profileUid)
        .catch((error: unknown) => {
          const message = error instanceof Error ? error.message : error;
          let transactionName = 'cache-profile-after-close';
          if (typeof message === 'string' && message.includes('ENOSPC')) {
            transactionName = 'no-disc-space';
          }

          sendErrorToSentry({
            message, transactionName,
            tags: [['scenario', 'close-profile'], ['profileId', profileId]],
          });
        }).finally(() => cachingSentrySpan.finish());
    });

  console.log('Sending analytics after upload to s3');
  const uploadDTime = moment().toDate().valueOf() - timeBeforeUpload;
  const body = {
    type: 'profile-s3-upload',
    status: 'success',
    profile: profileId,
    time: uploadDTime,
    os: userOs,
    deduplicationKey,
  };

  makeRequest({ url: `${CommonDataManagerInst.apiBaseUrl}/analytics`, method: 'POST', body }).catch(() => null);

  const removingFilesSentrySpan = stopProfileTransactionSentry.startChild({ op: 'remove-files' });
  removeItemFromArray(syncingProfiles, profileId);
  access(pathOs.PROFILES_DIR + profileId)
    .then(() => rm(pathOs.PROFILES_DIR + profileId, { recursive: true }))
    .catch(() => {
      removingFilesSentrySpan.setStatus('aborted');
    });
  removingFilesSentrySpan.finish();
  stopProfileTransactionSentry.finish();

  ProfileStatusManagerInst.removeStatus({ status: 'cloud_uploading' });
  delete ProfileStatusManagerInstances[profileId];
};

export const removeLocalStorage = (props: IRemoveLocalStorageProps): Promise<void[]> => {
  const {
    profileId,
    localStorageEnabled,
    indexedDbEnabled,
    stopProfileTransactionSentry,
  } = props;

  const removeDirs = {
    localStorage: `${pathOs.ROUTE_SLASH}Default${pathOs.ROUTE_SLASH}Local Storage`,
    indexedDb: `${pathOs.ROUTE_SLASH}Default${pathOs.ROUTE_SLASH}IndexedDB`,
  };

  const promises: Promise<void>[] = [];
  if (!localStorageEnabled) {
    stopProfileTransactionSentry && stopProfileTransactionSentry.setTag('local-storage-disabled', true);
    promises.push(removeFileOrFolder(pathOs.PROFILES_DIR + profileId + removeDirs.localStorage));
  }

  if (!indexedDbEnabled) {
    stopProfileTransactionSentry && stopProfileTransactionSentry.setTag('indexed-db-disabled', true);
    promises.push(removeFileOrFolder(pathOs.PROFILES_DIR + profileId + removeDirs.indexedDb));
  }

  return Promise.all(promises);
};

const cleanDirs = async (props: ICleanDirsProps): Promise<void> => {
  const {
    profileId,
    currentMs,
    localStorageEnabled,
    indexedDbEnabled,
    stopProfileTransactionSentry,
  } = props;

  const waitingSentrySpan = stopProfileTransactionSentry.startChild({ op: 'waiting-orbita-finish' });
  delete pidList[profileId];
  //   -      
  //      -  :
  //      ,   
  //     ,   ,     
  await new Promise(resolve => setTimeout(resolve, 1000));
  waitingSentrySpan.finish();
  const removingFoldersSentrySpan = stopProfileTransactionSentry.startChild({ op: 'removing-folders' });

  const promises: Promise<void | void[]>[] = [];
  promises.push(removeLocalStorage({ profileId, localStorageEnabled, indexedDbEnabled }));

  //        
  promises.push(rm(`${pathOs.PROFILES_DIR}${profileId}_${currentMs}.zip`).catch(() => void 0));

  promises.push(rm(`${pathOs.PROFILES_DIR}${profileId}_${currentMs}_sync.zip`).catch(() => void 0));

  await Promise.allSettled(promises);

  removingFoldersSentrySpan.finish();
};

const insertExtensionsToDb = async (extensionsToAdd: string[], pathToExtensions = CHROME_EXTENSIONS_PATH): Promise<unknown> => {
  if (!extensionsToAdd.length) {
    return;
  }

  const extensionsToUpdate = await ExtensionsManagerInst.getExtensionsNameAndImage(extensionsToAdd, pathToExtensions);

  return makeRequest({
    url: `${CommonDataManagerInst.apiBaseUrl}/extensions/create`, method: 'POST', body: { extensionsInfo: extensionsToUpdate },
  });
};

const getProfileArchiveBufferPromise = (profileId: string, sentrySpan: ITransactionSpanObject): Promise<Buffer | void | null> =>
  archiveProfile(pathOs.PROFILES_DIR + profileId, profileId)
    .catch((error: Error) => {
      console.log('archiving error', error);
      const cachedProfilePath = path.join(CACHED_PROFILES_FOLDER, profileId);
      rm(cachedProfilePath, { recursive: true, force: true }).catch(console.log);
      const message = error instanceof Error ? error.message : error;
      const isProfileDeleted = deletedProfiles.includes(profileId);

      if ((typeof message === 'string' && message.includes('Invalid profile folder path')) || isProfileDeleted) {
        return null;
      }

      sentrySpan.setStatus('aborted');
      sendCommonErrorToSentry({
        message, transactionName: 'archive-profile-after-close',
        tags: [['scenario', 'close-profile'], ['profileId', profileId]],
      });
    })
    .finally(() => sentrySpan.finish());

const loadCookiesPromise = async (profileId: string, sentrySpan: ITransactionSpanObject): Promise<any> => {
  const cookiesFilePath = await CookiesManager.getCookiesFileToRead(profileId);

  if (!cookiesFilePath) {
    return;
  }

  return loadCookiesFromFile(cookiesFilePath)
    .catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      if (deletedProfiles.includes(profileId)) {
        return;
      }

      sendErrorToSentry({
        message, transactionName: 'load-cookies-from-file',
        tags: [['scenario', 'close-profile'], ['profileId', profileId]],
      });

      sentrySpan.setStatus('aborted');

      const errorCookiesArr = [{ domain: 'error' }];

      return { cookies: errorCookiesArr };
    })
    .finally(() => sentrySpan.finish());
};

const loadPasswordsPromise = async (profileId: string, sentrySpan: ITransactionSpanObject): Promise<TLoadedPasswords> => {
  const passwordsFilePath = pathOs.PROFILES_DIR + profileId + pathOs.PASSWORDS_FILE;

  const { passwords } = await withRetry<{ passwords: any[] }>({
    fn() {
      return loadPasswordsFromFile(passwordsFilePath);
    },
    retryDelay: 300,
  })
    .catch(async (error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      const errorPassObj = { origin_url: 'error', username_value: 'error', password_value: 'error' };

      const isFileDeleted = deletedProfiles.includes(profileId);

      if (!isFileDeleted) {
        return { passwords: [errorPassObj] };
      }

      sendErrorToSentry({
        message, transactionName: 'load-passwords-from-file',
        tags: [['scenario', 'close-profile'], ['profileId', profileId]],
      });

      sentrySpan.setStatus('aborted');

      return { passwords: [errorPassObj] };
    })
    .finally(() => sentrySpan.finish());

  return passwords.map(el => ({
    domain: el.origin_url,
    username: el.username_value,
    password: el.password_value,
  })).filter(el => el.username && el.password && el.domain);
};

const getProfileExtPromise = async (pathToPreferences: string, sentrySpan: ITransactionSpanObject, profileId: string): Promise<string[]> =>
  BrowserUserDataManagerInst.getProfileChromeExtensions(pathToPreferences)
    .catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      if (deletedProfiles.includes(profileId)) {
        return [];
      }

      if (typeof message === 'string' && message.includes('ENOENT')) {
        return [];
      }

      sendErrorToSentry({
        message, transactionName: 'read-chrome-extensions-from-pref-after-close',
        tags: [['scenario', 'close-profile']],
      });

      sentrySpan.setStatus('aborted');

      return [];
    })
    .finally(() => sentrySpan.finish());

const getProfileBookmarksPromise = async (pathToBookmarks: string, profileId: string): Promise<IBookmarkFoldersObj> => {
  const profileBookmarks = await readFile(pathToBookmarks, 'utf-8')
    .catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      if (typeof message === 'string' && message.includes('ENOENT')) {
        return '';
      }

      sendErrorToSentry({
        message, transactionName: 'read-bookmarks-after-profile-close',
        tags: [['scenario', 'read-bookmarks'], ['profileId', profileId]],
      });

      return '';
    });

  let bookmarks = { roots: {} };

  if (!profileBookmarks) {
    return bookmarks.roots;
  }

  try {
    bookmarks = JSON.parse(profileBookmarks);
  } catch (error: unknown) {
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, transactionName: 'parse-bookmarks-after-profile-close',
      tags: [['scenario', 'parse-bookmarks'], ['profileId', profileId]],
    });
  }

  return bookmarks.roots;
};

const uploadDataAndGetS3Link = async (props: IUploadDataAndGetS3LinkProps): Promise<ISignedS3UrlObj> => {
  const {
    profileId,
    passwords,
    cookies,
    extensionsIds,
    bookmarks,
    fileSize,
    pathToExtensions,
    stopProfileTransactionSentry,
  } = props;

  const uploadDataSentrySpan = stopProfileTransactionSentry.startChild({ op: 'upload-profile-data' });
  if (deletedProfiles.includes(profileId)) {

    return { url: 'error', isCf: false };
  }

  const uploadProfileDataResponse = await BrowserUserDataManagerInst.uploadProfileData({
    browserId: profileId,
    passwords,
    cookies: cookies.cookies,
    extensionsIds,
    bookmarks,
    fileSize,
    cf: false,
    hk: false,
  }).catch((error: any) => {
    if (error.response?.body?.includes('Profile has been deleted')) {
      return { s3: { url: 'error', isCf: false }, extensionsToAdd: [] };
    }

    if (error.message.includes('429') || error.message.includes('Too many requests')) {
      return { s3: { url: 'error-429', isCf: false }, extensionsToAdd: [] };
    }

    sendCommonErrorToSentry({
      message: error.message, transactionName: 'update-profile-and-get-s3-url-after-close',
      tags: [['name', 'update-profile-and-get-s3-url-after-close'], ['profileId', profileId]],
    });
    uploadDataSentrySpan.setStatus('aborted');
    uploadDataSentrySpan.finish();
    removeItemFromArray(syncingProfiles, profileId);

    return { url: '', isCf: false };
  });

  uploadDataSentrySpan.finish();
  const { s3 = { url: '', isCf: false }, extensionsToAdd: missingExtIds = [] } = uploadProfileDataResponse;

  if (s3.url?.includes('error')) {
    return s3;
  }

  insertExtensionsToDb(missingExtIds, pathToExtensions).catch((error: unknown) => {
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, transactionName: 'insert-extensions-to-db-error',
      tags: [['scenario', 'close-profile'], ['profileId', profileId]],
    });
    console.log(error);
  });

  return s3;
};
