import { BrowserWindow } from 'electron';

import { BASE_URL } from './constants';
import { OpenProfileViaLink } from '../../../common/constants/analytics';
import { PROFILE_RUN_DEEP_LINK_EVENT_NAME } from '../../../common/constants/events';
import { PROFILE_LIST_PAGE } from '../../../common/constants/routes';
import { sendActionAnalytics } from '../send-action-analytics';
import { sendErrorToSentry } from '../sentry';

export const handleProfileRun = async (window: BrowserWindow, responseUrl = ''): Promise<void> => {
  const { href, searchParams, search } = new URL(responseUrl);
  const deepLinkProtocol = searchParams.get('protocol') || 'custom';
  const analyticsDataBase = { mode: deepLinkProtocol };
  sendActionAnalytics(OpenProfileViaLink.startedHandling, analyticsDataBase);
  if (deepLinkProtocol !== 'http') {
    sendActionAnalytics(OpenProfileViaLink.visited, analyticsDataBase);
  }

  const [_protocolName, argument1, argument2] = href.split('/').filter(Boolean);
  const profileIdOrName = argument1 === 'open' && argument2 ? argument2.replace(search, '') : argument1;
  if (!profileIdOrName) {
    return sendErrorToSentry({
      message: 'profile id or name is falsy', transactionName: 'profile-id-or-name-is-falsy',
      tags: [['scenario', 'open-profiles-via-link']],
    });
  }

  const urlCurrent = window.webContents.getURL();
  if (urlCurrent.includes(PROFILE_LIST_PAGE)) {
    sendActionAnalytics(OpenProfileViaLink.startedRedirecting, { ...analyticsDataBase, actionInfo: PROFILE_LIST_PAGE });

    return window.webContents.send(PROFILE_RUN_DEEP_LINK_EVENT_NAME, { profileIdOrName, deepLinkProtocol });
  }

  sendActionAnalytics(OpenProfileViaLink.startedRedirecting, analyticsDataBase);
  const urlToLoad = `${BASE_URL}#${PROFILE_LIST_PAGE}?profile-id-or-name=${profileIdOrName}&protocol=${deepLinkProtocol}`;

  return window.loadURL(urlToLoad.toString());
};
