import { createWriteStream, promises } from 'fs';
import path from 'path';

import got from 'got';

import withRetry from './with-retry';
import {
  IDownloadPersistentWithProgress,
  IDownloadWithRetry,
  IDownloadWithRetryBuffer,
} from '../interfaces/download-with-retry.interfaces';
import { CommonDataManager } from '../managers/common-data-manager';

const { rm, readdir } = promises;
const CommonDataManagerInst = new CommonDataManager();

export const downloadWithRetry = (props: IDownloadWithRetry): Promise<void> => withRetry({
  fn() {
    return new Promise((resolve, reject) => {
      const options: { timeout: { request: number }; headers?: Record<string, string> } = {
        timeout: {
          request: 350 * 1000,
        },
      };

      if (props.profileId) {
        options.headers = {
          Authorization: `Bearer ${CommonDataManagerInst.accessToken}`,
          'x-two-factor-token': CommonDataManagerInst.twoFaKey || '',
          browserId: props.profileId,
        };
      }

      const req = got.stream(props.url, options);

      const localStream = createWriteStream(props.destFile, { mode: 0o666 });

      localStream.on('error', (error) => {
        localStream.destroy();

        reject(error);
      });

      req.on('response', (response) => {
        if (response.statusCode.toString().startsWith('2')) {
          req.pipe(localStream);
          localStream.on('close', () => {
            resolve('success');
          });

          return;
        }

        localStream.destroy();
        reject(response);
      });

      req.on('error', (error) => {
        localStream.destroy();

        reject(error);
      });
    });
  },
  retryDelay: 300,
});

export const downloadWithRetryBuffer = (props: IDownloadWithRetryBuffer): Promise<Buffer> => withRetry<Buffer>({
  fn() {
    return new Promise((resolve, reject) => {
      const chunks: any[] = [];
      const req = got.stream(props.url, {
        timeout: {
          request: 30 * 1000,
        },
      });

      req.on('response', (response) => {
        if (response.statusCode.toString().startsWith('2')) {
          req.on('data', (data) => chunks.push(data));
          req.on('end', () => resolve(Buffer.concat(chunks)));

          return;
        }

        reject(response);
      });

      req.on('error', (error) => {
        reject(error);
      });
    });
  },
});

export const downloadPersistentWithProgress = (props: IDownloadPersistentWithProgress): Promise<unknown> => {
  const {
    url,
    destFile,
    progressEvent = null,
    progressEventName = '',
    retryDelay = 0,
    retryLimit = 5,
    orbitaVersion = '',
  } = props;

  let { progressState = {} } = props;

  return withRetry({
    fn() {
      // return stat(destFile)   R2. ,     
      //   .then((res: any) => res.size, () => 0)
      return rm(destFile)
        .then(() => 0, () => 0)
        .then((size: number) => new Promise((resolve, reject) => {
          const endRange = Number.MAX_SAFE_INTEGER;
          const headers = {
            // Range: `bytes=${size}-${endRange}`,   R2. ,     
          };

          console.log(`bytes=${size}-${endRange}`);
          const req = got.stream(url, {
            headers,
          });

          req.on('error', (error) => {
            console.log('request error', error);
            localStream.end();
            reject(error);
          });

          let streamOptions = {};

          if (size) {
            streamOptions = {
              start: size,
              mode: 0o666,
              flags: 'r+',
            };
          }

          const localStream = createWriteStream(destFile, streamOptions);
          localStream.on('error', (error: unknown) => {
            console.log('stream error', error);
            try {
              readdir(path.dirname(destFile)).then((dirContent) => {
                console.log('dir content', dirContent);
              }).catch(console.log);
            } catch (err) {
              console.log('dir content error', err);
            }

            const message = error instanceof Error ? error.message : error;
            if (typeof message === 'string' && message.includes('ENOSPC')) {
              localStream.end();
              reject(message);

              return;
            }

            localStream.end();
            reject(error);
          });

          req.on('response', (response) => {
            if (response.statusCode.toString().startsWith('2')) {
              console.log('received success code');
              req.pipe(localStream);
              localStream.on('close', () => {
                console.log('stream closed');
                resolve('success');
              });

              return;
            }

            localStream.end();
            reject(response);
          });

          req.on('downloadProgress', (progress) => {
            if (!progressEvent) {
              return;
            }

            progress.total = size + progress.total;
            progress.transferred = size + progress.transferred;
            progress.percent = progress.transferred / progress.total;

            // to send only percent change
            const isPercentDifferent = progressState?.percent?.toFixed(2) !== progress.percent.toFixed(2);

            progressState = { ...progressState, ...progress };
            isPercentDifferent && progressEvent.sender.send(progressEventName, {
              progressState,
              result: false,
              orbitaVersion,
            });
          });
        }));
    },
    retryDelay,
    onRetry: () => {
      progressEvent.sender.send(progressEventName, {
        progressState,
        result: false,
        isRetrying: true,
      });
    },
    callLimit: retryLimit,
  });
};
