import { readFile } from 'fs/promises';
import path from 'path';

import {
  BROWSER_FOLDER_NAME,
  PLATFORM as osPlatform,
  BROWSER_PATH,
} from '../../../electron/utils/path-platforms';
import { VERSION_FILE } from '../../constants/constants';

const DEFAULT_VERSION = '0.0.0';

export const getChromeMajorVersion = (orbitaVersion = ''): string => {
  if (!orbitaVersion.split) {
    return '0';
  }

  const [majorChromeVersion] = orbitaVersion.split('.');

  return majorChromeVersion;
};

export const getOldLocalOrbitaVersion = (): Promise<string> => {
  let versionFilePath = path.join(BROWSER_PATH, BROWSER_FOLDER_NAME, 'version');
  if (osPlatform === 'darwin') {
    versionFilePath = path.join(BROWSER_PATH, 'version', VERSION_FILE);
  }

  return readFile(versionFilePath, { encoding: 'utf-8' }).catch(() => DEFAULT_VERSION);
};

export const getOrbitaDirNameByVersion = (majorVersion = ''): string => {
  const chromiumVersion = majorVersion ? majorVersion : '0';

  return [BROWSER_FOLDER_NAME, chromiumVersion].join('-');
};
