import crypto from 'crypto';

// without `javascript:;` the ERR_BLOCKED_BY_CLIENT error is thrown
interface AutomationStartupUrlData {
  extensionId: string;
  workflowId: string;
}

export const getAutomationStartupUrl = ({ extensionId = '', workflowId = '' }: AutomationStartupUrlData): string =>
  `javascript:;window.open('chrome-extension://${extensionId}/newtab.html#/workflows/${workflowId}/run', '_self')`;

export const generatePublicKey = (): string => {
  const keyPair = crypto.generateKeyPairSync('rsa', {
    modulusLength: 2048,
    publicKeyEncoding: {
      type: 'spki',
      format: 'pem',
    },
    privateKeyEncoding: {
      type: 'pkcs8',
      format: 'pem',
    },
  });

  const publicKey = keyPair.publicKey
    .concat()
    .split('\n')
    .join('')
    .replace('-----BEGIN PUBLIC KEY-----', '')
    .replace('-----END PUBLIC KEY-----', '');

  return publicKey;
};

export const convertPublicKeyToExtensionId = (publicKey: string): string => {
  const publicKeyBuffer = Buffer.from(publicKey, 'base64');
  const hash = crypto.createHash('sha256').update(publicKeyBuffer).digest();

  let characters = [...hash.slice(0, 16).toString('hex').toLowerCase()];
  characters = characters.map((char) => isNaN(+char) ?
    String.fromCharCode(char.charCodeAt(0) + 10) : 'abcdefghijklmnop'[parseInt(char, 16)]);

  return characters.join('');
};
