import { IProfile } from '../../../../app/interfaces';
import { API_BASE_URL } from '../../../../common/constants/constants';
import { makeRequest } from '../../make-request';

type ShareLinkErrorMessage = 'tableProfiles.notification.cannotShareProfilesToYourself'|
  'tableProfiles.notification.linkNotFound'|
  'tableProfiles.notification.linkIsPaused';

export const shareProfilesViaLink = async (profileName: string, linkId: string): Promise<IProfile|ShareLinkErrorMessage|null> =>
  makeRequest<IProfile|ShareLinkErrorMessage|null>({
    url: `${API_BASE_URL}/share-links/profiles/share`,
    method: 'POST',
    body: { profileName, linkId },
  }).then((profile) => profile).catch((error: unknown) => {
    const errorMessage = error instanceof Error ? error.message : 'unknown';
    switch (true) {
      case errorMessage.includes('400'):
        return 'tableProfiles.notification.cannotShareProfilesToYourself';
      case errorMessage.includes('404'):
        return 'tableProfiles.notification.linkNotFound';
      case errorMessage.includes('405'):
        return 'tableProfiles.notification.linkIsPaused';
      default:
        return null;
    }
  });
