import { writeFile } from 'fs/promises';
import path from 'path';

import { screen } from 'electron';

import { getIconsConstants } from '../../../app/features/browser-system-icons/constants';
import { ISaveBrowserIconsInProfileDirectoryData } from '../../interfaces/save-browser-icons-in-profile-directory-data.interface';

export const saveBrowserIconsInProfileDirectory = async ({
  iconsPngs = [],
  profilePath = '',
  os,
  iconDesignVersion = null,
}: ISaveBrowserIconsInProfileDirectoryData, span?: { finish: () => void }): Promise<void> => {
  let { browserIconsSizes } = getIconsConstants(os);
  const { scaleFactor = 1 } = screen.getPrimaryDisplay();
  if (['win10', 'win11', 'mac'].includes(os)) {
    browserIconsSizes = browserIconsSizes.map(size => size * scaleFactor);
    if (os === 'win10') {
      const [iconBigSize] = browserIconsSizes;
      const iconSmallSize = iconBigSize / 2;
      browserIconsSizes = [iconBigSize, iconSmallSize]
    }
  }

  const iconPngsPromises = iconsPngs.reduce<Promise<void>[]>((acc, iconPng, index) => {
    let iconName = 'avatar.png';
    const isNotAvatar = iconDesignVersion === '2:green-square';
    if (isNotAvatar) {
      const iconSize = `${browserIconsSizes[index]}`;
      iconName = `${iconSize}.png`;
    }

    const iconPath = path.join(profilePath, iconName);
    const iconBase64Ready = iconPng.replace(/^data:image\/png;base64,/, '');
    acc.push(writeFile(iconPath, iconBase64Ready, 'base64'));

    return acc;
  }, []);

  await Promise.all(iconPngsPromises);
  span?.finish();
};
