import { ISetProxyStatusParams } from '../../../app/state/proxies.context/interfaces/ISetProxyStatusParams';
import { makeRequest } from '../../../electron/utils/make-request';
import { sendErrorToSentry } from '../../../electron/utils/sentry';
import { IProfileFingerprint } from '../../interfaces/profile-starter.manager.interfaces';
import withRetry from '../../utils/with-retry';
import { CommonDataManager } from '../common-data-manager';

const CommonDataManagerInst = new CommonDataManager();

export const getProfileParamsToken = (apiUrl: string): Promise<{ token?: string }> =>
  makeRequest<{ token?: string }>({ url: `${apiUrl}/browser/features/profile-params-token`, method: 'GET', timeout: 30 * 1000, retryLimit: 5 })
    .catch((error: unknown) => {
      const message = error instanceof Error ? error.message : error;
      sendErrorToSentry({
        message, transactionName: 'get-profile-params-token-error', level: 'warning',
        tags: [['scenario', 'run-profile']],
      });

      return {};
    });

export const requestProfileFingerprint = (profileId: string): Promise<IProfileFingerprint> => withRetry({
  fn() {
    return makeRequest<IProfileFingerprint>({ url: `${CommonDataManagerInst.apiBaseUrl}/browser/features/${profileId}/info-for-run` });
  },
  retryDelay: 300,
});

export const setProxyStatuses = async (proxies: ISetProxyStatusParams[]): Promise<void> =>
  makeRequest<void>({
    url: `${CommonDataManagerInst.apiBaseUrl}/proxy/set_proxy_statuses`,
    method: 'POST',
    body: { proxies },
  }).catch(() => void 0);

