import { readFile, writeFile } from 'fs/promises';

import { IAppConfigRecord } from '../interfaces/app-config.interfaces';
import { IExtendedError } from '../interfaces/browser.user-data.manager.interfaces';
import { sendErrorToSentry } from './sentry';

export const saveConfigFile = async (configFilePath: string, config: IAppConfigRecord, sentryPrefix = 'app'): Promise<void> => {
  try {
    const data = JSON.stringify(config, null, '\t');
    await writeFile(configFilePath, data);
  } catch (error: unknown) {
    console.error('Error saving config file', error);
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, transactionName: `save-${sentryPrefix}-config-error`, level: 'warning',
      tags: [['scenario', `save-${sentryPrefix}-config`]],
    });
  }
};

export const readConfigFile = async (configFilePath: string, sentryPrefix = 'app'): Promise<IAppConfigRecord|void> => {
  try {
    const data = await readFile(configFilePath, 'utf-8');
    const json = JSON.parse(data);

    return json;
  } catch (error: unknown) {
    const message = error instanceof Error ? error.message : error;
    const { code = '' } = error as IExtendedError;

    if (code === 'ENOENT') {
      return {};
    }

    sendErrorToSentry({
      message, transactionName: `load-${sentryPrefix}-config-error`, level: 'warning',
      tags: [['scenario', `load-${sentryPrefix}-config`]],
    });
  }
};
