import * as Sentry         from '@sentry/electron';

import { ISendErrorToSentryProps, ISentryTag } from '../interfaces/sentry.interfaces';
import { ElectronError } from './custom-errors';

export const sendErrorToSentry = (props: ISendErrorToSentryProps): void => {
  const {
    message,
    level = 'error',
    tags = [],
    transactionName,
  } = props;

  Sentry.captureException(new ElectronError(message.toString()), (scope) => {
    scope.setLevel(level);
    scope.setTransactionName(transactionName);
    scope.setFingerprint([transactionName]);

    tags.forEach((tagArray: ISentryTag) => {
      const [tag, value] = tagArray;
      scope.setTag(tag, value);
    });

    return scope;
  });
};

export const initSentry = (): void => {
  process.env.NODE_ENV === 'production' &&
  Sentry.init({
    dsn: 'https://8a490970aa7e0c82863d55ce224b68cb@sentry.amzn.pro/3',
    tracesSampleRate: 1.0,
    release: process.env.npm_package_version,
    environment: 'production',
    ignoreErrors: [
      'Invalid Minidump',
      'Object has been destroyed',
      'Cannot set headers after they are sent to the client',
      'Cannot read properties of undefined (reading \'isServer\')',
      'The `onCancel` handler was attached after the promise settled.',
    ],
  });
};

export const sendCommonErrorToSentry = (props: ISendErrorToSentryProps): void => {
  const {
    message,
    tags = [],
  } = props;

  let { transactionName } = props;

  const isInternetError = ['SSL', 'certificate', 'socket hang up', 'ECONNRESET', 'getaddrinfo', 'connect', 'Timeout']
    .some((messagePart) => message.includes(messagePart));

  switch(true) {
    case isInternetError:
      transactionName = 'internet-error';
      break;
    case message.includes('402'):
      transactionName = 'payment-required';
      break;
    case message.includes('EPERM') || message.includes('EACCES'):
      transactionName = 'permission-error';
      break;
    case message.includes('429') || message.toLowerCase() === 'too many requests':
      transactionName = 'too-many-requests';
      break;
    case message.includes('ENOSPC') || message.includes('SQLITE_FULL'):
      transactionName = 'no-disc-space';
      break;
    case message.includes('The server aborted pending request'):
      transactionName = 'aws-aborted';
      break;
    case message.includes('unexpected number of bytes'):
      transactionName = 'orbita-files-busy';
      break;
    default:
      // do nothing
  }

  Sentry.captureException(new ElectronError(message.toString()), (scope) => {
    scope.setTransactionName(transactionName);
    scope.setFingerprint([transactionName]);

    tags.forEach((tagArray: ISentryTag) => {
      const [tag, value] = tagArray;
      scope.setTag(tag, value);
    });

    return scope;
  });
};
