import { BrowserWindow, screen } from 'electron';

import { STORAGE_KEY_PREFIX } from '../../constants/constants';
import { BrowserReleasesManager } from '../../managers/browser-releases.manager';
import { getWinRelease } from '../userOs';

export const saveBrowserIconsData = (browserWindow: BrowserWindow|null): void => {
  if (!browserWindow) {
    return;
  }

  const { scaleFactor = 1 } = screen.getPrimaryDisplay();
  const osPlatform = process.platform;
  let winVersionMajor = 10;
  browserWindow.webContents.executeJavaScript(`
    localStorage.setItem('${STORAGE_KEY_PREFIX}:osPlatform', '${osPlatform}');
    localStorage.setItem('${STORAGE_KEY_PREFIX}:scaleFactor', ${scaleFactor});
  `);

  if (osPlatform === 'win32') {
    winVersionMajor = getWinRelease();
    browserWindow.webContents.executeJavaScript(`localStorage.setItem('${STORAGE_KEY_PREFIX}:winVersionMajor', '${winVersionMajor}');`);
  }

  browserWindow && new BrowserReleasesManager().updateReleasesListInLocalStorage(browserWindow);
};
