import { copyFile, mkdir, readdir, rmdir, stat, unlink } from 'fs/promises';
import path from 'path';

export const checkFileSizeSync = async (pathToFile: string): Promise<number> => {
  try {
    const [fileName] = pathToFile.split(path.sep).reverse();
    if (fileName === '.DS_Store') {
      return 0;
    }

    const fileStats = await stat(pathToFile);
    if (!fileStats.isDirectory()) {
      return fileStats.size;
    }

    const files = await readdir(pathToFile);
    const promises = files.map(async file => checkFileSizeSync(path.join(pathToFile, file)));

    return (await Promise.all(promises)).reduce((result, value) => result + value, 0);
  } catch {
    return -1;
  }
};

export const copyFolder = async (fromPath: string, destPath: string): Promise<void|void[]> => {
  const stats = await stat(fromPath);

  if (!stats.isDirectory()) {
    return copyFile(fromPath, destPath);
  }

  await mkdir(destPath, { recursive: true }).catch(() => void 0);
  const files = await readdir(fromPath);
  const promises = files.map(async file => {
    await mkdir(destPath, { recursive: true }).catch(() => void 0);

    return copyFolder(path.join(fromPath, file), path.join(destPath, file));
  });

  return Promise.all(promises) as Promise<void|void[]>;
};

export const removeFileOrFolder = async (pathToFile: string): Promise<void> => {
  const stats = await stat(pathToFile).catch(() => null);

  if (!stats) {
    return;
  }

  if (stats.isDirectory()) {
    return rmdir(pathToFile, { recursive: true });
  }

  return unlink(pathToFile);
};

export const getRootDirectory = (): string => __dirname;

export const extIdEncoding: { [key: string]: string } = {
  0: 'a',
  1: 'b',
  2: 'c',
  3: 'd',
  4: 'e',
  5: 'f',
  6: 'g',
  7: 'h',
  8: 'i',
  9: 'j',
  a: 'k',
  b: 'l',
  c: 'm',
  d: 'n',
  e: 'o',
  f: 'p',
};

export const removeItemFromArray = <Type>(array: Type[], itemToRemove: Type): void =>  {
  const indexToRemove = array.indexOf(itemToRemove);
  if (indexToRemove < -1) {
    return;
  }

  array.splice(indexToRemove, 1);
};
