import crypto from 'crypto';
import { copyFile, mkdir, readdir, readFile, rename, rm, writeFile, access } from 'fs/promises';
import path from 'path';

import { CommonDataManager } from './common-data-manager';
import fonts from '../../fonts';
import {
  ICalculateIdProps,
  IComposeFonts,
  IExtSettings,
  IExtsSettings,
  ISetPathAndRemoveProps,
  IUploadProfileData,
} from '../interfaces/browser.user-data.manager.interfaces';
import { ISignedS3UrlObj } from '../interfaces/close-orbita.interfaces';
import { downloadWithRetry } from '../utils/downloadWithRetry';
import { makeRequest } from '../utils/make-request';
import {
  getPathOs,
  GOLOGIN_BASE_FOLDER_NAME,
  GOLOGIN_TEST_FOLDER_NAME,
  BROWSER_PATH,
} from '../utils/path-platforms';
import { sendCommonErrorToSentry, sendErrorToSentry } from '../utils/sentry';
import { osPlatform } from '../utils/userOs';
import { extIdEncoding, getRootDirectory } from '../utils/utils';
import withRetry from '../utils/with-retry';

const FONTS_DIR_NAME = 'fonts';
const DEFAULT_ORBITA_EXTENSIONS_NAMES = ['Google Hangouts', 'Chromium PDF Viewer', 'CryptoTokenExtension', 'Web Store'];
const FONTS_URL = 'https://fonts.gologin.com/';

const pathOs = getPathOs();

export class BrowserUserDataManager {
  static instance: any;

  #commonDataManagerInst = new CommonDataManager();

  constructor() {
    if (!BrowserUserDataManager.instance) {
      BrowserUserDataManager.instance = this;
    }

    return BrowserUserDataManager.instance;
  }

  uploadProfileData(props: IUploadProfileData): Promise<{ s3: ISignedS3UrlObj; extensionsToAdd: string[] }> {
    const {
      browserId,
      passwords = [],
      cookies = [],
      extensionsIds = [],
      bookmarks = {},
      fileSize,
      cf = false,
      hk = false,
    } = props;

    const body = {
      passwords,
      cookies,
      extensionsIds,
      bookmarks,
      fileSize,
      cf,
      hk,
      isCookiesEncrypted: true,
      isStorageGateway: true,
    };

    const url = `${this.#commonDataManagerInst.apiBaseUrl}/browser/features/profile/${browserId}/update_after_close?isCookiesEncrypted=true`;

    return withRetry<{ s3: ISignedS3UrlObj; extensionsToAdd: string[] }>({
      fn() {
        return makeRequest(
          {
            url,
            method: 'POST',
            body,
            timeoutDetailed: {
              socket: 10000,
            },
          },
        );
      },
      retryDelay: 300,
    });
  }

  async downloadFonts(fontsList: string[], getAllFonts = false): Promise<void> {
    const browserFontsPath = path.join(BROWSER_PATH, FONTS_DIR_NAME);
    await mkdir(browserFontsPath, { recursive: true });

    if (getAllFonts) {
      fontsList = fonts.map((fontObj => fontObj.fileNames)).filter(Boolean).flat() as string[];
      fontsList.push('LICENSE.txt');
      fontsList.push('OFL.txt');
    }

    const files = await readdir(browserFontsPath);
    const fontsToDownload = fontsList.filter(font => !files.includes(font));

    const promises = fontsToDownload.map(font => downloadWithRetry({
      url: FONTS_URL + font,
      destFile: path.join(browserFontsPath, font),
    }).catch((error: unknown) => {
      console.log(error);
      const message = error instanceof Error ? error.message : error;
      !getAllFonts && sendCommonErrorToSentry({
        message, transactionName: 'download-fonts',
        tags: [['scenario', 'start-app']],
      });
    }));

    if (promises.length) {
      await Promise.all(promises);
    }

    console.log('fonts downloading completed');
  }

  async copyFontsToProfileFolder(fontsList: string[], profilePath: string): Promise<void> {
    const browserFontsPath = path.join(BROWSER_PATH, FONTS_DIR_NAME);
    const promises = fontsList.map((font) => withRetry({
      fn() {
        const targetFontName = font.replace('.otf', '.ttf');

        return copyFile(path.join(browserFontsPath, font), path.join(profilePath, FONTS_DIR_NAME, targetFontName));
      },
      retryDelay: 100,
      limit: 100,
    }));

    await Promise.all(promises);
  }

  async composeFonts(props: IComposeFonts): Promise<void> {
    const { fontsList = [], profilePath, differentOs = false, startProfileTransactionSentry = {} } = props;
    if (!(fontsList.length && profilePath)) {
      return;
    }

    const composeFontsSentrySpan = startProfileTransactionSentry.startChild({ op: 'download-fonts' });

    const fontsToDownload = fonts
      .filter((fontObj) => {
        if (
          ['Liberation Mono', 'Liberation Sans', 'Liberation Serif'].includes(fontObj.value) &&
          differentOs
        ) {
          return true;
        }

        return fontsList.includes(fontObj.value);
      })
      .reduce((res: string[], elem) => res.concat(elem.fileNames || []), []);

    if (differentOs && !fontsToDownload.length) {
      throw new Error('No fonts to download found. Please select at least 1 font in Fonts settings');
    }

    startProfileTransactionSentry.tags = { ...startProfileTransactionSentry.tags, 'fonts-count': fontsToDownload.length };

    fontsToDownload.push('LICENSE.txt');
    fontsToDownload.push('OFL.txt');

    const pathToFontsDir = path.join(profilePath, FONTS_DIR_NAME);
    await rm(pathToFontsDir, { recursive: true }).catch(() => null);

    await mkdir(pathToFontsDir, { recursive: true });
    await this.downloadFonts(fontsToDownload);
    await this.copyFontsToProfileFolder(fontsToDownload, profilePath);

    if (osPlatform === 'linux') {
      await this.copyFontsConfigFile(profilePath);
    }

    composeFontsSentrySpan.finish();
  }

  async copyFontsConfigFile(profilePath: string): Promise<void> {
    if (!profilePath) {
      return;
    }

    const dirname = getRootDirectory();
    const fileContent = await readFile(path.resolve(dirname, 'fonts_config'), 'utf-8');
    const result = fileContent.replace(/\$\$GOLOGIN_FONTS\$\$/g, path.join(profilePath, FONTS_DIR_NAME));

    const defaultFolderPath = path.join(profilePath, 'Default');
    await mkdir(defaultFolderPath, { recursive: true });
    await writeFile(path.join(defaultFolderPath, 'fonts_config'), result, { mode: 0o666 });
  }

  async getProfileChromeExtensions(profilePreferencesPath: string): Promise<string[]> {
    const profileChromeExtensions: string[] = [];
    if (!profilePreferencesPath) {
      return profileChromeExtensions;
    }

    const fileContent = await readFile(profilePreferencesPath, 'utf-8');
    const settings = JSON.parse(fileContent);
    const extensionsSettingsObj = settings?.extensions || { settings: {} };
    const extensionsSettings = extensionsSettingsObj.settings || {};
    const extensionsEntries = Object.entries(extensionsSettings);
    if (!extensionsEntries.length) {
      return profileChromeExtensions;
    }

    const currentExtensions: string[] = [];

    extensionsEntries.forEach((extensionObj: [string, any]) => {
      const [extensionsId, extensionsContent] = extensionObj;
      const { path: extPath = '' } = extensionsContent;
      const formattedPath = extPath.replace(/\\|@/g, '/');
      const regex = new RegExp(`^${extensionsId}|chrome-extensions/\\w+`);
      const pathMatch = formattedPath.match(regex);

      if (!pathMatch) {
        return;
      }

      currentExtensions.push(extensionsId);
      const [matched] = pathMatch;
      const [originalExtId] = matched.split('/').reverse();
      if (profileChromeExtensions.includes(originalExtId)) {
        return;
      }

      profileChromeExtensions.push(originalExtId);
    });

    await this.deleteUselessExtensionSettings(currentExtensions, profilePreferencesPath).catch(() => null);

    return profileChromeExtensions;
  }

  async deleteUselessExtensionSettings(currentExtensions: string[], preferencesPath: string): Promise<void> {
    const profilePath = path.normalize(path.join(preferencesPath, '..'));
    const localExtensionSettingsPath = path.join(profilePath, 'Local Extension Settings');
    const existingExtensionSettings = await readdir(localExtensionSettingsPath);

    const deletePromises = existingExtensionSettings.map(async (extId) => {
      if (!currentExtensions.includes(extId)) {
        await rm(path.join(localExtensionSettingsPath, extId), { recursive: true });
      }
    });

    await Promise.all(deletePromises);
  }

  async setExtPathsAndRemoveDeleted(props: ISetPathAndRemoveProps): Promise<IExtsSettings> {
    const { settings = { extensions: {} }, profileExtensionsCheckRes = [], profileId = '' } = props;
    const formattedLocalExtArray = profileExtensionsCheckRes.map((el: string) => {
      const [extFolderName = ''] = el.split(path.sep).reverse();
      const [originalId] = extFolderName.split('@');
      if (!originalId) {
        return null;
      }

      return {
        path: el,
        originalId,
      };
    }).filter(Boolean);

    const extensionsSettings: IExtsSettings = settings.extensions.settings || {};
    const extensionsEntries = Object.entries(extensionsSettings);

    const promises = extensionsEntries.map(async (extensionObj: [string, IExtSettings]) => {
      let [extensionId] = extensionObj;
      const [_, currentExtSettings = { manifest: {}, path: '' }] = extensionObj;
      const extName = currentExtSettings.manifest?.name || '';
      let extPath = currentExtSettings.path || '';
      let originalId = '';

      const isExtensionToBeDeleted = ['resources', 'passwords-ext', 'cookies-ext'].some(substring => extPath.includes(substring))
        && [GOLOGIN_BASE_FOLDER_NAME, GOLOGIN_TEST_FOLDER_NAME].some(substring => extPath.includes(substring))
        || DEFAULT_ORBITA_EXTENSIONS_NAMES.includes(extName)
        && [GOLOGIN_BASE_FOLDER_NAME, GOLOGIN_TEST_FOLDER_NAME].some(substring => extPath.includes(substring));

      if (isExtensionToBeDeleted) {
        delete extensionsSettings[extensionId];

        return;
      }

      if (osPlatform === 'win32') {
        extPath = extPath.replace(/\//g, '\\');
      } else {
        extPath = extPath.replace(/\\/g, '/');
      }

      extensionsSettings[extensionId].path = extPath;

      const splittedPath = extPath.split(path.sep);
      const isExtensionManageable = ['chrome-extensions', 'user-extensions'].some(substring => extPath.includes(substring))
        && [GOLOGIN_BASE_FOLDER_NAME, GOLOGIN_TEST_FOLDER_NAME].some(substring => extPath.includes(substring));

      if (isExtensionManageable) {
        const [extFolderName] = extPath.split(path.sep).reverse();
        [originalId] = extFolderName.split('@');
      } else if (splittedPath.length === 2) {
        [originalId] = splittedPath;
      }

      if (isExtensionManageable || splittedPath.length === 2) {
        const isExtensionInProfileSettings = formattedLocalExtArray.find((el: IExtSettings | null) => el?.path?.includes(originalId));
        if (!isExtensionInProfileSettings) {
          delete extensionsSettings[extensionId];

          return;
        }
      }

      const localExtObj = originalId && formattedLocalExtArray.find((el: IExtSettings | null) => el?.path?.includes(originalId));
      if (!localExtObj) {
        return;
      }

      const initialExtName = extensionId;

      extensionId = await this.recalculateId({
        localExtObj, extensionId, extensionsSettings, currentExtSettings,
      });

      if (initialExtName !== extensionId) {
        const profilePath = pathOs.PROFILES_DIR + profileId;

        const extensionSettingsFolder = await this.getExtensionSettingsFolder(profilePath, initialExtName);

        if (extensionSettingsFolder) {
          const extSyncFolder = path.join(profilePath, 'Default', extensionSettingsFolder, initialExtName);
          const newSyncFolder = path.join(profilePath, 'Default', extensionSettingsFolder, extensionId);

          const renameResult = await rename(extSyncFolder, newSyncFolder).catch(async (error: unknown) => {
            const isError = error instanceof Error;
            const message = isError ? error.message : JSON.stringify(error);
            //    -   
            if (message.includes('ENOENT')) {
              return;
            }

            //   ENOTEMPTY       
            if (message.includes('ENOTEMPTY')) {
              return 'ENOTEMPTY';
            }

            sendCommonErrorToSentry({
              message, transactionName: 'extensions-cache-folder-rename-error',
              tags: [['scenario', 'run-profile'], ['name', 'extensions-cache-folder-rename-error']],
            });
          });

          if (renameResult === 'ENOTEMPTY') {
            console.log('trying to delete existing folder');
            await rm(newSyncFolder, { recursive: true }).catch(() => null);

            await rename(extSyncFolder, newSyncFolder).catch(async (error: unknown) => {
              const isError = error instanceof Error;
              const message = isError ? error.message : JSON.stringify(error);
              //    -   
              if (message.includes('ENOENT')) {
                return;
              }

              sendCommonErrorToSentry({
                message, transactionName: 'extensions-cache-folder-rename-error',
                tags: [['scenario', 'run-profile'], ['name', 'extensions-cache-folder-rename-error']],
              });
            });
          }
        }
      }

      if (localExtObj.path.endsWith('.zip')) {
        localExtObj.path = localExtObj.path.replace('.zip', '');
      }

      extensionsSettings[extensionId].path = localExtObj.path || '';
    });

    return Promise.all(promises).then(() => extensionsSettings);
  }

  async setOriginalExtPaths(settings: IExtSettings = { path: '' }, originalExtensionsFolder = ''): Promise<IExtSettings | null> {
    if (!originalExtensionsFolder) {
      return null;
    }

    const extensionsSettings = settings.extensions?.settings || {};
    const extensionsEntries = Object.entries(extensionsSettings);

    const originalExtensionsList = await readdir(originalExtensionsFolder).catch(() => []);
    if (!originalExtensionsList.length) {
      return null;
    }

    const promises = originalExtensionsList.map(async (originalId: string) => {
      const extFolderPath = path.join(originalExtensionsFolder, originalId);
      const extFolderContent = await readdir(extFolderPath);
      if (!extFolderPath.length) {
        return {};
      }

      if (extFolderContent.includes('manifest.json')) {
        return {
          originalId,
          path: path.join(originalExtensionsFolder, originalId),
        };
      }

      const [version] = extFolderContent;

      return {
        originalId,
        path: path.join(originalExtensionsFolder, originalId, version),
      };
    });

    const originalExtPaths = await Promise.all(promises);

    extensionsEntries.forEach((extensionObj) => {
      const [extensionsId] = extensionObj;
      const extPath = extensionsSettings[extensionsId].path;
      if (!/chrome-extensions/.test(extPath)) {
        return;
      }

      const originalExtPath = originalExtPaths.find(el => el.originalId === extensionsId);
      if (!originalExtPath) {
        return;
      }

      extensionsSettings[extensionsId].path = originalExtPath.path || '';
    });

    return extensionsSettings;
  }

  async recalculateId(props: ICalculateIdProps): Promise<string> {
    const { localExtObj, extensionsSettings, currentExtSettings } = props;
    let { extensionId } = props;

    if (props.currentExtSettings.manifest?.key) {
      return extensionId;
    }

    const manifestFilePath = path.join(localExtObj.path, 'manifest.json');
    const manifestString = await readFile(manifestFilePath, { encoding: 'utf8' }).catch(() => '');

    if (!manifestString) {
      return extensionId;
    }

    let manifestObject;
    try {
      manifestObject = JSON.parse(manifestString);
    } catch {
      return extensionId;
    }

    if (manifestObject.key) {
      return extensionId;
    }

    let encoding = 'utf8';
    if (osPlatform === 'win32') {
      encoding = 'utf16le';
    }

    const extPathToEncode = Buffer.from(localExtObj.path, encoding as BufferEncoding);

    const hexEncodedPath = crypto.createHash('sha256').update(extPathToEncode).digest('hex');
    const newId = hexEncodedPath.split('').slice(0, 32).map(symbol => extIdEncoding[symbol]).join('');
    if (extensionId !== newId) {
      delete extensionsSettings[extensionId];

      extensionsSettings[newId] = currentExtSettings;
      extensionId = newId;
    }

    return extensionId;
  }

  async getExtensionSettingsFolder(profilePath: string, initialExtensionId: string): Promise<string> {
    const localFolderPath = path.join(profilePath, 'Default', 'Local Extension Settings', initialExtensionId);
    const syncFolderPath = path.join(profilePath, 'Default', 'Sync Extension Settings', initialExtensionId);

    switch (true) {
      case (await access(localFolderPath).then(() => true, () => false)):
        return 'Local Extension Settings';
      case (await access(syncFolderPath).then(() => true, () => false)):
        return 'Sync Extension Settings';
      default:
        return '';
    }
  }
}
