import * as isPortReachable from 'is-port-reachable';

export const getRandomPort = async (): Promise<number> => {
  let port = getRandomInt(20000, 30000);
  let isPortBusy = await isPortReachable(port);
  while (isPortBusy) {
    port = getRandomInt(20000, 30000);
    isPortBusy = await isPortReachable(port);
  }

  return port;
};

const getRandomInt = (min: number , max: number): number => {
  min = Math.ceil(min);
  max = Math.floor(max);

  return Math.floor(Math.random() * (max - min + 1)) + min;
};
