import net from 'net';

import { ICheckProxyData, ICheckProxyParams, ICheckProxyResult } from '../interfaces/proxy.service.interfaces';
import { makeProxyRequestWithTimeout } from '../utils/proxy-request-with-timeout';
import { sendErrorToSentry } from '../utils/sentry';

export class ProxyService {
  static async checkProxy(params: ICheckProxyParams, checkPing = false): Promise<ICheckProxyResult> {
    let status = 'fail';
    let origin = '';
    let country = '';
    let timezone = '';
    let languages = '';
    let city = '';
    let error = '';
    let ping: string|number|null = '';

    const { username = '', password = '', host } = params;

    if (!(username && password) && host.includes('floppydata.com')) {
      return {
        status: 'fail',
        error: 'profileStatusMessages.invalidProxy',
      };
    }

    if (params.port < 0 || params.port > 65535) {
      return {
        status: 'fail',
        error: 'notifications.error.invalidPort',
      };
    }

    if (checkPing) {
      ping = await this.getPing(params);
    }

    if (ping === 'Check timeout exceeded') {
      return {
        status: 'fail',
        error: 'notifications.error.timeoutExceeded',
      };
    }

    const checkProxyRes = await makeProxyRequestWithTimeout(params).catch((err: unknown) => {
      const message = err instanceof Error ? err.message : err;
      sendErrorToSentry({
        message,
        transactionName: 'check-proxy',
        tags: [['scenario', 'check-proxy']],
      });

      error = ProxyService.getFormattedError(err);

      return {
        response: {},
        status: 'fail',
        error: 'profileStatusMessages.invalidProxy',
      };
    });

    const checkData = checkProxyRes.response;
    if (Object.prototype.hasOwnProperty.call(checkData, 'status')) {
      return {
        status,
        origin,
        country,
        timezone,
        languages,
        city,
        ping,
        error: error ? error : 'notifications.error.proxyError',
      };
    }

    const checedProxyData = checkData as ICheckProxyData;
    if (!checedProxyData.ip && checedProxyData.statusCode && checedProxyData.statusCode.toString().startsWith('4')) {
      error = ProxyService.getFormattedError(checkData);
    }

    if (checedProxyData.toString().toLowerCase().includes('access denied')) {
      error = 'notifications.error.proxyAuthError';
    } else {
      origin = checedProxyData.ip || '';
      country = checedProxyData.country || '';
      timezone = checedProxyData.timezone || '';
      languages = checedProxyData.languages || '';
      city = checedProxyData.city || '';
      status = origin && origin.length ? 'success' : 'fail';
    }

    return {
      status,
      origin,
      country,
      timezone,
      languages,
      city,
      ping,
      error: error ? error : 'notifications.error.proxyError',
    };
  }

  static async getPing(params: ICheckProxyParams): Promise<string|number|null> {
    return new Promise((resolve) => {
      const timer = setTimeout(() => {
        socket.destroy();
        resolve('Check timeout exceeded');
      }, 10000);

      const socket = new net.Socket();
      const start = process.hrtime();

      socket.connect(params.port, params.host, () => {
        clearTimeout(timer);
        const timeArr = process.hrtime(start);
        const time = parseInt(((timeArr[0] * 1e9 + timeArr[1]) / 1e6).toString());

        socket.destroy();
        resolve(time);
      });

      socket.on('error', () => {
        clearTimeout(timer);
        socket.destroy();
        resolve(null);
      });
    });
  }

  static getFormattedError(error: any): string {
    const errorData = JSON.stringify(error);
    if (errorData.includes('407')) {
      return 'notifications.error.proxyAuthError';
    }

    return 'notifications.error.proxyConnectionError';
  }
}
