import { IAdditionalProps } from '../../../interfaces/server.api.interfaces';
import { BRAVE_PROCESSES } from '../../../managers/profile/renderer';
import { apiRequest } from './request';

export function apiStatusUpdater(profileId: string, additionalOpts: IAdditionalProps): void {
  let running = !!BRAVE_PROCESSES[profileId];
  // eslint-disable-next-line no-undefined
  if (additionalOpts.running !== undefined) {
    ({ running: running } = additionalOpts);
  }

  apiRequest({
    endpoint: `/browser/${profileId}/run-sync`,
    method: 'PATCH',
    body: {
      running,
    },
  }).catch(() => null);
}
