import EventEmitter from 'events';
import { readdir, rmdir, readFile, stat } from 'fs/promises';
import path from 'path';

import zipdir from 'zip-dir';

import { composeExtractionPromises } from '../helpers/helper.functions';
import { IAddExtResult, IUserExtData } from '../interfaces/user.extension.manager.interfaces';
import { downloadWithRetry } from '../utils/downloadWithRetry';
import { makeRequest } from '../utils/make-request';
import { CHROME_EXTENSIONS_PATH, USER_EXTENSIONS_PATH } from '../utils/path-platforms';
import { sendCommonErrorToSentry, sendErrorToSentry } from '../utils/sentry';
import { checkFileSizeSync, copyFolder } from '../utils/utils';
import withRetry from '../utils/with-retry';
import { CommonDataManager } from './common-data-manager';
import { ExtensionsExtractor } from './utils/extensions-extractor';

const MAX_FILE_SIZE = 80 * 1024 * 1024;
const MAX_FILE_SIZE_MB = MAX_FILE_SIZE / 1024 / 1024;

export class UserExtensionsManager {
  #existedUserExtensions: string[] = [];
  #commonDataManagerInst = new CommonDataManager();
  #extensionsInProgress: string[] = [];
  #blockEventEmitter = new EventEmitter();

  get existedUserExtensions(): string[] {
    return this.#existedUserExtensions;
  }

  set existedUserExtensions(fileList) {
    if (!fileList) {
      return;
    }

    this.#existedUserExtensions = fileList;
  }

  deleteUserExtIdFromInProgress(extIdToDelete: string): void {
    this.#extensionsInProgress = this.#extensionsInProgress.filter(extId => extId !== extIdToDelete);

    if (!this.#extensionsInProgress.length) {
      this.#blockEventEmitter.emit('unblocked');
    }
  }

  async addCustomExtension(pathToFiles: string): Promise<IAddExtResult> {
    try {
      const filesSize = await checkFileSizeSync(pathToFiles);
      const isZip = pathToFiles.endsWith('.zip');

      if (filesSize > MAX_FILE_SIZE) {
        return {
          status: 'error',
          body: {
            key: 'notifications.error.extensionFileSize',
            maxFileSize: MAX_FILE_SIZE_MB,
          },
        };
      }

      const customId = this.generateExtensionId();

      if (isZip) {
        const pathToExtract = path.join(USER_EXTENSIONS_PATH, customId);
        await ExtensionsExtractor.extractExtension(pathToFiles, pathToExtract);
        pathToFiles = pathToExtract;
      }

      let fileList = (await readdir(pathToFiles).catch((error) => {
        console.log(error);

        return ['cantReadError'];
      }))
        .filter((folderContent: string) => folderContent !== '.DS_Store');

      if (fileList.length === 1 && !fileList.includes('cantReadError')) {
        const isFolder = (await stat(pathToFiles)).isDirectory();
        if (isFolder) {
          const [folderName] = fileList;
          pathToFiles = path.join(pathToFiles, folderName);
          fileList = await readdir(pathToFiles).catch(() => ['cantReadError']);
        }
      }

      if (fileList.includes('cantReadError')) {
        throw new Error('notifications.error.extAccessFolderErr');
      }

      if (!fileList.includes('manifest.json')) {
        if (isZip) {
          rmdir(pathToFiles);
        }

        throw new Error('notifications.error.missingExtManifestFile');
      }

      if (!isZip) {
        const destPath = path.join(USER_EXTENSIONS_PATH, customId);
        await copyFolder(pathToFiles, destPath).catch(() => {
          throw new Error('notifications.error.extCopingFolder');
        });
      }

      const [nameIconId] = await this.getExtensionsNameAndImage([customId], USER_EXTENSIONS_PATH) || [];

      if (!nameIconId) {
        throw new Error('notifications.error.somethingWentWrongAgainLater');
      }

      const body = {
        extensionInfo: nameIconId,
      };

      const extensionUrl = this.#commonDataManagerInst.apiBaseUrl;

      const dbResult = await withRetry({
        fn() {
          return makeRequest({
            url: `${extensionUrl}/extensions/create_user_extension`, method: 'POST', body, json: false,
            timeout: 30 * 1000,
          });
        },
        retryDelay: 100,
      });

      // if success - there is no body
      if (dbResult) {
        throw new Error('notifications.error.insertingExtToDBErr');
      }

      const fileBuffer = await zipdir(pathToFiles).catch(() => null);
      if (!fileBuffer) {
        throw new Error('notifications.error.somethingWentWrongAgainLater');
      }

      const signedUrl = await makeRequest({
        url: `${this.#commonDataManagerInst.apiBaseUrl}/extensions/upload_url?extId=${customId}`, method: 'GET', json: false,
      }).catch((error: unknown) => {
        const message = error instanceof Error ? error.message : error;
        sendCommonErrorToSentry({
          message, transactionName: 'custom-extension-upload-url-error',
          tags: [['scenario', 'upload-custom-extensions'], ['name', 'custom-extension-upload-url-error']],
        });

        return 'error';
      }) as string;

      if (signedUrl === 'error') {
        return {
          status: 'error',
          message: 'no url',
        };
      }

      const uploadResponse = await withRetry({
        fn() {
          return makeRequest({
            url: signedUrl,
            headers: {
              'Content-Type': 'application/zip',
              'Content-Length': Buffer.byteLength(fileBuffer),
            },
            body: fileBuffer,
            timeout: 30 * 1000,
            method: 'PUT',
            isBuffer: true,
            json: false,
          });
        },
      }).catch((error: unknown) => {
        const message = error instanceof Error ? error.message : error;
        sendCommonErrorToSentry({
          message, transactionName: 'upload-custom-extension-to-aws',
          tags: [['scenario', 'upload-custom-extensions'], ['name', 'upload-custom-extension-to-aws']],
        });

        return {};
      });

      // if success - there is no body, in case of error - there will be an error in the body
      if (uploadResponse) {
        throw new Error('notifications.error.uploadExtError');
      }

      this.#existedUserExtensions = [...this.#existedUserExtensions, nameIconId.extId];

      return {
        status: 'success',
        message: nameIconId,
      };
    } catch (error: unknown) {
      const message = error instanceof Error ? error.message : error;
      if (typeof message === 'string' && message === 'notifications.error.missingExtManifestFile') {
        return {
          status: 'error',
          message,
        };
      }

      sendErrorToSentry({
        message,
        transactionName: 'upload-custom-extensions-global',
        tags: [['scenario', 'upload-custom-extensions']],
      });

      return {
        status: 'error',
        message,
      };
    }
  }

  async checkLocalUserChromeExtensions(userChromeExtensions?: string[], profileId = '', isFromExtManager = false): Promise<string[]> {
    if (!(userChromeExtensions && userChromeExtensions.length)) {
      return [];
    }

    if (!isFromExtManager && this.#extensionsInProgress.length) {
      await new Promise(resolve => this.#blockEventEmitter.on('unblocked', resolve));
    }

    const downloadedExtensions = await readdir(USER_EXTENSIONS_PATH);
    const extensionsToDownload = userChromeExtensions.filter(extId => !downloadedExtensions.includes(extId));

    if (!extensionsToDownload.length) {
      return this.getExtStrToIncludeAsOrbitaParam(userChromeExtensions, USER_EXTENSIONS_PATH);
    }

    if (isFromExtManager) {
      this.#extensionsInProgress.push(userChromeExtensions[0]);
    }

    const extensionsToDownloadPaths = await makeRequest<string[]>({
      url: `${this.#commonDataManagerInst.apiBaseUrl}/extensions/user_chrome_extensions_paths`,
      body: {
        existedUserChromeExtensions: this.#existedUserExtensions,
        profileId,
        userChromeExtensions: extensionsToDownload,
      },
      method: 'POST',
    }).catch(() => []);

    const extensionsToDownloadPathsFiltered =
      extensionsToDownloadPaths.filter((extPath: string) => userChromeExtensions.some(extId => extPath.includes(extId)));

    if (!extensionsToDownloadPathsFiltered.length) {
      if (isFromExtManager) {
        this.deleteUserExtIdFromInProgress(userChromeExtensions[0]);
      }

      return this.getExtStrToIncludeAsOrbitaParam(userChromeExtensions, USER_EXTENSIONS_PATH);
    }

    const promises = extensionsToDownloadPathsFiltered.map(async (awsPath: string) => {
      const [basePath] = awsPath.split('?');
      const [extId] = basePath.split('/').reverse();
      const zipPath = `${path.join(USER_EXTENSIONS_PATH, extId)}.zip`;
      const downloadResult = await downloadWithRetry({
        url: awsPath,
        destFile: zipPath,
      }).catch((error: unknown) => {
        const message = error instanceof Error ? error.message : error;
        sendErrorToSentry({
          message, transactionName: 'user-extensions-download-error',
        });

        return 'error';
      });

      if (downloadResult === 'error') {
        return '';
      }

      return zipPath;
    });

    const zipPaths = await Promise.all(promises).catch(() => []);

    if (!zipPaths.length) {
      if (isFromExtManager) {
        this.deleteUserExtIdFromInProgress(userChromeExtensions[0]);

        return [];
      }

      return this.getExtStrToIncludeAsOrbitaParam(userChromeExtensions, USER_EXTENSIONS_PATH);
    }

    const extractionPromises = composeExtractionPromises(zipPaths, USER_EXTENSIONS_PATH);
    const isExtensionsExtracted = await Promise.all(extractionPromises).catch(() => 'error');
    if (isFromExtManager) {
      this.deleteUserExtIdFromInProgress(userChromeExtensions[0]);
    }

    if (isExtensionsExtracted !== 'error') {
      const [downloadedFolders] = zipPaths.map(archivePath => archivePath.replace('.zip', '').split(path.sep).reverse());
      this.#existedUserExtensions = [...this.#existedUserExtensions, ...downloadedFolders];
    }

    return this.getExtStrToIncludeAsOrbitaParam(userChromeExtensions, USER_EXTENSIONS_PATH);
  }

  async getExtensionsNameAndImage(extensionsIds: string[], pathToExtensions: string): Promise<IUserExtData[]|[]> {
    const isCheckLocalFiles = [CHROME_EXTENSIONS_PATH, USER_EXTENSIONS_PATH].includes(pathToExtensions);
    const extensionFolderNames = await readdir(pathToExtensions).catch(() => []);
    const filteredExtensionFolderNames = extensionFolderNames.filter((extensionFolder: string) =>
      extensionsIds.some(extensionId => !extensionFolder.includes('.zip') && extensionFolder.includes(extensionId)));

    if (!filteredExtensionFolderNames.length) {
      return [];
    }

    const namesPromise = extensionsIds.map(async (extensionsId) => {
      const folderName = filteredExtensionFolderNames.find((name: string) => name.includes(extensionsId));
      if (!folderName) {
        return;
      }

      let pathToExtensionsFolder = [pathToExtensions, folderName];
      if (!isCheckLocalFiles) {
        const [extensionVersion] = await readdir(path.join(pathToExtensions, folderName));
        pathToExtensionsFolder = [pathToExtensions, folderName, extensionVersion];
      }

      const manifestPath = path.join(...pathToExtensionsFolder, 'manifest.json');
      const manifestString = await readFile(manifestPath, 'utf8').catch(() => '');
      if (!manifestString) {
        return;
      }

      const manifestObject = JSON.parse(manifestString);
      let name;
      if (manifestObject.name.includes('__MSG')) {
        const manifestName = manifestObject.name || '';
        const [_, fieldNameInLocale] = manifestName.replace(/__/g, '').split('MSG_');
        const localePath = path.join(...pathToExtensionsFolder, '_locales', manifestObject.default_locale, 'messages.json');
        const localeString = await readFile(localePath, 'utf8').catch((error) => {
          console.log(error);

          return '';
        });

        try {
          const parsedLocale = JSON.parse(localeString.trim());
          name = parsedLocale[fieldNameInLocale].message;
        } catch (error: unknown) {
          console.log('localeString: ', localeString, 'fieldNameInLocale: ', fieldNameInLocale);
          const message = error instanceof Error ? error.message : error;
          sendErrorToSentry({
            message,
            transactionName: 'get-extension-metadata',
            tags: [['scenario', 'upload-custom-extensions']],
          });
        }
      } else {
        ({ name: name } = manifestObject);
      }

      if (!name) {
        return;
      }

      const iconObject = manifestObject.icons;
      let iconPath = manifestObject.browser_action?.default_icon;
      if (iconObject) {
        const iconResolutions = Object.keys(iconObject).map(Number);
        const maxIconResolution = Math.max(...iconResolutions);
        iconPath = iconObject[maxIconResolution];
      }

      let iconBSON = '';
      if (iconPath) {
        const iconPathFull = path.join(...pathToExtensionsFolder, iconPath);
        iconBSON = await readFile(iconPathFull, 'base64').catch(() => '');
      }

      return {
        name,
        extId: extensionsId,
        iconBinary: iconBSON,
      };
    });

    return (await Promise.all(namesPromise)).filter(Boolean) as IUserExtData[]|[];
  }

  async getExtStrToIncludeAsOrbitaParam(profileExtensions: string[] = [], extensionsPath = CHROME_EXTENSIONS_PATH): Promise<string[]> {
    if (!(Array.isArray(profileExtensions) && profileExtensions.length)) {
      return [];
    }

    const folders = await Promise.all([
      readdir(extensionsPath).then((folderNames: string[]) => folderNames.map(folderName => path.join(extensionsPath, folderName))),
    ]);

    const chromeExtList = ([] as string[]).concat.apply([], folders).filter(Boolean);

    if (!chromeExtList.length) {
      return [];
    }

    console.log('splitting formattedList: ', chromeExtList);
    const formattedIdsList = chromeExtList.map((el: string) => {
      const [folderName] = el.split(path.sep).reverse();
      const [originalId] = folderName.split('@');

      return {
        originalId,
        path: el,
      };
    });

    return profileExtensions.map((extId) => {
      const extExisted = formattedIdsList.find(chromeExtPathElem => chromeExtPathElem.originalId === extId);
      if (!extExisted) {
        return '';
      }

      return extExisted.path;
    }).filter(Boolean);
  }

  generateExtensionId(): string {
    let result = '';
    let extensionIdLength = 32;
    const characters = 'abcdefghijklmnopqrstuvwxyz';
    const charactersLength = characters.length;
    while (extensionIdLength--) {
      result += characters.charAt(Math.floor(Math.random() *
        charactersLength));
    }

    return result;
  }
}
