import { access, mkdir } from 'fs/promises';
import path from 'path';

import { Database, open } from 'sqlite';
import sqlite3 from 'sqlite3';

import { createDBFile } from './cookies-decryptor';
import { ICheckLocalServerResult, ICryptoDbOptions } from '../../interfaces/crypto.interfaces';
import { makeRequest } from '../make-request';
import withRetry from '../with-retry';

export const checkLocalServer = async (profileId: string): Promise<boolean> => {
  const serverRes = await makeRequest<ICheckLocalServerResult>({
    url: `http://localhost:${global.appPort}/cookies/${profileId}?isAvailable=true`,
    method: 'GET',
  }).catch(() => ({} as ICheckLocalServerResult));

  return serverRes.status || false;
};

export const getDB = async (filePath: string, readOnly = true): Promise<Database> => {
  const connectionOpts: ICryptoDbOptions = {
    filename: filePath,
    driver: sqlite3.Database,
  };

  await access(filePath).then(() => console.log(`${filePath} cookies file exists`)).catch(() => {
    console.log('creating new cookies file');

    return mkdir(path.dirname(filePath), { recursive: true })
      .then(() => createDBFile(filePath))
      .catch((error) => {
        if (error.message.includes('table cookies already exists')) {
          return;
        }

        throw error;
      });
  });

  if (readOnly) {
    connectionOpts.mode = sqlite3.OPEN_READONLY;
  }

  return withRetry<Database>({
    fn() {
      return open(connectionOpts);
    },
    retryDelay: 500,
  });
};
