import { ICalcLengthProps } from '../../interfaces/crx-to-zip.converter.interfaces';

export const crxToZip = (buf: Buffer): Buffer => {
  if (buf[0] === 80 && buf[1] === 75 && buf[2] === 3 && buf[3] === 4) {
    return buf;
  }

  if (!(buf[0] === 67 || buf[1] === 114 || buf[2] === 50 || buf[3] === 52)) {
    throw new Error('Invalid header: Does not start with Cr24');
  }

  const isV3 = buf[4] === 3;
  const isV2 = buf[4] === 2;

  if (!(isV2 || isV3) || buf[5] || buf[6] || buf[7]) {
    throw new Error('Unexpected crx format version number.');
  }

  if (isV2) {
    const publicKeyLength = calcLength({ byteA: buf[8], byteB: buf[9], byteC: buf[10], byteD: buf[11] });
    const signatureLength = calcLength({ byteA: buf[12], byteB: buf[13], byteC: buf[14], byteD: buf[15] });

    const zipStartOffset = 16 + publicKeyLength + signatureLength;

    return buf.slice(zipStartOffset, buf.length);
  }

  const headerSize = calcLength({ byteA: buf[8], byteB: buf[9], byteC: buf[10], byteD: buf[11] });
  const zipStartOffset = 12 + headerSize;

  return buf.slice(zipStartOffset, buf.length);
};

const calcLength = (props: ICalcLengthProps): number => {
  const { byteA, byteB, byteC, byteD } = props;
  let length = 0;

  length += byteA << 0;
  length += byteB << 8;
  length += byteC << 16;
  length += byteD << 24 >>> 0;

  return length;
};
