import { BrowserWindow } from 'electron';

import { ACCESS_TOKEN_STORAGE_KEY, BASE_URL } from './constants';
import { TWO_FA_TOKEN_KEY_NAME } from '../../../common/constants/constants';
import { sendErrorToSentry } from '../sentry';

export const handleOAuth = async (window: BrowserWindow, responseUrl = ''): Promise<void> => {
  try {
    const { searchParams } = new URL(responseUrl);
    const accessToken = searchParams.get('accessToken');
    const twoFaToken = searchParams.get('twoFaToken');
    const isNewUser = !!searchParams.get('isNewUser');
    if (!accessToken) {
      return;
    }

    const pathToNavigateTo = isNewUser ? 'quiz' : 'profileList';
    await window.webContents.executeJavaScript(`
      localStorage.setItem('${ACCESS_TOKEN_STORAGE_KEY}', '${accessToken}');
      localStorage.setItem('${TWO_FA_TOKEN_KEY_NAME}', '${twoFaToken}');
    `);

    const absolutePathUrl = new URL(`${BASE_URL}#/${pathToNavigateTo}`);
    window.loadURL(absolutePathUrl.toString());
  } catch (error) {
    const message = error instanceof Error ? error.message : JSON.stringify(error);
    sendErrorToSentry({
      message, transactionName: 'google-oauth-error',
      tags: [['scenario', 'google-oauth']],
    });
  }
};
