import { Router } from 'express';
import got from 'got';
import moment from 'moment';
import kill from 'tree-kill';

import { sendErrorToSentry } from '../../../electron/utils/sentry';
import { IStartProfileCb, IWebSocketDebugger } from '../../interfaces/server.api.interfaces';
import { CommonDataManager } from '../../managers/common-data-manager';
import {
  loadProfile,
  killProfile,
  BRAVE_PROCESSES,
} from '../../managers/profile/renderer';
import { getRandomPort } from '../../utils/get-random-port';
import { apiRequest } from '../src/services/request';

const router = Router();
const CommonDataManagerInst = new CommonDataManager();

router.get('/service/check-server', (req, res) => {
  console.log('check-server');
  res.status(200);

  return res.json({
    status: true,
  });
});

router.post('/service/credentials', (req, res) => {
  console.log('credentials');
  const {
    access = '',
    api = 'https://api.gologin.com',
    twoFa = '',
  } = req.body;

  global.credentials = {
    access,
    api,
    twoFa,
    userAgent: req.headers['user-agent'],
  };

  res.status(200);

  return res.json({
    credentials: global.credentials,
  });
});

router.post('/browser/start-profile', async (req, res) => {
  console.log('start-profile');
  const { profileId = '', sync = false } = req.body;
  if (!profileId) {
    return res.status(400).send('Invalid params').end();
  }

  let debuggingPort = 0;
  if (sync) {
    debuggingPort = await getRandomPort();
  }

  const startProfileCb = async (_: any, error: IStartProfileCb = {} as IStartProfileCb): Promise<void> => {
    if (error) {
      const { status = 400, message = '' } = error;

      return res.status(status).send(message).end();
    }

    let wsUrl = '';
    if (debuggingPort) {
      const data = await got(`http://127.0.0.1:${debuggingPort}/json/version`).json<IWebSocketDebugger>().catch(() => null);
      if (data) {
        wsUrl = data.webSocketDebuggerUrl;
      }
    }

    res.status(200).json({ status: 'success', wsUrl });
  };

  const remoteApiOptions = {
    cb: startProfileCb,
    sync,
    debuggingPort,
  };

  await loadProfile({
    profileId,
    iconBase64: '',
    channel: global.channel,
    remoteApiOptions,
    launchViaRestApi: true,
  }).catch(() => null);
});

router.post('/browser/stop-profile', async (req, res) => {
  console.log('stop-profile');
  const { profileId = '' } = req.body;
  if (!profileId) {
    return res.status(400).send('Invalid params').end();
  }

  killProfile(profileId);

  return res.status(204).end();
});

router.get('/cookies/:id', async (req, res): Promise<any|Record<string, any>|number> => {
  console.log('get cookies');
  if (req.query?.isAvailable === 'true') {
    return res.json({
      status: true,
    });
  }

  if (BRAVE_PROCESSES[req.params.id]?.init) {
    return res.json([]);
  }

  const startProfileTimestamp = BRAVE_PROCESSES[req.params.id]?.startProfileTimestamp;
  const cookies = await apiRequest({
    endpoint: `/browser/${req.params.id}/cookies`,
    method: 'GET',
  }) || [];

  if (startProfileTimestamp) {
    const timeDif = moment.now() - startProfileTimestamp;
    console.log(timeDif, 'browser start');

    apiRequest({
      endpoint: '/analytics/compound_action_analytics',
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${CommonDataManagerInst.accessToken}`,
        'user-agent': CommonDataManagerInst.userAgent,
        'x-two-factor-token': CommonDataManagerInst.twoFaKey || '',
      },
      body: JSON.stringify({
        action: 'browser opened',
        time: timeDif,
        mode: BRAVE_PROCESSES[req.params.id]?.startMode || 'unknown',
        profile: req.params.id.toString(),
      }),
    }).catch(() => null);
  }

  if (BRAVE_PROCESSES[req.params.id]) {
    BRAVE_PROCESSES[req.params.id].init = true;
  }

  return res.json(cookies);
});

router.post('/cookies/:id', async (req, res) => {
  console.log('post cookies');
  const request = await apiRequest({
    endpoint: `/browser/${req.params.id}/cookies`,
    method: 'POST',
    body: req.body,
  });

  return res.json(request);
});

router.post('/cookies/:id/cookies_stats', async(req, res) => {
  console.log('cookies_stats');
  const request = await apiRequest({
    endpoint: `/browser/${req.params.id}/cookies/cookies_stats`,
    method: 'POST',
    body: req.body,
  });

  return res.json(request);
});

router.get('/session/ready', (req, res) => res.end());

router.get('/timer/update/:id', (req, res) => res.end());

//       
router.get('/close/:id', (req, res) => {
  console.log('close');
  const profileId = req.params.id;
  try {
    if (process.platform === 'darwin') {
      console.log('Closing browser...');
      BRAVE_PROCESSES[profileId] && kill(BRAVE_PROCESSES[profileId].orbitaProc?.pid, 'SIGTERM');
    }
  } catch(error: unknown) {
    console.log(profileId);
    console.log(BRAVE_PROCESSES);
    const message = error instanceof Error ? error.message : error;
    sendErrorToSentry({
      message, transactionName: 'kill-process-mac',
      tags: [['scenario', 'run-profile'], ['profileId', profileId]],
    });
  }

  res.end();
});

export default router;
