const competitorsCryptoMap: { [key: string]: string } = ({
  'DOLPHIN': 'rjzfxd',
  'OCTO': '0fprsl',
  'MULTILOGIN': 'yx2dnu',
  'ADSPOWER': 'wpvub7',
  'UNDETECTABLE': 'j5qcm9',
  'INCOGNITION': '9o1tie',
  'INDIGO': 'lhzv3p',
  'MORELOGIN': '9dbawo7',
});

export const encryptCompetitorName = (competitor: string): string =>
  competitorsCryptoMap[competitor];

export const decryptCompetitorName = (code: string): string =>
  Object.keys(competitorsCryptoMap).find(key => competitorsCryptoMap[key] === code) || 'unknown';

