/* eslint-disable camelcase */
import moment from 'moment';

import { CommonDataManager } from './common-data-manager';
import { loadingProfiles } from './profile/renderer';
import packageSettings from '../../../package.json';
import { makeRequest } from '../../electron/utils/make-request';
import {
  IProfileStatus,
  IProfileStatusOrUnexpected,
  ITransferStatusData,
  IUpdateProfileStatusEvent,
} from '../interfaces/profile.status.manager.interfaces';
import { removeItemFromArray } from '../utils/utils';

export class ProfileStatusManager {
  #channel: Electron.IpcMainInvokeEvent;
  #profileId: string;
  #statusStack: IProfileStatus[];
  #statusChangeBlocked = false;
  #defaultStatus = this.getDefaultStatusData();
  #commonDataManagerInst = new CommonDataManager();

  constructor(channel: Electron.IpcMainInvokeEvent, profileId: string) {
    this.#channel = channel;
    this.#profileId = profileId;
    this.#statusStack = [this.#defaultStatus];
  }

  unblock(): void {
    this.#statusChangeBlocked = false;
  }

  cleanStack(): void {
    this.#statusStack = [];
    const curStatus = this.getCurrentStatus();

    this.sendStatus({
      status: curStatus.key,
    });
  }

  refreshStatus(): void {
    const curStatus = this.getCurrentStatus();
    this.sendStatus({
      status: curStatus.key,
      statusMessage: curStatus.info,
    });
  }

  getCurrentStatus(): IProfileStatus {
    if (this.#statusStack.length) {
      return this.#statusStack[0];
    }

    return this.#defaultStatus;
  }

  addStatus(transferStatusData: ITransferStatusData): void {
    if (this.#statusChangeBlocked) {
      return;
    }

    if (transferStatusData.status === 'error') {
      removeItemFromArray<string>(loadingProfiles, this.#profileId);
    }

    const statusData = this.getStatusData(transferStatusData.status);
    if (statusData.info === 'unexpected') {
      console.log('error adding status, unexpected status');

      return;
    }

    const statusObj: IProfileStatus = {
      key: transferStatusData.status,
      priority: statusData.priority,
      info: statusData.info,
    };

    const index = this.#statusStack.findIndex(stackItem => stackItem.key === statusObj.key);
    if (index < 0) {
      this.#statusStack.push(statusObj);
      this.#statusStack.sort((curr, next) => curr.priority - next.priority);
      this.sendStatus(transferStatusData);

      return;
    }

    this.#statusStack[index] = statusObj;
    this.#statusStack.sort((curr, next) => curr.priority - next.priority);

    this.sendStatus(transferStatusData);
  }

  removeStatus(transferStatusData: ITransferStatusData): void {
    const statusInStack = this.#statusStack.find(statusFromStack => statusFromStack.key === transferStatusData.status);
    if (!statusInStack) {
      return;
    }

    this.#statusStack =
      this.#statusStack
        .filter((element) => element.key !== statusInStack.key)
        .sort((curr, next) => curr.priority - next.priority);

    this.sendStatus({ status: '' });
  }

  sendStatus(transferStatusData: ITransferStatusData): void {
    if (this.#statusChangeBlocked) {
      return;
    }

    if (transferStatusData.status === 'error') {
      this.#statusChangeBlocked = true;
    }

    const curStatus = this.getCurrentStatus();

    if (transferStatusData.startProfileTimestamp && ['running', 'error'].includes(transferStatusData.status)) {
      const dTime = moment.utc().valueOf() - transferStatusData.startProfileTimestamp;
      console.log(dTime, 'time');
      const analyticsStatus = transferStatusData.status === 'error' ? 'error' : 'success';
      const formattedErrorMessage = transferStatusData.errorMessage?.replace(',', '');

      const body: any = {
        type: 'profile-loading',
        status: analyticsStatus,
        profile: this.#profileId,
        time: dTime,
        os: this.#commonDataManagerInst.userOs,
        message: formattedErrorMessage || transferStatusData.statusMessage,
        mode: transferStatusData.profileOptions?.startMode,
        timings: transferStatusData.timings,
        proxyMode: transferStatusData.profileOptions?.proxyMode,
        appVersion: packageSettings.version,
      };

      if (transferStatusData.profileOptions?.proxyCountry) {
        body.proxyCountry = transferStatusData.profileOptions?.proxyCountry;
      }

      makeRequest({
        method: 'POST',
        url: `${this.#commonDataManagerInst.apiBaseUrl}/analytics`,
        body,
      }).catch(() => {
        console.log('error sending profile-loading analytics');
      });
    }

    const toSend: IUpdateProfileStatusEvent = {
      status: curStatus.info,
      message:  transferStatusData.statusMessage || transferStatusData.errorMessage || curStatus.info,
      profileId: this.#profileId,
    };

    try {
      this.#channel.sender.send('update-profile-status', toSend);
    } catch (err) {
      console.log('channel send error:', err);
    }
  }

  private getDefaultStatusData(): IProfileStatus {
    const res = this.getStatusData('ready');
    if (res.info === 'unexpected') {
      throw new Error('Unexpected status used as default');
    }

    return res;
  }

  getStatusData(key: string): IProfileStatusOrUnexpected {
    //        -   
    const statusObjects: any = {
      error: { info: 'profileStatuses.error' },
      cloud_uploading: { info: 'profileStatuses.cloudUploading' },
      archiving: { info: 'profileStatuses.archiving' },
      syncing: { info: 'profileStatuses.syncing' },
      running: { info: 'profileStatuses.running' },
      checking_proxy: { info: 'profileStatuses.checkingProxy' },
      downloading_profile_init: { info: 'profileStatuses.downloadingProfile' },
      downloading_profile_chrome_ext: { info: 'profileStatuses.downloadingChromeExtension' },
      downloading_profile_user_ext: { info: 'profileStatuses.downloadingUserExtensions' },
      downloading_profile_fonts: { info: 'profileStatuses.downloadingFonts' },
      preparing_profile: { info: 'profileStatuses.preparingProfile' },
      ready: { info: 'profileStatuses.ready' },
    };

    const priority = Object.keys(statusObjects).indexOf(key);

    if (priority < 0) {
      return { key, priority: 10000, info: 'unexpected' };
    }

    const { info } = statusObjects[key];

    return { key, priority, info };
  }
}
