import { copyFile, stat } from 'fs/promises';

import {
  loadCookiesFromFile,
  removeCookiesFile,
  saveCookiesToFile,
} from '../../electron/utils/crypto/cookies-decryptor';
import { IUserCookiesData } from '../interfaces/browser.user-data.manager.interfaces';
import { checkLocalServer } from '../utils/crypto/methods';
import { getPathOs } from '../utils/path-platforms';

const paths = getPathOs();

export class CookiesManager {
  static async prepareCookies(profileId: string, cookiesData: IUserCookiesData, cookiesFilePath: string): Promise<void> {
    const { checkCookies } = cookiesData;
    const { clearCookies } = cookiesData;
    const { userCookies } = cookiesData;

    if (clearCookies) {
      return removeCookiesFile(cookiesFilePath);
    }

    if (!(userCookies || checkCookies)) {
      return;
    }

    const cookiesFileReq = await loadCookiesFromFile(cookiesFilePath);
    const { cookies: cookiesFromFile } = cookiesFileReq;
    const localServerAvailable = await checkLocalServer(profileId);
    console.log('localServerAvailable:', localServerAvailable);
    console.log('cookies.length:', cookiesFromFile.length);

    if (userCookies || cookiesFromFile.length < 2 || !localServerAvailable) {
      const serverCookies = cookiesData.cookies || [];

      console.log('serverCookies.length:', serverCookies.length);
      if (serverCookies.length) {
        return saveCookiesToFile({
          cookiesFromFile, serverCookies, profileId, cookiesFilePath,
        });
      }
    }
  }

  static async copyCookiesFile(fromPath: string, profileId: string): Promise<void> {
    const destPath = fromPath.includes(paths.COOKIES_FILE_NEW)
      ? paths.PROFILES_DIR + profileId + paths.COOKIES_FILE
      : paths.PROFILES_DIR + profileId + paths.COOKIES_FILE_NEW;

    await copyFile(fromPath, destPath);
  }

  static async getCookiesFileToRead(profileId: string): Promise<string> {
    const basePath = paths.PROFILES_DIR + profileId;
    const defaultPath = basePath + paths.COOKIES_FILE;
    const networkPath = basePath + paths.COOKIES_FILE_NEW;

    const promises = [defaultPath, networkPath].map(async (path) => {
      const stats = await stat(path).catch(() => ({ mtimeMs: 0 }));

      return stats.mtimeMs;
    });

    const [defaultPathLastMod, networkPathLastMod] = await Promise.all(promises);

    if (!(defaultPathLastMod || networkPathLastMod)) {
      return '';
    }

    return defaultPathLastMod > networkPathLastMod ? defaultPath : networkPath;
  }
}
