import { readdir } from 'fs/promises';

import { sendErrorToSentry } from '../../electron/utils/sentry';
import { ISendOrbitaErrors } from '../interfaces/send-orbita-errors.interfaces';
import { ISentryTag } from '../interfaces/sentry.interfaces';
import { BrowserReleasesManager } from '../managers/browser-releases.manager';
import { downloadBrowser } from '../utils/check-browser';

export const ORBITA_SELF_CLOSE_MS = 3000;

export const sendOrbitaErrorToSentry = async ({
  orbitaConsoleContainer,
  message,
  profileData,
  processDurationMs,
  code,
  signal,
  ProfileStatusManagerInst,
  channel,
}: ISendOrbitaErrors): Promise<void> => {
  const ua = profileData.gologin?.userAgent || '';
  const majorChromeVersion = new BrowserReleasesManager().getMajorVersionByUA(ua);
  const {
    browserWorkingDir,
  } = new BrowserReleasesManager().getOrbitaPathToStart(majorChromeVersion);

  const browserDir = await readdir(browserWorkingDir).catch(() => console.log('error reading browser dir'));

  console.log(orbitaConsoleContainer);
  console.log('orbita logs: ');
  console.log(browserDir);
  console.log('browser dir content: ');

  let transactionName = 'crash-orbita';

  const tags: ISentryTag[] = [['scenario', 'close-profile']];

  if (processDurationMs < ORBITA_SELF_CLOSE_MS) {
    transactionName = 'open-orbita';
  }

  switch (true) {
    case code === 7:
      tags.push(['reason', 'no-dll']);
      downloadBrowser(channel, { orbitaVersion: majorChromeVersion });
      ProfileStatusManagerInst.addStatus({
        status: 'error',
        statusMessage: 'profilesTable.profileStatusTooltip.reDownloadOrbita',
      });

      break;
    case signal === 'SIGABRT':
      ProfileStatusManagerInst.addStatus({
        status: 'error',
        statusMessage: 'profilesTable.profileStatusTooltip.oldMacOs',
      });

      transactionName = 'not-supported-os';
      break;
    case code === 2147483651:
      ProfileStatusManagerInst.addStatus({
        status: 'error',
        statusMessage: 'profilesTable.profileStatusTooltip.gpuCrash',
      });
      tags.push(['reason', 'gpu']);
      break;
    case orbitaConsoleContainer.some((log: string) => log.includes('Opening in existing browser session')):
      tags.push(['reason', 'existing-session']);
      break;
    case code === 126:
      tags.push(['reason', 'mac-processor']);
      ProfileStatusManagerInst.addStatus({
        status: 'error',
        statusMessage: 'profilesTable.profileStatusTooltip.macNotSupport',
      });
      break;
    case orbitaConsoleContainer.some((log: string) => log.includes('chrome.exe')):
      tags.push(['reason', 'no-exe']);
      downloadBrowser(channel, { orbitaVersion: majorChromeVersion });
      ProfileStatusManagerInst.addStatus({
        status: 'error',
        statusMessage: 'profilesTable.profileStatusTooltip.reDownloadOrbita',
      });
      break;
    case code === 3221225785:
      tags.push(['reason', '3221225785']);
      break;
    case code === 21:
      tags.push(['reason', 'cant-create-singleton']);
      break;
    case code === 3221225506:
      ProfileStatusManagerInst.addStatus({
        status: 'error',
        statusMessage: 'profilesTable.profileStatusTooltip.antivirus',
      });
      tags.push(['reason', 'access']);
      break;
    case code === 127:
      tags.push(['reason', 'no-files-mac']);
      downloadBrowser(channel, { orbitaVersion: majorChromeVersion });
      ProfileStatusManagerInst.addStatus({
        status: 'error',
        statusMessage: 'profilesTable.profileStatusTooltip.reDownloadOrbita',
      });
      break;
    case code === 3765269347:
      tags.push(['reason', '3765269347']);
      break;
    case code === 4294967295:
      tags.push(['reason', '4294967295']);
      break;
    case code === 3221225477:
      tags.push(['reason', '3221225477']);
      break;
    case code === 3221225501:
      tags.push(['reason', '3221225501']);
      break;
    case code === 0:
      return;
    default:
      tags.push(['reason', 'unknown']);
  }

  sendErrorToSentry({
    message, transactionName, level: 'fatal',
    tags,
  });
};
