import { access, unlink } from 'fs/promises';

import decompress from 'decompress';
import decompressUnzip from 'decompress-unzip';

import withRetry from '../../utils/with-retry';

export class ExtensionsExtractor {
  static extractExtension(source: string, dest: string): Promise<void> {
    if (!(source && dest)) {
      throw new Error('Missing parameter');
    }

    return access(source)
      .then(() =>
        withRetry({
          fn() {
            return decompress(source, dest, {
              plugins: [decompressUnzip()],
              filter: (file: { path: string }) => !file.path.endsWith('/'),
            });
          },
        }),
      );
  }

  static deleteExtensionArchive(dest: string): Promise<void> {
    if (!dest) {
      throw new Error('Missing parameter');
    }

    return access(dest)
      .then(
        () => unlink(dest),
        () => Promise.resolve(),
      );
  }
}
