ipcMain.handle('clear-profile-cache', async (event, profileId) => {
  const { ProfileCachingManager } = await import('./electron/managers/profile.caching.manager');
  const ProfileCachingManagerInst = new ProfileCachingManager();

  return ProfileCachingManagerInst.init()
    .then(() => ProfileCachingManagerInst.clearProfileCache(profileId))
    .catch(() => void 0);
});

ipcMain.handle('restore-profiles', async (_event, profileIds: string[]) => {
  const { updateDeletedProfiles } = await import('./electron/managers/profile/renderer');
  updateDeletedProfiles(profileIds);
});

ipcMain.handle('del-profile-ext-folder', async (event, arg) => {
  const { deletedProfiles } = await import('./electron/managers/profile/renderer');
  const { deleteProfileExtFolder } = await import('./electron/utils/check-gologin-extensions');

  const pathOs = getPathOs();
  deletedProfiles.push(arg);
  rm(pathOs.PROFILES_DIR + arg, { recursive: true });
  deleteProfileExtFolder(arg);
});

ipcMain.handle('del-profiles-ext-folder', async (event, arg: string[]) => {
  const { deletedProfiles } = await import('./electron/managers/profile/renderer');
  const { deleteProfileExtFolder } = await import('./electron/utils/check-gologin-extensions');
  const pathOs = getPathOs();
  deletedProfiles.push(...arg);

  arg.forEach((profileId: string) => {
    rm(pathOs.PROFILES_DIR + profileId, { recursive: true, force: true }).catch(() => null);
    deleteProfileExtFolder(profileId).catch(() => null);
  });
});

ipcMain.handle('get-app-version', () => `${version} ${subVersion}`);

ipcMain.handle('get-orbita-browser-version', async () => {
  const { BrowserReleasesManager } = await import('./electron/managers/browser-releases.manager');
  const currentOrbitaVer = await new BrowserReleasesManager().getLocalOrbitaVersionByPath();

  return currentOrbitaVer || '0.0.0';
});

ipcMain.handle('check-supported-orbita', async (_event, { key = 'orbitaMajorVersion', value }: IOrbitaWinCompatibilityData) => {
  const { BrowserReleasesManager } = await import('./electron/managers/browser-releases.manager');

  const versionToDownload = new BrowserReleasesManager().getOrbitaVersionToDownload({ key, value });
  const browserVersionMajorFromUA = new BrowserReleasesManager().getMajorVersionByUA(value);
  const { pickedMajorVer: pickedVersionIfSkipDownload } = new BrowserReleasesManager().getOrbitaPathToStart(browserVersionMajorFromUA);

  return { versionToDownload, pickedVersionIfSkipDownload };
});

ipcMain.handle('check-orbita-compatible', async (_event, { key = 'orbitaMajorVersion', value }: IOrbitaWinCompatibilityData) => {
  const { BrowserReleasesManager } = await import('./electron/managers/browser-releases.manager');

  return new BrowserReleasesManager().checkOrbitaWinCompatible({ key, value });
});

ipcMain.handle('check-orbitas-available', async (_event, orbitasMajorVersions: number[]) => {
  const { BrowserReleasesManager } = await import('./electron/managers/browser-releases.manager');

  return new BrowserReleasesManager().checkOrbitasAvailable(orbitasMajorVersions);
});

ipcMain.handle('check-browser', async (event) => {
  const { checkBrowser } = await import('./electron/utils/check-browser');

  return checkBrowser()
    .then((res: any) => event.sender.send('check-browser', res))
    .catch(() => event.sender.send('check-browser', {}));
});

ipcMain.handle('download-browser', async (event, args) => {
  const { downloadBrowser } = await import('./electron/utils/check-browser');
  downloadBrowser(event, args).catch(error => {
    sendErrorToSentry({
      message: error.message, level: 'fatal', transactionName: 'download-orbita-global',
      tags: [['scenario', 'download-orbita']],
    });
  });
});

ipcMain.handle('check-profiles-running', async () => {
  const { BRAVE_PROCESSES } = await import('./electron/managers/profile/renderer');

  return Object.keys(BRAVE_PROCESSES).length > 0;
});

ipcMain.handle('close-all-profiles', async () => {
  const { closeAllProfiles } = await import('./electron/managers/profile/renderer');
  closeAllProfiles();
});

ipcMain.handle('get-client-id-from-metadata', async () => {
  const getClientIdFromMetadataFileModule = await import('./electron/utils/get-clientId-from-metadata');
  const getClientIdFromMetadataFile = getClientIdFromMetadataFileModule.default;

  const currentGoogleClientId = process.platform === 'win32' && await getGoogleClientIdFromVault.catch(() => null);
  if (!currentGoogleClientId) {
    return getClientIdFromMetadataFile();
  }

  return currentGoogleClientId;
});

ipcMain.handle('update-google-client-id', async (event, googleClientId) => {
  if (process.platform !== 'win32') {
    return;
  }

  await saveGoogleClientIdToVault(googleClientId);
});

ipcMain.handle('get-system-fonts-from-file', async () => {
  const { getSystemFontsFromFile } = await import('./electron/utils/check-system-fonts');

  return getSystemFontsFromFile().catch(() => []);
});

ipcMain.handle('upload-custom-extension', async (event, extPath: string) => {
  const { ExtensionsManager } = await import('./electron/managers/extensions.manager');

  const extensionManagerInst = new ExtensionsManager();

  return extensionManagerInst.addCustomExtension(extPath);
});

ipcMain.handle('get-dolphin-cookies', async (event, profileIds: string[]) => {
  const result = await getDolphinProfilesCookies(profileIds);

  return result.filter(isNotNull);
});

ipcMain.handle('get-system-locale', () => app.getLocale() || 'en');

ipcMain.handleOnce('check-auth-token', async () => {
  const { CommonDataManager } = await import('./electron/managers/common-data-manager');
  const CommonDataManagerInst = new CommonDataManager();

  return CommonDataManagerInst.accessToken;
});

ipcMain.handle('remove-token-electron', async () => {
  const { CommonDataManager } = await import('./electron/managers/common-data-manager');
  const CommonDataManagerInst = new CommonDataManager();

  CommonDataManagerInst.accessToken = '';
});

ipcMain.handle('get-app-config', async () => {
  const { CommonDataManager } = await import('./electron/managers/common-data-manager');
  const CommonDataManagerInst = new CommonDataManager();

  return CommonDataManagerInst.appConfig;
});

ipcMain.handle('set-app-config', async (event, args) => {
  await writeToAppConfig(args);
});

ipcMain.handle('too-many-requests', async () => console.log('TOO MANY REQUESTS'));
ipcMain.handle('redirected-to-2fa', async () => console.log('REDIRECTED-TO-2FA'));
ipcMain.handle('redirected-to-sign-up', async () => console.log('REDIRECTED-TO-SIGN-UP'));
ipcMain.handle('changed-account', async () => console.log('CHANGED ACCOUNT'));

// handling `gologin://` urls on macOS and linux
app.on('open-url', async (_event, responseUrl) => {
  if (window) {
    window.isMinimized() && window.restore();
    window.focus();
  } else {
    deepLinkOnAppLaunch = responseUrl;
  }

  await handleDeepLink(window, responseUrl);
});

app.on('before-quit', async (event) => {
  const promises: Promise<void>[] = [];
  if (window) {
    promises.push(saveAppConfig(window, translationData, theme).catch(() => void 0));
    promises.push(window.webContents.session.clearCache().catch(() => void 0));
  }

  if (isAppUpdateReady) {
    promises.push(saveAccessToken().catch(() => void 0));
  }

  await Promise.all(promises);

  const { BRAVE_PROCESSES, closeAllProfiles } = await import('./electron/managers/profile/renderer');
  const runningProfiles = BRAVE_PROCESSES || {};
  const runningProfilesCount = Object.keys(runningProfiles)?.length || 0;
  if (!runningProfilesCount) {
    return closeApp();
  }

  try {
    const { closeAppTitle, closeAppMessage, closeAppBtnClose, closeAppBtnCancel } = translationData;
    const choice = dialog.showMessageBoxSync(
      window as any,
      {
        type: 'question',
        buttons: [closeAppBtnCancel, closeAppBtnClose],
        title: closeAppTitle,
        message: closeAppMessage,
        noLink: true,
      },
    );

    if (choice === 0) {
      console.log('Preventing close application');
      event.preventDefault();

      return null;
    }

    // TODO:           
    await closeAllProfiles();
  } catch (error: unknown) {
    const message = error instanceof Error ? error.message : error;
    console.log(message);
  }

  return closeApp();
});

app.on('activate', () => window?.show());

app.on('window-all-closed', app.quit);

